import _ from 'lodash'
import Vue from 'vue'
import Vuex from 'vuex'
import util from '../util'
// import api from '../api'

Vue.use(Vuex)

const state = {
  uploadURLs: {},
  buildings: [],
  building: {},
  currentBuildingId: '',
  shareCodes: [],
  shareCode: {},
  codeBuildings: [],
  panoramas: [],
  panorama: {},
  selectedPanorama: {},
  currentPanorama: {},
  targetPanorama: {},
  selectedMarker: {},
  alertParams: {},
  isLoading: false,
  // isEditorChangeLoading: false,
  currentPanoramaId: '',
  activeModal: '',
  activeAlert: '',
  activeLoading: '',
  sort: {
    key: '',
    order: -1
  },
  leanBuildingInfo: {
    address: ''
  },
  leanAuthInfo: {
    username: '',
    password: ''
  },
  authInfo: {
    email: '',
    password: '',
    confirmPassword: ''
  },
  signupInfo: {
    name: '',
    email: '',
    phone: '',
    company: '',
    country: '',
    role: '',
    password: '',
    confirmPassword: ''
  },
  changePasswordInfo: {
    oldPassword: '',
    newPassword: '',
    confirmNewPassword: ''
  },
  user: {
    role: '',
    username: '',
    name: '',
    company: '',
    phone: '',
    photo: '',
    VRAuthenticated: false,
    oldPassword: '',
    newPassword: '',
    confirmNewPassword: ''
  },
  hasFieldChanged: false,
  firebaseComments: [],
  firebaseFiles: [],
  currentCommentId: '',
  areBuildingsFetched: false,
  areShareCodesFetched: false,
  isNewBuilding: false,
  isNewPanorama: false,
  isUpdatedPanorama: false,
  isLineGridToggled: false,
  isOverlayActive: false,
  cameraRotation: {
    x: 0,
    y: 0,
    z: 0
  },
  defaultPhoto: require('../assets/avatar.png'),
  profilePhoto: '',
  sortList: [],
  agents: [
    {
      name: 'Agent 1',
      role: '',
      company: 'Company 1',
      country: 'Taiwan',
      thumbnail: '',
      phone: '1234'
    },
    {
      name: 'Agent 2',
      role: '',
      company: 'Company 1',
      country: 'Taiwan',
      thumbnail: '',
      phone: '1234'
    }
  ],
  editorState: 0,
  sceneEventCount: 0,
  isEditorSceneReady: true,
  hasAddedMarker: false,
  hasClickedAddMarker: false,
  cameraContainerEl: null,
  cameraElRotation: null,
  cameraElOrigRotation: null,
  cameraContainerElRotation: null,
  isImageLoading: false,
  isUploadingMarker: false,
  savedMarkerRotation: 0,
  sortedPanoramas: [],
  hasSortTriggered: false
}

const mutations = {
  FETCH_SHARE_CODE_BY_ID (state, id) {
    const shareCodes = state.shareCodes
    const shareCode = _.find(shareCodes, { 'id': id })
    state.shareCode = shareCode
  },

  FETCH_BUILDINGS_BY_CODE_ID (state, buildings) {
    state.codeBuildings = buildings
  },

  SET_BUILDINGS (state, buildings) {
    state.buildings = buildings
  },

  UPDATE_BUILDINGS_STATE (state, newBuilding) {
    const newBuildings = state.buildings.map(building => {
      if (building.id === newBuilding.id) {
        building = newBuilding
      }
      return building
    })
    state.buildings = newBuildings
  },

  SET_BUILDING_BY_ID (state, building) {
    building.isDirty = false
    // delete building.panorama
    state.building = building
  },

  CLEAR_BUILDING (state) {
    state.building = null
  },

  SET_BUILDINGS_CONCAT (state, buildings) {
    state.buildings = state.buildings.concat(buildings)
  },

  UPDATE_BUILDING (state) { // TODO remove this
    console.log('Building updated')
  },

  REMOVE_BUILDING_FROM_STATE (state, id) {
    state.buildings = state.buildings.filter(obj => {
      return obj.id !== id
    })
  },

  REMOVE_PANORAMA (state, id) {
    state.panoramas = state.panoramas.filter(obj => {
      return obj.id !== id
    })
    state.building.panoramas = state.panoramas
  },

  SET_CURRENT_BUILDING_ID (state, id) {
    state.currentBuildingId = id
  },

  FETCH_PANORAMA_BY_ID (state, id) {
    const panoramas = state.panoramas
    const panorama = _.find(panoramas, { 'id': `${id}` })
    state.panorama = panorama
  },

  CLEAR_PANORAMAS (state) {
    state.panoramas = []
  },

  CLEAR_PANORAMA (state) {
    state.panorama = null
  },

  SET_PANORAMAS (state, panoramas) {
    state.panoramas = panoramas
  },

  SET_SHARECODES (state, shareCodes) {
    state.shareCodes = shareCodes
  },

  SET_SHARECODES_CONCAT (state, shareCodes) {
    state.shareCodes = state.shareCodes.concat(shareCodes)
  },

  SET_CURRENT_PANORAMA_ID (state, id) {
    state.currentPanoramaId = id
  },

  SET_ACTIVE_MODAL (state, modal) {
    state.activeModal = modal
  },

  CLEAR_ACTIVE_MODAL (state, modal) {
    state.activeModal = ''
  },

  SET_ACTIVE_LOADING (state, loading) {
    state.activeLoading = loading
  },

  CLEAR_ACTIVE_LOADING (state) {
    state.activeLoading = ''
  },

  CHECK_BUILDINGS (state) {
    state.areBuildingsFetched = true
    // if (state.buildings.length === 0) {
    //   // state.activeModal = 'noBuildings'
    //   state.noBuildings = true
    // } else {
    //   state.noBuildings = false
    // }
  },

  CHECK_SHARECODES (state) {
    state.areShareCodesFetched = true
    // if (state.buildings.length === 0) {
    //   // state.activeModal = 'noBuildings'
    //   state.noBuildings = true
    // } else {
    //   state.noBuildings = false
    // }
  },

  SET_MARKERS (state, marker, x, y) { // move this out of the store
    marker.x = x
    marker.y = y
    marker.isDirty = true
  },

  SET_ROTATION (state, marker, rotation) { // move this out of the store
    marker.rotation = rotation
    marker.isDirty = true
  },

  SET_SORT (state, key, order) {
    if (state.sort.key === key) state.sort.order = order * -1
    else state.sort.order = -1
    state.sort.key = key
  },

  SET_START_SORT (state, key, order) {
    console.log('OR:', order)
    state.sort.order = order || -1
    state.sort.key = key
  },

  // SET_SORT_LIST (state, sortItem) {
  //   state.sortList.push({ key: sortItem, order: -1 })
  // },

  SET_FIELD_CHANGED (state) {
    state.hasFieldChanged = true
  },

  UNSET_FIELD_CHANGED (state) {
    state.hasFieldChanged = false
  },

  SET_USER_PHOTO (state, photo) {
    state.user.photo = photo
  },

  SET_USER_INFO (state, user) {
    state.user = user
  },

  SET_USER_LEAN_AUTH_STATE (state, authenticated) {
    state.user.VRAuthenticated = authenticated
  },

  SET_CURRENT_COMMENT_ID (state, id) {
    state.currentCommentId = id
  },

  FETCH_FIREBASE_COMMENTS (state, comments) {
    state.firebaseComments = comments
  },

  // PROCESS_FIREBASE_FILE (state, file) {
  //   const foundFile = state.firebaseFiles.filter(firebaseFile => {
  //     return firebaseFile.filename === file.filename
  //   })
  //   if (foundFile.length > 0) {
  //     foundFile[0].progress = file.progress
  //   } else {
  //     state.firebaseFiles.push(file)
  //   }
  // },

  // PROCESS_FIREBASE_DOWNLOAD_URL (state, file) {
  //   const foundFile = state.firebaseFiles.filter(firebaseFile => {
  //     return firebaseFile.filename === file.filename
  //   })
  //   if (foundFile.length > 0) foundFile[0].downloadURL = file.downloadURL
  // },

  DO_IMAGE_UPLOAD (state, uploadURLs) {
    state.uploadURLs = uploadURLs
  },

  ADD_BUILDING_TO_STATE (state, building) {
    state.buildings.push(building)
    state.building = building
  },

  ADD_PANORAMA_TO_STATE (state, panorama) {
    state.panoramas.push(panorama)
    state.building.panoramas.push(panorama)
    state.panorama = panorama
  },

  SET_PANORAMA_PHOTO_STATE (state, web, thumbnail) {
    const newPanoramas = state.panoramas.map(panorama => {
      if (state.panorama.id === panorama.id) {
        panorama.thumbnail = thumbnail
      }
      return panorama
    })
    state.panoramas = newPanoramas
    state.panorama.web = web
    state.panorama.thumbnail = thumbnail
  },

  UPDATE_PANORAMA_STATE (state, updatedPanorama) {
    const foundPanos = state.panoramas.filter(panorama => {
      return panorama.id !== updatedPanorama.id
    })
    // const foundBuildingPanos = state.building.panoramas.filter(panorama => {
    //   return panorama.id !== updatedPanorama.id
    // })
    // foundPanoramas.push(updatedPanorama)
    state.panoramas = foundPanos
    // state.building.panoramas = foundBuildingPanos
    state.panoramas.push(updatedPanorama)
    // state.panoramas.push(updatedPanorama)
    state.panorama = updatedPanorama
    // console.log('FOUND: ', state.panoramas)
  },

  SET_IS_NEW_BUILDING (state, isSet) {
    state.isNewBuilding = isSet
  },

  SET_IS_NEW_PANORAMA (state, isSet) {
    state.isNewPanorama = isSet
  },

  SET_IS_UPDATED_PANORAMA (state, isSet) {
    state.isUpdatedPanorama = isSet
  },

  SET_SCENE_EVENT_COUNT (state, sceneEventCount) {
    if (typeof sceneEventCount === 'undefined') {
      state.sceneEventCount = state.sceneEventCount + 1
    } else {
      state.sceneEventCount = sceneEventCount
    }
  },

  // SET_IS_OVERLAY_ACTIVE (state, isSet) {
  //   state.isOverlayActive = isSet
  // },

  GET_CAMERA_ROTATION (state, camera, cameraContainer) {
    state.cameraRotation = {
      x: camera.x + cameraContainer.x,
      y: camera.y + cameraContainer.y,
      z: camera.z + cameraContainer.z
    }
  },

  SET_PROFILE_PHOTO (state) {
    (state.user && state.user.photo)
      ? state.profilePhoto = state.user.photo
      : state.profilePhoto = state.defaultPhoto
  },

  CLEAR_CHANGE_PASSWORD (state) {
    state.changePasswordInfo.oldPassword = ''
    state.changePasswordInfo.newPassword = ''
    state.changePasswordInfo.confirmNewPassword = ''
  },

  SET_IS_LINE_GRID_TOGGLED (state) {
    state.isLineGridToggled = !state.isLineGridToggled
  },

  SET_SELECTED_PANORAMA (state, selectedPanorama) {
    state.selectedPanorama = selectedPanorama
  },

  SET_CURRENT_PANORAMA (state, currentPanorama) {
    state.currentPanorama = currentPanorama
  },

  SET_TARGET_PANORAMA (state, targetPanorama) {
    state.targetPanorama = targetPanorama
  },

  SET_SELECTED_MARKER (state, selectedMarker) {
    state.selectedMarker = selectedMarker
  },

  ADD_APP_MARKER (state, newAppMarkerType) {
    let newAppMarker = {}
    let selectedRotation = null
    // if (state.savedMarkerRotation) selectedRotation = state.savedMarkerRotation
    // else selectedRotation = state.cameraRotation.y
    selectedRotation = state.cameraRotation.y
    console.log('ROTATION:', selectedRotation)
    switch (newAppMarkerType) {
      case 'point':
        newAppMarker = {
          initId: makeid(),
          objectId: 'none',
          forward_x: 0,
          forward_y: 0,
          forward_z: 0,
          currPanoId: '',
          nextPanoId: '',
          rotation: 0,
          text: '',
          type: 'point',
          isDeleted: false,
          x: util.genAppMarkerPostion(selectedRotation).x,
          z: util.genAppMarkerPostion(selectedRotation).z,
          y: util.genAppMarkerPostion(state.cameraRotation.x).y
        }
        break
      case 'tag':
        newAppMarker = {
          initId: makeid(),
          objectId: 'none',
          currPanoId: '',
          title: '',
          description: '',
          price: '',
          photo: '',
          type: 'tag',
          action: 0,
          url: '',
          isDeleted: false,
          x: util.genAppMarkerPostion(state.cameraRotation.y).x,
          z: util.genAppMarkerPostion(state.cameraRotation.y).z,
          y: util.genAppMarkerPostion(state.cameraRotation.x).y
        }
        break
      case 'memo':
        newAppMarker = {
          initId: makeid(),
          objectId: 'none',
          description: '',
          currPanoId: '',
          type: 'memo',
          isDeleted: false,
          x: util.genAppMarkerPostion(state.cameraRotation.y).x,
          z: util.genAppMarkerPostion(state.cameraRotation.y).z,
          y: util.genAppMarkerPostion(state.cameraRotation.x).y
        }
        break
    }
    const foundPanorama = state.panoramas.filter(panorama => {
      return panorama.id === state.selectedPanorama.id
    })
    foundPanorama[0].markers.push(newAppMarker)
    // state.selectedMarker = newAppMarker

    function makeid () {
      var text = ''
      var possible = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789'
      for (var i = 0; i < 5; i++) {
        text += possible.charAt(Math.floor(Math.random() * possible.length))
      }
      return text
    }
  },

  DELETE_APP_MARKER (state) {
    const foundMarker = state.selectedPanorama.markers.filter(marker => {
      return marker.initId === state.selectedMarker.initId
    })
    if (foundMarker[0].objectId === 'none') {
      const filteredMarkers = state.selectedPanorama.markers.filter(marker => {
        return marker.initId !== foundMarker[0].initId
      })
      state.selectedPanorama.markers = filteredMarkers
    } else {
      foundMarker[0].isDeleted = true
    }
  },

  UPDATE_ALL_APP_MARKERS (state, markers) {
    state.selectedPanorama.markers = markers
  },

  UPDATE_APP_MARKER (state, marker, x, y, z) {
    marker.x = x
    marker.y = y
    marker.z = z
    marker.isUnity = false // need this to prevent marker from disappearing
  },

  UPDATE_APP_MARKER_ROTATION (state, marker, cameraRotation) {
    marker.forward_y = cameraRotation.y
  },

  // UPDATE_APP_MARKER_PANO_ID (state, marker, panoId) {
  //   marker.panoId = panoId
  // },

  UPDATE_APP_MARKER_NEXT_PANO (state, marker, nextPanoId) {
    marker.nextPanoId = nextPanoId
  },

  SET_EDITOR_STATE (state, editorState) {
    state.editorState = editorState
  },

  SET_HAS_ADDED_MARKER (state, isSet) {
    state.hasAddedMarker = isSet
  },

  SET_HAS_CLICKED_ADD_MARKER (state, isSet) {
    state.hasClickedAddMarker = isSet
  },

  GET_CAMERA_CONTAINER_EL (state, cameraContainerEl) {
    state.cameraContainerEl = cameraContainerEl
  },

  SET_CAMERA_EL_ROTATION (state, cameraContainerEl, cameraRotation) {
    state.cameraContainerEl.setAttribute('rotation', cameraRotation)
  },

  SET_CAMERA_EL_ORIG_ROTATION (state) {
    state.cameraElOrigRotation = {
      x: 0,
      y: state.cameraElRotation.y + state.cameraContainerElRotation.y,
      z: 0
    }
  },

  GET_CAMERA_EL_ROTATION (state, cameraEl) {
    state.cameraElRotation = cameraEl.getAttribute('rotation')
  },

  GET_CAMERA_CONTAINER_EL_ROTATION (state, cameraContainerEl) {
    state.cameraContainerElRotation = cameraContainerEl.getAttribute('rotation')
  },

  SET_IS_EDITOR_SCENE_READY (state, isSet) {
    state.isEditorSceneReady = isSet
  },

  SET_EDITOR_LOADING (state, loading) {
    state.isLoading = loading
  },

  SET_EDITOR_ALERT (state, alertParams) {
    state.alertParams = alertParams
  },

  SET_IS_IMAGE_LOADING (state, isSet) {
    state.isImageLoading = isSet
  },

  SET_IS_UPLOADING_MARKER (state, isSet) {
    state.isUploadingMarker = isSet
  },

  SET_TAG_PHOTO (state, photo) {
    const foundMarker = state.selectedPanorama.markers.filter(marker => {
      return marker.initId === state.selectedMarker.initId
    })
    foundMarker[0].photo = photo
  },

  SET_SORTED_PANORAMAS (state, panoramas) {
    state.sortedPanoramas = panoramas.slice()
  },

  // SORT_PANORAMAS (state, arr, fromIndex, toIndex) {
  //   const element = arr[fromIndex]
  //   arr.splice(fromIndex, 1)
  //   arr.splice(toIndex, 0, element)
  // },

  ASSIGN_INDEX_TO_PANORAMAS (state, panoramas, oldIndex, newIndex) {
    let i = 0
    const element = panoramas[oldIndex]
    panoramas.splice(oldIndex, 1)
    panoramas.splice(newIndex, 0, element)
    panoramas.forEach(panorama => {
      console.log(panorama.name)
      panorama.index = i++
    })
  },

  SET_HAS_SORT_TRIGGERED (state, isSet) {
    state.hasSortTriggered = isSet
  },

  RESET_ALL (state) {
    state.shareCodes = []
    state.buildings = []
    state.profilePhoto = ''
    state.user = {}
  }
}

export default new Vuex.Store({
  state,
  mutations
})



