<template>
  <div id="panorama-prev">
    <div class="subheader clearfix col-md-12">
      <h1 class="header-left">{{ $t('panoramaPreview') }}</h1>
      <!-- <p>{{ $t('panoramaInstruction') }}</p> -->
    </div>
    <div class="panorama-container col-md-12">
      <div class="plane-container">
        <!-- <div class="line-grid" v-if="isLineGridToggled"></div>
        <i class="fa fa-th" v-if="item.web" @click="toggleIsLineGrid"></i> -->
        <a-scene
          id="panos"
          v-el:panos
          v-if="item.web && isSceneReady"
          vr-mode-ui="enabled: false">
          <a-assets @loaded="setLoadedImage" timeout="100000">
            <img
              id="panorama-image"
              :src="setImgSize(item.web)"
              crossorigin="anonymous">
          </a-assets>
          <a-sky
            radius="5000"
            class="pano"
            src="#panorama-image">
          </a-sky>
          <a-entity
            v-el:camera-container
            rotation="0 0 0"
            @componentchanged="onCameraChange($event)">
            <a-camera
              v-el:camera
              drag-look-controls
              rotation="0 0 0">
            </a-camera>
          </a-entity>
        </a-scene>
        <form class="panorama-prev-submit" v-if="item && item.web">
          <div class="form-group">
            <input
              type="submit"
              class="btn btn-primary"
              :class="{ 'disabled': isImageLoading }"
              :value="$t('updateDirection')"
              @click.prevent="update()">
            <input
              type="submit"
              :class="{ 'disabled': isImageLoading }"
              class="btn btn-default"
              :value="$t('resetDirection')"
              @click.prevent="reset()">
          </div>
        </form>
        <div class="alert alert-warning" v-if="noResizing && !isImageLoading">
          {{ $t('invalidPanorama') }}
        </div>
        <div
          class="in-progress"
          v-if="isImageLoading"
          :class="['in-progress', resizingFailed ? 'in-progress-error' : '']">
          <div class="in-progress-els">
            <i class="fa fa-circle-o-notch fa-spin fa-3x fa-fw"></i>
            <!-- <i class="fa fa-frown-o" v-if="resizingFailed"></i> -->
            <!-- <h4 v-if="resizingFailed">{{ $t('resizingFailed') }}</h4> -->
            <p>{{ $t('loadingMessage') }}<p>
            <!-- <p v-if="resizingFailed">{{{ $t('resizingFailedInstruction') }}}<p> -->
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import 'aframe'
import util from '../util'
import 'aframe-drag-look-controls-component'
import {
  cameraRotation,
  panoramas,
  activeLoading,
  isUpdatedPanorama,
  isImageLoading
} from '../vuex/getters'

import {
  getCameraRotation,
  setIsLoading,
  setAlertParams,
  setIsImageLoading
} from '../vuex/actions'

export default {
  vuex: {
    getters: {
      activeLoading,
      cameraRotation,
      panoramas,
      isUpdatedPanorama,
      isImageLoading
    },
    actions: {
      getCameraRotation,
      setIsLoading,
      setAlertParams,
      setIsImageLoading
    }
  },

  props: {
    item: Object
  },

  data () {
    return {
      cameraElRotation: null,
      cameraContainerElRotation: null,
      isLineGridToggled: false,
      isSceneReady: true,
      panoramaLoading: require('../assets/panorama-loading.png'),
      panoramaImage: null
    }
  },

  computed: {
    noResizing () {
      return this.item.resizeProgress === 0
    },

    isResizing () {
      const isResizing = this.item.resizeProgress === 1
      const noWebOrThumb = this.item.web === '' || this.item.thumbnail === ''
      return isResizing && noWebOrThumb
    },

    resizingFailed () {
      return this.item.resizeProgress === -1
    }
  },

  ready () {
    if (this.item.web) this.setIsImageLoading(true)
    this.$nextTick(() => {
      // TODO find a way better way of doing this
      if (!this.$els.camera) return
      this.cameraElRotation = this.getCameraElRotation(this.$els.camera)
      this.cameraContainerElRotation = this.getCameraElRotation(this.$els.cameraContainer)
      this.setCameraElStartRotation()
    })
  },

  watch: {
    panoramas () {
      // to reset the rotation of the panorama
      if (this.isUpdatedPanorama) {
        this.setIsLoading(false)
        this.setAlertParams({ type: 'success', text: 'panoramaUpdated', show: true })
      }
      if (!this.$els.camera || this.item.panoRotation !== 0) return
      this.isSceneReady = false
      this.$nextTick(() => { this.isSceneReady = true })
    },

    isSceneReady () {
      this.cameraElRotation = this.getCameraElRotation(this.$els.camera)
      const cameraRotation = { x: 0, y: this.cameraElRotation.y, z: 0 }
      this.setCameraElRotation(this.$els.cameraContainer, cameraRotation)
    }
  },

  methods: {
    update () {
      this.$emit('update')
    },

    reset () {
      this.$emit('reset')
    },

    setImgSize (image) {
      return util.setLeancloudImgSize(image, '1024', '512')
    },

    setLoadedImage (e) {
      this.setIsImageLoading(false)
    },

    toggleIsLineGrid () {
      this.isLineGridToggled = !this.isLineGridToggled
    },

    setCameraElStartRotation () {
      const cameraRotation = { x: 0, y: this.item.panoRotation, z: 0 }
      this.setCameraElRotation(this.$els.cameraContainer, cameraRotation)
    },

    onCameraChange (event) {
      const oldData = event.detail.oldData
      const newData = event.detail.newData
      this.cameraElRotation = this.getCameraElRotation(this.$els.camera)
      this.cameraContainerElRotation = this.getCameraElRotation(this.$els.cameraContainer)
      if (oldData.y !== newData.y) {
        this.getCameraRotation(this.cameraElRotation, this.cameraContainerElRotation)
      }
    },

    getCameraElRotation (cameraEl) {
      return cameraEl.getAttribute('rotation')
    },

    setCameraElRotation (cameraEl, cameraRotation) {
      const joinedCameraRotation = `${cameraRotation.x} ${cameraRotation.y} ${cameraRotation.z}`
      cameraEl.setAttribute('rotation', joinedCameraRotation)
    }
  }
}
</script>

<style lang="stylus">
@import '../variables'
@import '../mixins'

$panorama-height = 400px

canvas {
  cursor: default !important
}

#panorama-prev {
  height: $panorama-height
}

.panorama-container {
  height: $panorama-height
  padding: 0
  border-radius($border-radius)

  .plane-container {
    p {
      margin: 0
      text-align: center
    }
  }

  iframe {
    display: block
    margin: 0 auto 10px
    position: relative
    width: 100%
    height: 300px
  }

  .in-progress {
    background: $brand-color
    height: $panorama-height
    position: relative
    text-align: center

    i {
      color: alpha($white, 50%)
      font-size: 40px
      margin-bottom: 8px
    }

    h4 {
      color: $white
      margin-top: 5px
      text-transform: uppercase
    }

    p {
      color: $white
    }

    .in-progress-els {
      position: absolute
      top: 50%
      left: 50%
      transform: translate(-50%, -50%)
    }

    &.in-progress-error {
      background: $red
    }
  }

  .line-grid {
    background-size: 100px 100px
    background-image:
      linear-gradient(to right, $gray-lighter 1px, transparent 1px),
      linear-gradient(to bottom, $gray-lighter 1px, transparent 1px)
    pointer-events: none
    position: absolute
    width: 100%
    height: 100%
    z-index: 9999
  }

  .fa-th {
    color: $white
    cursor: pointer
    display: block
    font-size: 18px
    top: 15px
    right: 15px
    position: absolute
    text-shadow: 0 1px 6px rgba(0,0,0,.6)
    z-index: 9999

    &:hover {
      color: $brand-color
    }
  }
}

.panorama-prev-submit {
  position: absolute
  bottom: -15px
  left: 50%
  transform: translate(-50%,-50%)
  z-index: 9999

  input[type="submit"] {
    opacity: .8
  }
}
</style>



