import Vue from 'vue'
import VueI18n from 'vue-i18n'
import Router from 'vue-router'
import { sync } from 'vuex-router-sync'
import Resource from 'vue-resource'
import locales from './locales'
import { active, activeLink, drag } from './directives'
import { camelCaseToText, urlPathToFileName } from './filters'
import App from './App'
import Login from './pages/Login'
import ShareCodes from './pages/ShareCodes'
import ShareCode from './pages/ShareCode'
import Buildings from './pages/Buildings'
import Building from './pages/Building'
import Panorama from './pages/Panorama'
import ForgotPass from './pages/ForgotPass'
import ResetPass from './pages/ResetPass'
import EmailVerified from './pages/EmailVerified'
import Profile from './pages/Profile'
import Signup from './pages/Signup'
import ManageAgents from './pages/ManageAgents'
import SystemMaintenance from './pages/SystemMaintenance'
import store from './vuex/store'
import Sortable from 'vue-sortable'

Vue.use(VueI18n)
Vue.use(Router)
Vue.use(Resource)
Vue.use(Sortable)

Vue.filter('camelCaseToText', camelCaseToText)
Vue.filter('urlPathToFileName', urlPathToFileName)
Vue.directive('active', active)
Vue.directive('active-link', activeLink)
Vue.directive('drag', drag)

Vue.config.debug = true

Vue.config.lang = 'en'

Object.keys(locales).forEach(lang => {
  Vue.locale(lang, locales[lang])
})

// export var router = new Router()
export var router = new Router()

router.map({
  '/': {
    component: Login
  },

  '/forgot-pass': {
    component: ForgotPass
  },

  '/reset-pass/:nonce': {
    component: ResetPass
  },

  '/email-verified/:nonce': {
    component: EmailVerified
  },

  '/share-codes': {
    component: ShareCodes
  },

  '/share-codes/:shareCodeId': {
    component: ShareCode
  },

  '/buildings': {
    component: Buildings
  },

  '/buildings/:buildingId': {
    component: Building
  },

  '/buildings/:buildingId/panoramas/:panoramaId': {
    component: Panorama
  },

  '/share-codes/:shareCodeId/buildings/:buildingId': {
    component: Building
  },

  '/share-codes/:shareCodeId/buildings/:buildingId/panoramas/:panoramaId': {
    component: Panorama
  },

  '/profile': {
    component: Profile
  },

  '/signup': {
    component: Signup
  },

  '/manage-agents': {
    component: ManageAgents
  },

  '/system-maintenance': {
    component: SystemMaintenance
  }
})

router.afterEach(function () {
  window.scrollTo(0, 0)
  // reset the body's class
  const body = document.getElementsByTagName('body')[0]
  body.className = ''
})

router.redirect({
  '*': '/buildings'
})

sync(store, router)

router.start(App, '#app')



