<template>
  <div id="app">
    <navbar></navbar>
    <div class="container">
      <div class="row">
        <router-view
          class="view"
          transition
          transition-mode="out-in">
        </router-view>
      </div>
    </div>
    <i-footer></i-footer>
    <alert v-show="alertParams.show"></alert>
  </div>
</template>

<script>
import store from './vuex/store'
import util from './util'
import Navbar from './components/Navbar'
import IFooter from './components/IFooter'
import Alert from './components/Alert'
import auth from './auth'
import { user, buildings, alertParams } from './vuex/getters'
import {
  fetchBuildings,
  fetchShareCodes,
  checkBuildings,
  checkShareCodes,
  setUserInfo,
  setUserLeanAuth,
  setProfilePhoto,
  setAlertParams,
  setIsLoading
} from './vuex/actions'

export default {
  store,
  components: {
    Navbar,
    IFooter,
    Alert
  },

  vuex: {
    getters: {
      user,
      buildings,
      alertParams
    },
    actions: {
      fetchBuildings,
      fetchShareCodes,
      checkBuildings,
      checkShareCodes,
      setUserInfo,
      setUserLeanAuth,
      setProfilePhoto,
      setAlertParams,
      setIsLoading
    }
  },

  // computed: {
  //   fetchNotReady () {
  //     return !this.shareCodes &&
  //       !this.shareCodes.length &&
  //       !this.building &&
  //       !this.buildings.length
  //   }
  // },

  created () {
    const browserLang = util.getBrowserLang()
    util.setLang(browserLang)
  },

  ready () {
    auth.checkAuth()
    auth.checkLeanAuth(authenticated => {
      this.setUserLeanAuth(authenticated)
    })
    if (auth.user.authenticated) {
      this.setUserInfo(auth.user)
      // this.fetchShareCodes(() => this.checkShareCodes())
      this.fetchBuildings(err => {
        this.checkBuildings()
        if (err) {
          console.log('update building failed E: ', err)
          this.setAlertParams({ type: 'danger', text: 'errorOccurred', show: true })
        }
      })
      this.setProfilePhoto()
      if (this.$route.path === '/') this.$route.router.go('/buildings')
    }
  }
}
</script>

<style lang="stylus">
@import './global'

.bg-brand {
  background: $brand-color

  .navbar {
    display: none
  }
}

// override aframe's CSS

html {
  position: relative !important
}

body {
  margin-bottom: 100px !important
  overflow: auto !important
}

</style>



