<template>
  <div id="profile" class="col-md-12">
    <alert
      v-if="alert && processingForm === 'update-user'"
      :alert="alert"
      @on-click="alert = null">
    </alert>
    <div class="subheader clearfix">
      <img
        v-if="!isEmptyObj(user)"
        class="img-circle"
        :src="profilePhoto">
      <h1
        v-if="!isEmptyObj(user)"
        class="header-center">
        {{ user ? user.name : '' }}
      </h1>
    </div>
    <form-builder
      :object="user"
      :fields="formFields"
      :has-delete="true"
      @on-change="validatePhoto"
      @on-submit="updateUser">
    </form-builder>
    <hr>
    <alert
      v-if="alert && processingForm === 'change-password'"
      :alert="alert"
      @on-click="alert = null">
    </alert>
    <div class="subheader clearfix">
      <h1 class="header-left">{{ $t('changePassword') }}</h1>
    </div>
    <form-builder
      :object="changePasswordInfo"
      :fields="changePasswordFormFields"
      @on-submit="changePassword">
    </form-builder>
    <loading v-show="isLoading" color="brand"></loading>
    <spinner v-if="!user"></spinner>
    <alert v-show="alertParams.show"></alert>
  </div>
</template>

<script>
import _ from 'lodash'
import util from '../util'
import FormBuilder from '../components/FormBuilder'
import Alert from '../components/Alert'
import Spinner from '../components/Spinner'
import Loading from '../components/Loading'
import auth from '../auth'
import api from '../api'
import {
  user,
  hasFieldChanged,
  isLoading,
  activeLoading,
  profilePhoto,
  changePasswordInfo,
  alertParams,
  uploadURLs
} from '../vuex/getters'

import {
  setUserInfo,
  setUserPhoto,
  clearChangePassword,
  setProfilePhoto,
  setAlertParams,
  setIsLoading,
  doImageUpload
} from '../vuex/actions'

export default {
  name: 'Profile',
  components: {
    FormBuilder,
    Alert,
    Spinner,
    Loading
  },

  vuex: {
    getters: {
      user,
      hasFieldChanged,
      isLoading,
      activeLoading,
      profilePhoto,
      changePasswordInfo,
      alertParams,
      uploadURLs
    },
    actions: {
      setUserInfo,
      setUserPhoto,
      clearChangePassword,
      setProfilePhoto,
      setAlertParams,
      setIsLoading,
      doImageUpload
    }
  },

  data () {
    return {
      formFields: [
        { name: 'name', type: 'text', required: true, minLength: 3, text: 'yourName' },
        { name: 'company', type: 'text' },
        { name: 'country' },
        { name: 'phone', type: 'text' },
        { name: 'photo', type: 'file', text: 'profilePhoto' }
      ],
      changePasswordFormFields: [
        { name: 'oldPassword', type: 'password', required: true, minLength: 3 },
        { name: 'newPassword', type: 'password', required: true, minLength: 3 },
        { name: 'confirmNewPassword', type: 'password', required: true, minLength: 3 }
      ],
      alert: null,
      processingForm: null,
      userUpdatePayload: Object
    }
  },

  watch: {
    uploadURLs (val, oldVal) {
      this.userUpdatePayload.photo = val[0]
      this.updateUserFileMetadata(this.userUpdatePayload)
    }
  },

  methods: {
    isEmptyObj (user) {
      return _.isEmpty(user)
    },

    validatePhoto () {
      const photoFile = util.getFileFromInput('photo')
      const maxFileSize = 1048576 // 1MB
      if (photoFile.size > maxFileSize) {
        this.setAlertParams({ type: 'danger', text: 'photoError', show: true })
        return
      }
      util.setFileLinkName('photo-link', photoFile.name)
    },

    updateUser () {
      this.processingForm = 'update-user'
      this.setIsLoading(true)
      const photoFile = util.getFileFromInput('photo')
      // let formData = null
      this.userUpdatePayload = {
        name: this.user.name,
        company: this.user.company,
        country: this.user.country,
        phone: this.user.phone,
        newPassword: this.user.newPassword,
        confirmNewPassword: this.user.confirmNewPassword
      }

      if (photoFile) {
        // payload.profileUrl = photoFile
        // formData = new FormData() // eslint-disable-line
        // formData.append('photo', photoFile)
        this.doImageUpload(photoFile, [])
      } else {
        return api.updateUserProfile(this.userUpdatePayload).then(result => {
          this.setIsLoading(false)
          this.setAlertParams({ type: 'success', text: 'userUpdated', show: true })
        }, err => {
          console.log(err)
          this.setIsLoading(false)
          this.setAlertParams({ type: 'danger', text: 'errorOccurred', show: true })
        })
      }
    },

    updateUserFileMetadata (userUpdatePayload) {
      return api.updateUserProfile(this.userUpdatePayload).then(result => {
        this.setUserLocalStorage(result, this.userUpdatePayload)
        this.setIsLoading(false)
        this.setAlertParams({ type: 'success', text: 'userUpdated', show: true })
      }, err => {
        console.log(err)
        this.setIsLoading(false)
        this.setAlertParams({ type: 'danger', text: 'errorOccurred', show: true })
      })
    },

    changePassword () {
      this.processingForm = 'change-password'
      const oldPassword = this.changePasswordInfo.oldPassword
      const newPassword = this.changePasswordInfo.newPassword
      const confirmNewPassword = this.changePasswordInfo.confirmNewPassword
      if (oldPassword && newPassword && newPassword !== confirmNewPassword) {
        this.setAlertParams({ type: 'danger', text: 'newPassMatchFailed', show: true })
        return
      } else {
        const creds = {
          username: this.user.username,
          oldPassword,
          newPassword
        }
        this.setIsLoading(true)
        auth.changePassword(this, creds).then(result => {
          console.log('result: ', result)
          this.setIsLoading(false)
          this.setAlertParams({ type: 'success', text: 'passwordUpdated', show: true })
        }, err => {
          console.log(err)
          this.setIsLoading(false)
          this.setAlertParams({ type: 'danger', text: 'passwordError', show: true })
        })
      }
    },

    setUserLocalStorage (result, payload) {
      let photoUrl = null
      if (payload.photo) photoUrl = payload.photo
      // provide an empty string to avoid saving undefined as a string
      const userInfo = {
        name: result.data.name,
        company: result.data.company || '',
        country: result.data.country || '',
        phone: result.data.phone || '',
        photo: photoUrl || ''
      }
      auth.setUserLocalStorage(userInfo)
      if (payload.photo) {
        this.setUserPhoto(photoUrl)
        this.setProfilePhoto()
      }
    }
  }
}
</script>



