<template>
  <div class="action-buttons">
    <a
      class="delete-button"
      v-show="editorState === 2"
      @click="handleDeleteButton">
      <span class="fa-stack fa-lg delete-marker-button delete-button-group">
        <i class="fa fa-circle fa-stack-1x"></i>
        <i class="fa fa-times fa-stack-1x"></i>
      </span>
    </a>
    <a class="undo-button"
      v-show="(editorState === 2 && !hasAddedMarker) || editorState === 4"
      @click="undo">
      <span class="fa-stack fa-lg undo-button-group">
        <i class="fa fa-circle fa-stack-1x"></i>
        <i class="fa fa-2x fa-undo fa-stack-1x"></i>
      </span>
    </a>
    <a class="save-rotation-button"
      v-show="editorState === 2 || editorState === 4"
      @click="onConfirm(selectedMarker)">
      <span class="fa-stack fa-lg">
        <i class="fa fa-circle fa-stack-1x"></i>
        <i class="fa fa-check-circle fa-stack-1x"></i>
      </span>
    </a>
  </div>
</template>

<script>
import {
  editorState,
  isEditorSceneReady,
  hasAddedMarker,
  selectedMarker,
  selectedPanorama,
  cameraContainerEl,
  cameraElOrigRotation,
  cameraElRotation,
  cameraContainerElRotation
} from '../../vuex/getters'

import {
  setSelectedMarker,
  setEditorState,
  setHasAddedMarker,
  setIsEditorSceneReady,
  setIsImageLoading,
  setAlertParams,
  updateAppMarkerRotation,
  // updateAppMarkerNextPano,
  updateAppMarkerNextPano,
  deleteAppMarker,
  // deleteTag,
  // deleteLink,
  undoSetRotation,
  resetCamera,
  saveMarkerRotation
} from '../../vuex/actions'

export default {
  vuex: {
    getters: {
      editorState,
      isEditorSceneReady,
      hasAddedMarker,
      selectedPanorama,
      selectedMarker,
      cameraContainerEl,
      cameraElOrigRotation,
      cameraElRotation,
      cameraContainerElRotation
    },
    actions: {
      setSelectedMarker,
      setEditorState,
      setHasAddedMarker,
      setIsImageLoading,
      setIsEditorSceneReady,
      setAlertParams,
      updateAppMarkerRotation,
      // updateAppMarkerPanoId,
      updateAppMarkerNextPano,
      deleteAppMarker,
      // deleteTag,
      // deleteLink,
      undoSetRotation,
      resetCamera,
      saveMarkerRotation
    }
  },

  methods: {
    undo () {
      // this.setEditorState(1)
      // this.undoSetRotation(this)
      if (this.editorState === 2) {
        this.setEditorState(1)
        this.setSelectedMarker({})
      } else if (this.editorState === 4) {
        this.setEditorState(3)
        this.undoSetRotation(this)
      }
    },

    onConfirm (selectedMarker) {
      if (this.editorState === 2) {
        this.setEditorState(3)
      } else if (this.editorState === 4) {
        const totalCameraRotation = {
          x: 0,
          y: this.cameraElRotation.y + this.cameraContainerElRotation.y,
          z: 0
        }
        if (this.hasAddedMarker) this.setHasAddedMarker(false)
        this.setEditorState(1)
        this.setIsEditorSceneReady(false)
        this.setIsImageLoading(true)
        this.updateAppMarkerRotation(selectedMarker, totalCameraRotation)
        // this.updateAppMarkerPanoId(selectedMarker, this.selectedPanorama.id)
        this.updateAppMarkerNextPano(selectedMarker, this.selectedPanorama.id)
        // without this the position of the added marker is set to forward_y
        // when the camera comes back to the old position
        // this.saveMarkerRotation(selectedMarker)
        this.undoSetRotation(this)
        this.setSelectedMarker({})
        this.$nextTick(() => {
          this.setIsEditorSceneReady(true)
        })
        // this.setAlertParams({ type: 'success', text: 'markerUpdated', show: true })
      }
    },

    handleDeleteButton () {
      this.setEditorState(-1)
      // this.deleteAppMarker(this.selectedMarker)
      // if (this.hasAddedMarker) this.setHasAddedMarker(false)
      // this.setEditorState(1)
      // this.setSelectedMarker({})
    }

    // isShowSaveButton () {
    //   if (this.hasAddedMarker) {
    //     if (this.editorState === 2) return true
    //     else return false
    //   } else {
    //     if (this.editorState) return true
    //     else return false
    //   }
    // }
  }
}
</script>

<style lang="stylus">
@import '../../variables'

.action-buttons {
  position: absolute
  bottom: 20px
  left: 50%
  transform: translateX(-50%)
  padding: 0 2px // to fix cut-off icon

  > a {
    display: inline-block
    margin: 0 5px
    opacity: .8

    &:hover {
      opacity: 1
    }

    /*&:last-child {*/
      /*margin-left: 50px*/
    /*}*/
  }
}

.fa-stack {
  .fa-circle {
    color: $white
    font-size: 36px
  }

  .fa-arrow-circle-right,
  .fa-check-circle,
  .fa-arrow-circle-left  {
    border-radius: 50%
    font-size: 43px
  }

  &.undo-button-group {
    .fa-circle {
      color: $gray-light
      font-size: 43px
    }
  }

  .fa-undo {
    color: $white
    font-size: 27px

    &:hover {
      color: $white
    }
  }

  .fa-check-circle,
  .fa-arrow-circle-right {
    color: $brand-color
  }

  .fa-arrow-circle-left  {
    color: $gray-light
  }
}

/*.undo-button {
  .fa-circle {
    color: $gray-light
    font-size: 46px

    &:hover {
      color: $gray-light
    }
  }

  .fa-arrow-circle-left  {
    color: $white
    font-size: 28px
    margin-left: 6px

    &:hover {
      color: $white
    }
  }
}*/

.delete-button {
  .fa-circle {
    color: $red
    font-size: 45px

    &:hover {
      color: $red
    }
  }

  .fa-times {
    color: $white
    font-size: 28px
    margin-left: 4px
    margin-top: 0px

    &:hover {
      color: $white
    }
  }
}

</style>



