<template>
  <div id="editor" class="col-md-12">
    <!-- <alert v-if="alert" :alert="alert" @on-click="alert = null"></alert> -->
    <div class="subheader clearfix">
      <h1 class="header-left">{{ $t('panoramaEditor') }}</h1>
    </div>
    <div class="plane-container">
      <div
        class="plane"
        :style="{ backgroundImage: 'url(' + buildingFloorplan + ')' }">
        <p v-if="!buildingFloorplan">{{ $t('uploadFloorplan') }}</p>
        <div
          v-el:marker
          v-for="panorama in panoramas"
          :id='panorama.id'
          :style="{ left: panorama.x + 'px', top: panorama.y + 'px' }"
          :class="{'hidden': shouldHidePanorama(panorama) }"
          class="marker"
          v-drag>
          <div class="marker-elements">
            <div class="rotation-instruction" v-if="panorama.web">
              <div
                class="photo-container"
                :style="setPanoramaStyle(panorama)">
                <!-- <img :src="panorama.web"> -->
              </div>
              <p>{{{ $t('panoramaEditorTooltip') }}}</p>
              <i class="fa fa-caret-down"></i>
            </div>
            <i
              class="fa fa-circle rotation-circle"
              :style="{ transform: 'rotate(' + panorama.rotation + 'rad)' }">
              <img :src="panoramaRange">
            </i>
            <h4>{{ panorama.name }}</h4>
          </div>
        </div>
      </div>
    </div>
    <form>
      <div class="form-group">
        <input
          type="submit"
          class="btn btn-primary"
          :value="$t('updateFloorplan')"
          @click.prevent="updatePanoramas">
      </div>
      <!-- <loading v-show="activeLoading === 'bulkLoading'" color="brand"></loading> -->
    </form>
  </div>
</template>

<script>
import $ from 'jquery'
import 'aframe'
// import Alert from '../components/Alert'
// import Loading from '../components/Loading'
import AlertParams from '../components/Alert'
import EditorLoading from '../components/EditorLoading'
import api from '../api'
import {
  building,
  panoramas,
  // activeLoading
  isLoading,
  alertParams
} from '../vuex/getters'

import {
  setMarkers,
  setRotation,
  fetchBuildings,
  fetchBuildingById,
  // setActiveLoading,
  // clearActiveLoading
  setIsLoading,
  setAlertParams
} from '../vuex/actions'

export default {
  components: {
    // Alert,
    // Loading
    EditorLoading,
    AlertParams
  },

  props: {
    currentPanorama: Object,
    btnText: String
  },

  vuex: {
    getters: {
      building,
      panoramas,
      // activeLoading
      alertParams,
      isLoading
    },
    actions: {
      setMarkers,
      setRotation,
      fetchBuildings,
      fetchBuildingById,
      // setActiveLoading,
      // clearActiveLoading
      setIsLoading,
      setAlertParams
    }
  },

  data () {
    return {
      // alert: null,
      buildingId: '',
      buildingFloorplan: '',
      isMarkerFormLoading: false,
      panoramaRange: require('../assets/range.png')
    }
  },

  created () {
    this.buildingId = this.$route.params.buildingId
    if (this.building) this.setFloorplan()
  },

  watch: {
    building (value) {
      if (value) this.setFloorplan()
    }

    // panoramas (value) {
    //   if (value.length === 0) return
    //   this.$nextTick(() => {
    //     const numberRE = /[+-]?\d+(\.\d+)?/g
    //     const child = this.$el.querySelector('.rotation-circle')
    //     const childRotation = child.style.transform
    //     const childRotationNumber = childRotation.match(numberRE).map(v => {
    //       return parseFloat(v)
    //     })
    //     // the if pevents the error that happens when reloading Panorama and going
    //     // to the previous page
    //     $('.marker').draggable({
    //       containment: $('.marker').parent(),
    //       drag: dragListener
    //     })
    //     $('.rotation-circle').rotatable({
    //       containment: $('.marker').parent(),
    //       angle: childRotationNumber[0],
    //       rotate: rotateListener
    //     })
    //
    //     const self = this
    //     function dragListener (e) { self.dragEvent(e) }
    //     function rotateListener (e) { self.rotateEvent(e) }
    //   })
    // }
  },

  events: {
    'drag-event': function (ev) {
      const target = ev.target
      const $target = $(ev.target)
      const childPos = $target.offset()
      const parentPos = $target.parent().offset()
      let x = childPos.left - parentPos.left
      let y = childPos.top - parentPos.top
      this.panoramas.forEach(el => {
        if (el.id === target.id) {
          // TODO find a way to remove this hack
          // if (x > 401) { ev.preventDefault(); x = 400 }
          // if (y > 371) { ev.preventDefault(); y = 370 }
          // TODO think how to pass this function as a parameter
          this.setMarkers(el, x, y)
        }
      })
    },

    'rotate-event': function (ev) {
      const target = ev.target
      const $target = $(ev.target)
      const $targetParent = $target.closest('.marker')
      const numberRE = /[+-]?\d+(\.\d+)?/g
      const targetRotation = target.style.transform
      const targetRotationNumber = targetRotation.match(numberRE).map(v => {
        return parseFloat(v)
      })
      this.panoramas.forEach(el => {
        if (el.id === $targetParent.attr('id')) {
          // TODO think how to pass this function as a parameter
          // 1 Radian = 57.2958 Degree
          // const rotationDegrees = rotationNumber[0] * (180 / Math.PI)
          this.setRotation(el, targetRotationNumber[0])
        }
      })
    }
  },

  methods: {
    // dragEvent (e) {
    //   const target = e.target
    //   const $target = $(e.target)
    //   const childPos = $target.offset()
    //   const parentPos = $target.parent().offset()
    //   let x = childPos.left - parentPos.left
    //   let y = childPos.top - parentPos.top
    //   this.panoramas.forEach(el => {
    //     if (el.id === target.id) {
    //       // TODO find a way to remove this hack
    //       if (x > 401) { e.preventDefault(); x = 400 }
    //       if (y > 371) { e.preventDefault(); y = 370 }
    //       // TODO think how to pass this function as a parameter
    //       this.setMarkers(el, parseInt(x, 10), parseInt(y, 10))
    //     }
    //   })
    // },

    // rotateEvent (e) {
    //   const target = e.target
    //   const $target = $(e.target)
    //   const $targetParent = $target.closest('.marker')
    //   const numberRE = /[+-]?\d+(\.\d+)?/g
    //   const targetRotation = target.style.transform
    //   const targetRotationNumber = targetRotation.match(numberRE).map(v => {
    //     return parseFloat(v)
    //   })
    //   this.panoramas.forEach(el => {
    //     if (el.id === $targetParent.attr('id')) {
    //       // TODO think how to pass this function as a parameter
    //       // 1 Radian = 57.2958 Degree
    //       // const rotationDegrees = rotationNumber[0] * (180 / Math.PI)
    //       this.setRotation(el, targetRotationNumber[0])
    //     }
    //   })
    // },

    setPanoramaStyle (panorama) {
      const baseRotation = 2.8 // 1000 / 360
      const positionOffset = 535
      const calcPosition = (panorama.panoRotation * baseRotation) - positionOffset
      return {
        backgroundImage: 'url(' + panorama.web + ')',
        backgroundPosition: `${calcPosition}px -153px`
      }
    },

    setFloorplan () {
      this.buildingFloorplan = this.building.floorplan
    },

    updatePanoramas () {
      // need to turn the values into integers here, because doing it
      // before causes a bug in IE
      this.panoramas.forEach(panorama => {
        panorama.x = parseInt(panorama.x)
        panorama.y = parseInt(panorama.y)
      })
      const payload = {
        panos: this.panoramas.filter(panorama => panorama.isDirty === true)
      }
      if (payload.panos.length > 0) {
        // this.setActiveLoading('bulkLoading')
        this.setIsLoading(true)
        api.bulkUpdatePanorama(payload).then(result => {
          this.fetchBuildingById(this.building.id, err => {
            if (err) {
              console.log('An error ocurred: ', err)
              this.setAlertParams({ type: 'danger', text: 'errorOccurred', show: true })
            }
          })
          // this.clearActiveLoading()
          // this.alert = { type: 'success', text: 'panoramaUpdated' }
          this.setIsLoading(false)
          this.setAlertParams({ type: 'success', text: 'panoramaUpdated', show: true })
        }, err => {
          console.log('An error occurred: ', err)
          this.setIsLoading(false)
          this.setAlertParams({ type: 'danger', text: 'errorOccurred', show: true })
        })
      } else {
        this.setIsLoading(false)
        // this.alert = { type: 'warning', text: 'noChanges' }
        this.setAlertParams({ type: 'warning', text: 'noChanges', show: true })
      }
    },

    shouldHidePanorama (panorama) {
      return this.currentPanorama && panorama !== this.currentPanorama
    }
  }
}
</script>

<style lang="stylus">
@import '../variables'
@import '../mixins'

#editor {
  text-align: center

  .plane-container {
    background-color: $gray-lighter
    margin: 0 0 15px
    padding: 20px 0
    border-radius($border-radius)
  }

  .plane {
    margin: 0 auto
    overflow: visible
    position: relative
    width: 400px
    height: 400px
    background-repeat: no-repeat
    background-size:  100% 100%
    /*background-color: $gray-light*/

    > p {
      color: $gray-light
      font-size: 16px
      position: absolute
      top: 50%
      left: 50%
      transform: translate(-50%,-50%)
    }
  }

  .marker {
    font-size: $marker-size
    display: inline-block
    cursor: pointer
    position: absolute !important // need !important to override jQuery's default
    height: $marker-size // need this because Bootstrap adds extra height
    line-height: $marker-size
    z-index: 999

    .marker-elements {
      // Add 36px offset on Y
      top: -13px
      left: 50%
      position: absolute
      transform: translateX(-50%)

      > i {
        color: $brand-color
        text-shadow: 0 1px 6px rgba(0,0,0,0.3)

        img {
          opacity: 0
          position: absolute
          top: -53px
          left: 50%
          transform: translateX(-50%)
        }
      }
    }

    .rotation-instruction {
      background: $default-color
      color: $white
      opacity: 0
      font-size: 14px
      line-height: 20px
      position: absolute
      top: -330px
      left: 50%
      transform: translateX(-50%)
      transition: all 0.2s ease
      pointer-events: none
      border-radius($border-radius)

      .photo-container {
        background-size: 1000px 500px
        background-repeat: repeat
        width: 440px
        height: 200px
        overflow: hidden
        border-radius($border-radius)
      }

      p {
        margin: 10px
      }

      i {
        color: $default-color
        font-size: 50px
        position: absolute
        bottom: -30px
        left: 50%
        transform: translateX(-50%)
      }
    }

    .ui-rotatable-handle {
      width: 20px
      height: 20px
      top: -40px
      left: 50%
      opacity: 0
      position: absolute
      transform: translateX(-50%)
      transition: all 0.2s ease

      i {
        color: $default-color
        font-size: 21px
        text-shadow: 0 0 0 $default-color
      }
    }

    h4 {
      background: alpha($default-color, 30%)
      color: white
      font-size: 14px
      padding: 2px 5px
      margin: 0
      white-space: nowrap
      border-radius(10px)
    }

    &:hover {
      i img {
        opacity: .5
      }
      .ui-rotatable-handle {
        opacity: .4
      }

      .rotation-instruction {
        opacity: 1
      }
    }

    &:.hidden {
      display: none
    }
  }
}
</style>



