<template>
  <div id="share-codes" class="col-md-12">
    <list
      :class-name="shareCodes"
      :fields="shareCodeFields"
      default-sort-key="date">
    </list>
    <spinner v-if="!areShareCodesFetched"></spinner>
  </div>
</template>

<script>
import List from '../components/List'
import Modal from '../components/Modal'
import Alert from '../components/Alert'
import Spinner from '../components/Spinner'
import FormBuilder from '../components/FormBuilder'
import Auth from '../mixins/Auth'
import api from '../api'
import { shareCodes, areShareCodesFetched } from '../vuex/getters'

export default {
  name: 'ShareCodes',
  mixins: [
    Auth
  ],

  components: {
    List,
    Modal,
    Alert,
    Spinner,
    FormBuilder
  },

  vuex: {
    getters: {
      shareCodes,
      areShareCodesFetched
    }
  },

  data () {
    return {
      props: [
        'number',
        'date',
        'email'
      ],
      shareCodeFields: [
        { name: 'number', column: 2, hasLink: true },
        { name: 'buildings', column: 3, isArray: true },
        { name: 'emails', column: 4, isArray: true },
        { name: 'date', column: 3, isDetail: true }
      ],
      alertAction: {
        target: 'Share code',
        type: 'success',
        text: 'created'
      }
    }
  },

  methods: {
    deleteShareCode () {
      api.deleteShareCode()
    }
  }
}
</script>



