<template>
  <div id="share-code" class="col-md-12">
    <div class="subheader clearfix">
      <h1
        v-if="!isEmptyObj(shareCode)"
        class="header-center">
        {{ shareCode.number }}
      </h1>
    </div>
    <div class="row">
      <hr>
      <buildings :props="props" :code-buildings="codeBuildings"></buildings>
    </div>
  </div>
</template>

<script>
import _ from 'lodash'
import Buildings from '../pages/Buildings'
import FormBuilder from '../components/FormBuilder'
import Spinner from '../components/Spinner'
import Auth from '../mixins/Auth'
import {
  shareCodes,
  shareCode,
  codeBuildings
} from '../vuex/getters'

import {
  fetchBuildings,
  fetchShareCodeById,
  fetchBuildingsByCodeId
} from '../vuex/actions'

export default {
  name: 'ShareCode',
  mixins: [Auth],

  components: {
    FormBuilder,
    Buildings,
    Spinner
  },

  vuex: {
    getters: {
      shareCodes,
      shareCode,
      codeBuildings
    },
    actions: {
      fetchBuildings,
      fetchShareCodeById,
      fetchBuildingsByCodeId
    }
  },

  data () {
    return {
      shareCodeId: '',
      props: [
        'name',
        'categoryCount',
        'panoramaCount'
      ]
    }
  },

  watch: {
    shareCodes (val, oldVal) {
      this.fetchShareCodeById(this.shareCodeId)
      this.fetchBuildingsByCodeId(this.shareCodeId)
    }
  },

  route: {
    data ({ to }) {
      return this.fetchBuildings(() => {
        const shareCodeId = to.params.shareCodeId
        this.fetchShareCodeById(shareCodeId)
        this.fetchBuildingsByCodeId(shareCodeId)
        return {
          shareCodeId: shareCodeId
        }
      })
    }
  },

  methods: {
    isEmptyObj (shareCode) {
      return _.isEmpty(shareCode)
    }
  }
}
</script>

<style lang="stylus">
  /*#share-code {
    .col-md-1 {
      display: none // temportary fix
    }
  }*/
</style>



