<template>
  <div id="building" class="col-md-12">
    <!-- <alert v-if="alert" :alert="alert" @on-click="alert = null"></alert> -->
    <div class="subheader clearfix">
      <!-- loop through all the categories and display the thumbnail of the
      fist one -->
      <img
        v-if="!isEmptyObj(building) && isArrayWithValue($key, value)"
        v-for="value in building"
        class="img-circle"
        :src="getObjsWithThumbnails($key, building)
          ? getObjsWithThumbnails($key, building)[0].thumbnail
          : ''">
      <h1
        v-if="!isEmptyObj(building)"
        class="header-center">
        {{ building ? building.name : '' }}
      </h1>
      <building-buttons v-if="building">
      </building-buttons>
    </div>
    <progress-bar :file-progress="fileProgress"></progress-bar>
    <form-builder
      :object="building"
      :fields="formFields"
      :has-delete="true"
      btn-text="updateDetails"
      del-text="deleteBuilding"
      @on-change="validateFloorplan"
      @on-submit="updateBuilding"
      @on-delete="deleteBuilding">
    </form-builder>
    <modal
      v-show="activeModal === 'deleteBuildingConfirm'"
      modal-title="confirmDelete"
      :post-save="deleteBuildingConfirm">
      <p>{{ $t('confirmDeleteBuilding') }}</p>
    </modal>
    <div class="row">
      <hr>
      <panoramas></panoramas>
    </div>
    <br>
    <div class="row">
      <hr>
      <panorama-editor></panorama-editor>
    </div>
    <div class="row">
      <hr>
      <marker-editor></marker-editor>
    </div>
    <div class="row">
      <hr>
      <comments-editor></comments-editor>
    </div>
    <spinner v-if="isEmptyObj(building)"></spinner>
    <loading v-show="isLoading" color="brand"></loading>
    <alert v-show="alertParams.show"></alert>
  </div>
</template>

<script>
import _ from 'lodash'
import firebase from 'firebase'
import util from '../util'
import BuildingButtons from '../pages/BuildingButtons'
import PanoramaEditor from '../pages/PanoramaEditor'
import MarkerEditor from '../pages/MarkerEditor'
import CommentsEditor from '../pages/CommentsEditor'
import Panoramas from '../pages/Panoramas'
import FormBuilder from '../components/FormBuilder'
import Alert from '../components/Alert'
import Loading from '../components/Loading'
// import AlertParams from '../components/Alert'
// import EditorLoading from '../components/EditorLoading'
import Modal from '../components/Modal'
import Spinner from '../components/Spinner'
import ProgressBar from '../components/ProgressBar'
import Auth from '../mixins/Auth'
import api from '../api'
import {
  buildings,
  building,
  panoramas,
  activeModal,
  // activeLoading,
  hasFieldChanged,
  isNewBuilding,
  isLoading,
  alertParams,
  isUpdatedPanorama,
  uploadURLs,
  isUploadingMarker
} from '../vuex/getters'

import {
  fetchBuildings,
  removeBuildingFromState,
  fetchBuildingById,
  fetchPanoramasByBuildingId,
  setActiveModal,
  setIsLoading,
  setAlertParams,
  clearActiveModal,
  // setActiveLoading,
  // clearActiveLoading,
  checkBuildings,
  fetchFirebaseComments,
  setIsNewBuilding,
  doImageUpload,
  setIsUpdatedPanorama,
  clearBuilding,
  clearPanoramas,
  updateBuildingsState
} from '../vuex/actions'

export default {
  name: 'Building',
  mixins: [
    Auth
  ],

  components: {
    FormBuilder,
    PanoramaEditor,
    MarkerEditor,
    CommentsEditor,
    Panoramas,
    Alert,
    Loading,
    // EditorLoading,
    // AlertParams,
    Modal,
    Spinner,
    BuildingButtons,
    ProgressBar
  },

  vuex: {
    getters: {
      buildings,
      building,
      panoramas,
      activeModal,
      // activeLoading,
      hasFieldChanged,
      isNewBuilding,
      isLoading,
      isUpdatedPanorama,
      isUploadingMarker,
      alertParams,
      uploadURLs
    },
    actions: {
      doImageUpload,
      fetchBuildings,
      removeBuildingFromState,
      fetchBuildingById,
      fetchPanoramasByBuildingId,
      setActiveModal,
      clearActiveModal,
      // setActiveLoading,
      setIsLoading,
      setAlertParams,
      // clearActiveLoading,
      checkBuildings,
      clearPanoramas,
      fetchFirebaseComments,
      setIsNewBuilding,
      setIsUpdatedPanorama,
      clearBuilding,
      updateBuildingsState
    }
  },

  data () {
    return {
      buildingId: '',
      // TODO only shows the categories that exist in the object
      props: [],
      formFields: [
        { name: 'name', type: 'text', required: true, minLength: 2 },
        { name: 'description', type: 'text' },
        { name: 'viewerLink', type: 'text' },
        { name: 'floorplan', type: 'file', description: 'floorplanHint' }
      ],
      // alert: null,
      // alertParams: null,
      // activeAlert: '',
      filename: '',
      fileProgress: -1,
      buildingUpdatePayload: Object
    }
  },

  watch: {
    building: function (val, oldVal) {
      if (!val) return
      this.fetchPanoramasByBuildingId(this.buildingId)
      const commentRef = firebase.database().ref('comments/' + this.building.code)
      commentRef.on('child_added', snapshot => {
        this.fetchFirebaseComments(this.building.code)
      })
      commentRef.on('child_removed', snapshot => {
        this.fetchFirebaseComments(this.building.code)
      })
    },

    uploadURLs (val, oldVal) {
      if (this.isUploadingMarker) return
      this.buildingUpdatePayload.floorplan = val[0]
      this.fileProgress = 100
      this.updateBuildingFileMetadata(this.buildingId, this.buildingUpdatePayload)
    }
  },

  route: {
    data (transition) {
      // TODO remove warning when reloading this page
      const buildingId = transition.to.params.buildingId
      if (!this.isNewBuilding) {
        this.fetchBuildingById(buildingId, err => {
          if (err) {
            console.log('An error ocurred: ', err)
            this.setAlertParams({ type: 'danger', text: 'errorOccurred', show: true })
          }
        })
      }
      if (this.building) this.fetchFirebaseComments(this.building.code)
      this.setIsNewBuilding(false)
      this.setIsUpdatedPanorama(false)
      return { buildingId: buildingId }
    },

    deactivate (transition) {
      this.clearBuilding()
      this.clearPanoramas()
      transition.next()
    }
  },

  computed: {
    categories () {
      return api.categories
    }
  },

  ready () {
    this.props = this.categories
  },

  methods: {
    hasAtLeastOnePanorama (building) {
      return building.panoramas[0]
    },

    isArrayWithValue (key, value) {
      const propertyIsThumbnail = _.find(value, 'thumbnail')
      if (propertyIsThumbnail) {
        if (value instanceof Array) return value.length > 0
      }
    },

    isEmptyObj (building) {
      return _.isEmpty(building)
    },

    getObjsWithThumbnails (key, object) {
      return util.getObjsWithThumbnails(key, object)
    },

    updateBuilding () {
      const payload = {
        name: this.building.name,
        description: this.building.description,
        floorplan: this.building.floorplan
      }

      this.buildingUpdatePayload = payload
      // this.setActiveLoading('formLoading')
      this.setIsLoading(true)
      const floorplanFile = util.getFileFromInput('floorplan')
      if (floorplanFile) {
        this.filename = floorplanFile.name
        this.fileProgress = 0
        this.doImageUpload(floorplanFile, [])
      } else {
        api.updateBuilding(this.buildingId, payload).then(result => {
          console.log('RES:', result)
          // this.fetchBuildings() // TODO: need to optimize
          this.fetchBuildingById(this.buildingId, err => {
            if (err) {
              console.log('An error ocurred: ', err)
              this.setIsLoading(false)
              this.setAlertParams({ type: 'danger', text: 'errorOccurred', show: true })
            }
          })
          this.updateBuildingsState(this.building)
          this.setIsLoading(false)
          this.setAlertParams({ type: 'success', text: 'buildingUpdated', show: true })
        }, err => {
          console.log('update building failed E: ', err)
          this.setIsLoading(false)
          this.setAlertParams({ type: 'danger', text: 'errorOccurred', show: true })
        })
      }
    },

    updateBuildingFileMetadata (buildId, payload) {
      api.updateBuilding(buildId, payload).then(result => {
        // TODO: need to optimize
        this.fetchBuildingById(this.buildingId, err => {
          if (err) {
            console.log('An error ocurred: ', err)
            this.setIsLoading(false)
            this.setAlertParams({ type: 'danger', text: 'errorOccurred', show: true })
            return
          }
          this.updateBuildingsState(this.building)
          this.fileProgress = -1
          this.setIsLoading(false)
          this.setAlertParams({ type: 'success', text: 'buildingUpdated', show: true })
          util.clearFileFromInput('floorplan')
        })
      }, err => {
        console.log('update building failed E: ', err)
        this.setIsLoading(false)
        this.setAlertParams({ type: 'danger', text: 'errorOccurred', show: true })
        util.clearFileFromInput('floorplan')
      })
    },

    deleteBuilding () {
      this.setActiveModal('deleteBuildingConfirm')
    },

    deleteBuildingConfirm () {
      const payload = { visible: false }
      this.setIsLoading(true)
      return api.deleteBuilding(this.buildingId, payload).then(result => {
        const deletedBuilding = result.data.objectId
        this.removeBuildingFromState(deletedBuilding)
        this.clearActiveModal()
        this.setIsLoading(false)
        this.setAlertParams({ type: 'default', text: 'buildingDeleted', show: true })
        this.checkBuildings()
        setTimeout(() => {
          this.$route.router.go('/buildings')
        }, 2000)
        // })
      }, err => {
        console.log('update building failed E: ', err)
        this.setIsLoading(false)
        this.setAlertParams({ type: 'danger', text: 'errorOccurred', show: true })
        this.clearActiveModal()
      })
    },

    validateFloorplan () {
      const floorplanFile = util.getFileFromInput('floorplan')
      const maxFileSize = 1048576 // 1MB
      const imageExtentionRE = /\.(gif|jpg|jpeg|tiff|png)$/i
      if (floorplanFile.size > maxFileSize) {
        this.setAlertParams({ type: 'danger', text: 'floorplanError', show: true })
        util.clearFileFromInput('floorplan')
        return
      }
      if (!imageExtentionRE.test(floorplanFile.name)) {
        this.setAlertParams({ type: 'danger', text: 'panoramaInvalid', show: true })
        util.clearFileFromInput('floorplan')
        return
      }
      util.setFileLinkName('floorplan-link', floorplanFile.name)
    }
  }
}
</script>

<style lang="stylus">
#building {
  h1.header-center {
    margin-bottom: 2px
  }
}
</style>



