<template>
  <div id="editor-alert"
    class="alert"
    :class="alertParams.show ? 'active alert-' + alertParams.type : ''">
    <p>{{ $t(alertParams.text) }}</p>
  </div>
</template>

<script>
import { alertParams } from '../vuex/getters'
import { setAlertParams } from '../vuex/actions'
export default {
  vuex: {
    getters: {
      alertParams
    },
    actions: {
      setAlertParams
    }
  },
  watch: {
    alertParams () {
      if (!this.alertParams.show) return
      if (this.timer) clearTimeout(this.timer)
      this.timer = setTimeout(() => {
        this.setAlertParams({ type: '', text: '', show: false })
      }, 2000)
    }
  }
}
</script>

<style lang="stylus">
@import '../variables'

#editor-alert {
  opacity: 0
  /*width: 100%*/
  max-width: 400px
  padding-left: 45px
  padding-right: 45px
  position: fixed
  top: 50%
  left: 50%
  /*top: 40px*/
  transition: all .3s ease
  transform: translate(-50%, -50%)
  z-index: 9999
  text-align: center

  p {
    white-space: nowrap
  }

  &.active {
    -webkit-animation: fade 2s
    -moz-animation: fade 2s
    -o-animation: fade 2s
    animation: fade 2s
  }
}

@-webkit-keyframes fade {
  0%   { opacity: 0 }
  50%  { opacity: 1 }
  100% { opacity: 0 }
}
@-moz-keyframes fade {
  0%   { opacity: 0 }
  50%  { opacity: 1 }
  100% { opacity: 0 }
}
@-o-keyframes fade {
  0%   { opacity: 0 }
  50%  { opacity: 1 }
  100% { opacity: 0 }
}
@keyframes fade {
  0%   { opacity: 0 }
  50%  { opacity: 1 }
  100% { opacity: 0 }
}
</style>



