<template>
  <div id="buildings" class="col-md-12">
    <!-- <alert v-if="alert" :alert="alert" @on-click="alert = null">
    </alert> -->
    <list
      :class-name="selectedBuildings"
      object-name="panorama"
      :fields="buildingFields"
      :actions="buildingActions"
      :has-thumbnails="true"
      :has-sorting="true"
      default-sort-key="date">
    </list>
    <spinner v-if="!areBuildingsFetched"></spinner>
    <modal
      v-show="activeModal === 'deleteConfirm'"
      modal-title="confirmDelete"
      :post-save="deleteBuildingConfirm">
      <p>{{ $t('confirmDeleteBuilding') }}</p>
      <!-- <loading v-show="activeLoading === 'deleteLoading'" color="brand">
      </loading> -->
    </modal>
    <modal
      v-show="activeModal === 'shareConfirm'"
      modal-title="Viewer link">
      <a
        class="share-link"
        :href="currentBuilding ? currentBuilding.shareCode : ''"
        target="_blank">
        {{ currentBuilding ? currentBuilding.shareCode : '' }}
      </a>
      <share-buttons
        :url="currentBuilding ? currentBuilding.shareCode : ''">
      </share-buttons>
    </modal>
    <alert v-show="alertParams.show"></alert>
    <loading v-show="isLoading" color="brand"></loading>
  </div>
</template>

<script>
import api from '../api'
import FormBuilder from '../components/FormBuilder'
import List from '../components/List'
import Alert from '../components/Alert'
import Modal from '../components/Modal'
import Spinner from '../components/Spinner'
import Loading from '../components/Loading'
import ShareButtons from '../components/ShareButtons'
import Auth from '../mixins/Auth'
import {
  user,
  buildings,
  activeModal,
  activeLoading,
  currentBuildingId,
  areBuildingsFetched,
  alertParams,
  isLoading
} from '../vuex/getters'

import {
  fetchBuildings,
  checkBuildings,
  removeBuildingFromState,
  setActiveModal,
  clearActiveModal,
  // setActiveLoading,
  // clearActiveLoading,
  setCurrentBuildingId,
  setAlertParams,
  setIsLoading
} from '../vuex/actions'

export default {
  name: 'Buildings',
  mixins: [
    Auth
  ],

  components: {
    List,
    Alert,
    Modal,
    Spinner,
    Loading,
    FormBuilder,
    ShareButtons
  },

  props: {
    codeBuildings: Array
  },

  vuex: {
    getters: {
      user,
      buildings,
      activeModal,
      activeLoading,
      currentBuildingId,
      areBuildingsFetched,
      alertParams,
      isLoading
    },
    actions: {
      fetchBuildings,
      checkBuildings,
      removeBuildingFromState,
      setActiveModal,
      clearActiveModal,
      // setActiveLoading,
      // clearActiveLoading,
      setCurrentBuildingId,
      setAlertParams,
      setIsLoading
    }
  },

  data () {
    const fields = [
      { name: 'panoramas', column: 1, isThumbnail: true },
      { name: 'name', column: 2, hasLink: true },
      { name: 'date', column: 2, isDetail: true }
    ]
    return {
      props: [
        'name',
        'date',
        'user'
      ],
      buildingFields: fields,
      buildingActions: {
        delete: { submit: this.deleteBuilding },
        share: { submit: this.shareBuilding }
      },
      currentBuilding: null
      // alert: null
    }
  },

  computed: {
    selectedBuildings () {
      const parentName = this.$parent.$options.name
      if (parentName === 'ShareCode') return this.codeBuildings
      else return this.buildings
    }
  },

  ready () {
    this.addFieldsByRole()
  },

  methods: {
    addFieldsByRole () {
      if (this.user.role === '' ||
        this.user.role === 'normal' ||
        this.user.role === 'agent' ||
        this.user.role === 'shopifyAgent') {
        const description = { name: 'description', column: 4, isDetail: true }
        this.buildingFields.splice(-1, -2, description)
      }
      if (this.user.role === 'admin') {
        const user = { name: 'user', column: 2, isDetail: true }
        const description = { name: 'description', column: 2, isDetail: true }
        this.buildingFields.splice(-1, -2, description)
        this.buildingFields.splice(-1, -2, user)
      }
    },

    shareBuilding (buildingId) {
      this.setActiveModal('shareConfirm')
      const foundBuilding = this.buildings.filter(building => {
        return building.id === buildingId
      })
      this.currentBuilding = foundBuilding[0]
    },

    deleteBuilding (id) {
      this.setCurrentBuildingId(id)
      this.setActiveModal('deleteConfirm')
    },

    deleteBuildingConfirm () {
      const payload = { visible: false }
      this.setIsLoading(true)
      return api.deleteBuilding(this.currentBuildingId, payload).then(result => {
        const deletedBuilding = result.data.objectId
        this.removeBuildingFromState(deletedBuilding)
        // this.fetchBuildings(() => {
        this.setAlertParams({ type: 'default', text: 'buildingDeleted', show: true })
        this.clearActiveModal()
        this.setIsLoading(false)
        this.checkBuildings()
        // })
      }, err => {
        console.log('update building failed E: ', err)
        // this.clearActiveLoading()
        this.setIsLoading(false)
        this.setAlertParams({ type: 'danger', text: 'errorOccurred', show: true })
        this.clearActiveModal()
      })
    }
  }
}
</script>



