<template>
  <div class="row" v-sortable="options">
    <labels
      :fields="fields"
      :sortable="sortable"
      :has-thumbnails="hasThumbnails"
      :default-sort-key="defaultSortKey"
      :default-sort-order="defaultSortOrder">
    </labels>
    <div
      class="list-item col-md-12 clearfix"
      v-if="className.length === 0">
      <br>
      <div class="alert alert-warning">
        {{ $t('noItems') }}
      </div>
    </div>
    <div
      v-for="object in className | orderBy sortKey sortOrder"
      class="list-item list-draggable col-md-12 clearfix">
    <!-- <div
      v-for="object in className"
      class="list-item list-draggable col-md-12 clearfix"> -->
      <i class="fa fa-th list-grip" v-if="sortable"></i>
      <thumbnail-error
        :object-name="objectName"
        v-if="hasNoCategories(object)">
      </thumbnail-error>
      <div
        v-if="isKeyInFields($key) && isValidValue($key, value, object)"
        v-for="value in object"
        :class="setColumnsByKey($key)">
        <thumbnail
          v-if="keyIsThumbnail($key)"
          :value="value"
          :fields="fields"
          :object="object">
        </thumbnail>
        <name
          v-if="keyHasLink($key)"
          :value="value"
          :fields="fields"
          :object="object">
        </name>
        <detail
          v-if="keyIsDetail($key)"
          :key="$key"
          :value="value"
          :fields="fields">
        </detail>
        <tags
          v-if="keyIsArray($key)"
          :key="$key"
          :value="value"
          :fields="fields">
        </tags>
        <checkbox
          v-if="keyIsCheckbox($key)"
          :key="$key"
          :value.sync="value"
          :fields="fields">
        </checkbox>
      </div>
      <div v-if="object.shareCode" class="col-md-1 col-del text-right">
        <a
          :href="object.shareCode"
          target="_blank"
          @click="showBrandModal(object.shareCode, $event)">
          <i class="fa fa-eye"></i>
        </a>
      </div>
      <div v-if="actions && actions.share" class="col-md-1 col-del text-right">
        <a @click="actions.share.submit(object.id)">
          <i class="fa fa-share-alt"></i>
        </a>
      </div>
      <div v-if="actions && actions.delete" class="col-md-1 col-del text-right">
        <a @click="actions.delete.submit(object.id)">
          <i class="fa fa-times-circle"></i>
        </a>
      </div>
      <modal
        v-show="activeModal === object.shareCode"
        class="choose-brand"
        modal-title="selectLink"
        :no-confirm="true">
        <h5>{{ $t('branded') }}</h5>
        <a :href="object.shareCode" target="_blank">
          {{ object.shareCode }}
        </a>
        <h5>{{ $t('noBranded') }}</h5>
        <a :href="buildingShareCodeNoBrand(object)" target="_blank">
          {{ buildingShareCodeNoBrand(object) }}
        </a>
      </modal>
    </div>
    <div v-if="actions && actions.add" class="col-md-12 col-add">
      <input
        type="submit"
        :value="$t(actions.add.name)"
        class="btn btn-primary"
        @click="actions.add.submit()">
    </div>
  </div>
</template>

<script>
import util from '../../util'
import api from '../../api'
// import { sort, sortList } from '../../vuex/getters'
import { sort, user, activeModal } from '../../vuex/getters'
import {
  setIsLoading,
  setAlertParams,
  fetchBuildings,
  setSortedPanoramas,
  setHasSortTriggered,
  assignIndexToPanoramas,
  setActiveModal
} from '../../vuex/actions'

import Modal from '../../components/Modal'
import Thumbnail from './Thumbnail'
import ThumbnailError from './ThumbnailError'
import Name from './Name'
import Detail from './Detail'
import Tags from './Tags'
import Labels from './Labels'
import Checkbox from './Checkbox'

export default {
  name: 'List',
  components: {
    Thumbnail,
    ThumbnailError,
    Name,
    Detail,
    Tags,
    Labels,
    Checkbox,
    Modal
  },

  props: {
    className: Array,
    sortedClassName: Array,
    objectName: String,
    actions: Object,
    fields: Array,
    hasThumbnails: Boolean,
    defaultSortKey: String,
    defaultSortOrder: Number,
    sortable: Boolean
  },

  vuex: {
    getters: {
      sort,
      user,
      activeModal
    },
    actions: {
      setIsLoading,
      setAlertParams,
      fetchBuildings,
      // sortPanoramas,
      setSortedPanoramas,
      setHasSortTriggered,
      assignIndexToPanoramas,
      setActiveModal
    }
  },

  data () {
    return {
      tooltipVisible: false,
      options: {
        handle: '.list-grip',
        draggable: '.list-draggable',
        onEnd: this.onSortEnd
      }
    }
  },

  computed: {
    sortKey () {
      if (this.sortable) return
      return this.sort.key
    },

    sortOrder () {
      if (this.sortable) return
      return this.sort.order
    },

    categories () {
      return api.categories
    }
  },

  watch: {
    className (val) {
      if (val.length > 0) {
        this.setSortedPanoramas(val)
      }
    }
  },

  methods: {
    buildingShareCodeNoBrand (object) {
      return object.shareCode + '?ui=true'
    },

    showBrandModal (shareCode, event) {
      if (this.user.country !== 'United States') return
      event.preventDefault()
      this.setActiveModal(shareCode)
    },

    onSortEnd (evt) {
      const oldIndex = evt.oldIndex - 1
      const newIndex = evt.newIndex - 1
      // this.sortPanoramas(this.sortedClassName, fromIndex, toIndex)
      this.assignIndexToPanoramas(this.sortedClassName, oldIndex, newIndex)
      this.setHasSortTriggered(true)
    },

    isKeyInFields (key) {
      let field = null
      this.fields.filter(obj => {
        if (obj.name === key) field = obj
      })
      return field
    },

    findField (key, prop) {
      let field = null
      this.fields.filter(obj => {
        if (obj.name === key) field = obj
      })
      return field ? field[prop] : ''
    },

    setColumnsByKey (key) {
      const fieldColumns = this.findField(key, 'column')
      return 'col-md-' + fieldColumns
    },

    keyIsThumbnail (key) {
      const fieldIsThumbnail = this.findField(key, 'isThumbnail')
      return fieldIsThumbnail
    },

    keyHasLink (key) {
      const fieldHasLink = this.findField(key, 'hasLink')
      return fieldHasLink
    },

    keyIsArray (key) {
      const fieldIsArray = this.findField(key, 'isArray')
      return fieldIsArray
    },

    keyIsDetail (key) {
      const fieldIsLabel = this.findField(key, 'isDetail')
      return fieldIsLabel
    },

    keyIsCheckbox (key) {
      const fieldIsLabel = this.findField(key, 'isCheckbox')
      return fieldIsLabel
    },

    hasNoCategories (object) {
      if (!this.hasThumbnails) return
      // for buildings
      for (let key in object) {
        const value = object[key]
        for (let i = 0; i < value.length; i++) {
          const item = value[i]
          if (item.thumbnail && item.thumbnail !== '') return false
        }
        // for panoramas
        if (key === 'thumbnail' && value !== '') {
          return false
        }
      }
      return true
    },

    getPropType (key) {
      return this.fields.map(field => {
        if (field.name === key) return field.type
      }).join('') // TODO find out why commas are being added
    },

    isValidValue (key, value, object) {
      if (key === 'thumbnail' && value === '') return false
      if (key === 'panoramas' && value.length === 0) return false
      if (key === 'panoramas' && value.length !== 0) {
        const currObjsWithThumbs = value.filter(obj => {
          return obj.thumbnail && obj.thumbnail !== ''
        })
        const origObjsWithThumbs = util.getObjsWithThumbnails(key, object)
        if (currObjsWithThumbs.length === 0) return false
        if (currObjsWithThumbs[0] !== origObjsWithThumbs[0]) return false
      }
      return true
    },

    updatePanoramas () {
      const payload = { panos: this.className }
      console.log('PAY: ', payload)
      if (payload.panos.length > 0) {
        // this.setActiveLoading('bulkLoading')
        this.setIsLoading(true)
        api.bulkUpdatePanorama(payload).then(result => {
          this.fetchBuildings()
          // this.clearActiveLoading()
          // this.alert = { type: 'success', text: 'panoramaUpdated' }
          this.setIsLoading(false)
          this.setAlertParams({ type: 'success', text: 'panoramaUpdated', show: true })
        })
      } else {
        this.setIsLoading(false)
        // this.alert = { type: 'warning', text: 'noChanges' }
        this.setAlertParams({ type: 'warning', text: 'noChanges', show: true })
      }
    }
  }
}
</script>

<style lang="stylus">
@import '../../variables'
@import '../../mixins'

.list-labels div.col-md-1:last-child {
  padding-top: 15px
}

.list-item {
  border-bottom: 1px solid $gray-lighter
  padding: 0

  .modal {
    text-align: center
  }

  .list-grip {
    cursor: move
    position: absolute
    top: 50%
    left: 0
    transform: translateY(-50%) rotate(90deg)
    z-index: 9999
  }

  h2,
  h3,
  h4 {
    margin: 0
  }

  input[type="checkbox"] {
    margin-top: 10px
  }

  img {
    margin: 0 0 0 20px
    width: 38px
    height: 38px
  }

  ul {
    display: inline-block
    list-style-type: none
    margin: 0
    padding-left: 0

    li {
      display: inline-block
      margin-left: 6px

      &:first-child {
        margin-left: 0
      }

      &:not(:first-child):after {
        content: ", "
      }

      &:last-child:after {
        content: ""
      }
    }
  }

  em {
    color: $gray
  }

  div.col-md-1:first-of-type {
    padding-top: 10px
    padding-bottom: 10px
  }

  > div {
    padding-top: 15px // TODO think of a better way to vertically center the divs
    padding-bottom: 15px // TODO think of a better way to vertically center the divs
  }

  .col-del {
    text-align: right

    a {
      display: inline-block
    }

    i {
      color: $gray-light
      font-size: 18px

      &:hover {
        color: $brand-color
      }
    }
  }

  .col-error {
    a {
      i {
        color: $yellow
        font-size: 38px
        line-height: 38px
      }

      > div {
        background: $default-color
        color: $white
        display: none
        padding: 5px 10px
        position: absolute
        top: 12px
        left: 62px
        z-index: 9999

        p {
          color: $gray-lighter
          font-size: 13px
          margin: 0
          white-space: nowrap
        }

        i {
          color: $default-color
          font-size: 18px
          position: absolute
          top: 50%
          left: -5px
          -webkit-transform: translateY(-50%)
          transform: translateY(-50%)
        }
      }

      &:hover {
        > div {
          display: block
        }
      }
    }
  }

  &:last-child {
    border-bottom: 0
  }

  &.list-labels {
    /*background: $cloud-light*/
    border-bottom-width: 2px
    color: $gray
    padding: 0

    a {
      color: $gray
      font-size: 13px
      text-transform: uppercase

      &:hover {
        color: $default-color
        text-decoration: none
      }

      i {
        margin-left: 5px
      }
    }
  }

  .empty-value {
    color: $gray
  }

  &.sortable-ghost {
    opacity: .4
  }

  &.sortable-chosen {
    background: #ddd
  }
}

.col-add input {
  margin-top: 15px
}

.choose-brand h5 {
  text-transform: uppercase
}
</style>



