import Vue from 'vue'

const util = {}

// util.isMobile = () => {
//   const phones = /Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i
//   return phones.test(navigator.userAgent)
// }

util.stripCommas = (string) => {
  return string.replace(/,/g, '')
}

util.capitalize = (string) => {
  return string.charAt(0).toUpperCase() + string.slice(1)
}

// TODO turn all these functions into two

util.getThumbnail = (object) => {
  let category = []
  for (var key in object) {
    if (object[key] instanceof Array && object[key].length >= 1) {
      category.push(object[key])
    }
  }
  return category[0]
}

util.getFirstThumbnail = (object) => {
  let category = []
  for (var key in object) {
    if (object[key] instanceof Array && object[key].length >= 1) {
      category.push(object[key])
    }
  }
  if (category[0] && category[0][0]) return category[0][0].thumbnail
  else return ''
}

util.getObjsWithThumbnails = (key, object) => {
  for (let key in object) {
    if (key === 'panoramas' && object[key].length !== 0) {
      const objsWithThumbs = object[key].filter(obj => {
        return obj.thumbnail && obj.thumbnail !== ''
      })
      return objsWithThumbs
    }
  }
}

util.isFirstThumbnail = (key, object) => {
  const value = object[key]
  if (key === 'panoramas' && value.length !== 0) {
    return value === util.getThumbnail(object)
  }
  return true // to prevent hiding the other objects
}

util.getFileFromInput = (input) => {
  const floorplan = document.getElementsByName(input)[0]
  return floorplan.files[0]
}

util.clearFileFromInput = (input) => {
  const floorplan = document.getElementsByName(input)[0]
  floorplan.value = ''
}

util.setFileLinkName = (input, file) => {
  const floorplanLink = document.getElementsByName(input)[0]
  floorplanLink.placeholder = file
}

util.getParentElements = (self) => {
  const htmlItem = self.el.parentElement
  const htmlList = htmlItem.parentElement.children
  return [].slice.call(htmlList)
}

util.addClassToParent = (self) => {
  self.el.parentElement.className += ' active'
}

util.resetActiveClass = (arr) => {
  arr.forEach(item => {
    item.className = ''
  })
}

util.PascalToParamCase = (str) => {
  return str.replace(/([A-Z])/g, (str) => {
    return '-' + str.toLowerCase()
  }).substring(1)
}

util.getBrowserLang = () => {
  return navigator.languages
    ? navigator.languages[0]
    : (navigator.language || navigator.userLanguage)
}

util.setLang = (lang) => {
  let selectedLang = ''
  // const english = ['en', 'en-us', 'en-uk']
  const chinese = ['zh', 'zh-tw', 'zh-cn']
  // if (english.indexOf(lang.toLowerCase()) > -1) selectedLang = 'en'
  if (chinese.indexOf(lang.toLowerCase()) > -1) selectedLang = 'zh'
  else selectedLang = 'en'
  Vue.config.lang = selectedLang
}

util.genAppMarkerPostion = (deg) => {
  const position = {}
  const rad = deg * (Math.PI / 180)
  let x = 0
  let z = 0
  let y = 0
  x = -Math.sin(rad)
  z = -Math.cos(rad)
  y = Math.sin(rad)
  position.y = y * 200
  position.x = x * 200
  position.z = z * 200
  return position
}

util.genUnityAppMarkerPostion = (marker) => {
  const depth = 200
  const newAppMarker = {}
  newAppMarker.x = -marker.z * depth
  newAppMarker.y = marker.y * depth
  newAppMarker.z = -marker.x * depth

  return newAppMarker
}

util.setLeancloudImgSize = (imgSrc, w, h) => {
  return imgSrc.replace('w/4096/h/2048', 'w/' + w + '/h/' + h)
}

export default util



