<template>
  <div id="marker-info">
    <div
      class="marker-window panorama-menu"
      v-if="editorState === 0 ||
        (selectedMarker.type === 'point' && editorState === 3)">
      <div class="marker-window-inner">
        <a
          class="close-button"
          v-if="selectedPanorama && selectedPanorama.id"
          @click="menuClose"></a>
        <a
          v-for="panorama in panoramas"
          track-by="id"
          class="panorama-thumbnail"
          :class="{
            'current': isCurrentPanorama(panorama),
            'target': isTargetPanorama(panorama),
            'disabled': isDisabledPanorama(panorama)
          }"
          @click="selectPanorama(panorama)">
          <img
            class="thumbnail-img"
            :src="panorama.thumbnail"
            v-if="panorama.thumbnail">
          <img
            class="thumbnail-img thumbnail-default"
            :src="defaultThumbnail"
            v-if="!panorama.thumbnail">
          <span class="thumbnail-label" v-if="isCurrentPanorama(panorama)">
            {{ $t('current') }}
          </span>
          <span class="thumbnail-label" v-if="isTargetPanorama(panorama)">
            {{ $t('target') }}
          </span>
          <span class="thumbnail-text">
            {{ panorama.name }}
          </span>
        </a>
      </div>
    </div>
    <div class="marker-window tag-menu" v-if="selectedMarker.type === 'tag' && editorState === 3">
      <div class="marker-window-inner">
        <a
          class="close-button"
          @click="menuClose">
        </a>
        <form-builder
          :object="selectedMarker"
          :fields="selectedTagFields"
          :has-preview-photo="true"
          btn-text="updateMarker"
          @on-change="validatePhoto"
          @on-submit="formDone">
        </form-builder>
      </div>
    </div>
    <div class="marker-window memo-menu" v-if="selectedMarker.type === 'memo' && editorState === 3">
      <div class="marker-window-inner">
        <a
          class="close-button"
          @click="menuClose">
        </a>
        <form-builder
          :object="selectedMarker"
          :fields="selectedMemoFields"
          @on-submit="formDone">
        </form-builder>
      </div>
    </div>
    <div class="marker-window delete-menu" v-if="editorState === -1">
      <div class="marker-window-inner">
        <div class="delete-inner clearfix">
          <button
            class="col-sm-5 col-sm-offset-1 btn btn-default"
            @click="handleCancelDelete">
            {{ $t('cancel') }}
          </button>
          <button
            class="col-sm-5 btn btn-danger"
            @click="handleDeleteMarker">
            {{ $t('delete') }}
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import util from '../../util'
import FormBuilder from '../../components/FormBuilder'
import MarkerButtons from '../../pages/MarkerEditor/MarkerButtons'
import {
  panoramas,
  selectedPanorama,
  currentPanorama,
  // targetPanorama,
  selectedMarker,
  editorState,
  isEditorSceneReady,
  isUploadingMarker,
  hasAddedMarker,
  cameraContainerEl,
  cameraElRotation,
  cameraElOrigRotation,
  cameraContainerElRotation,
  uploadURLs
} from '../../vuex/getters'

import {
  setSelectedMarker,
  setEditorState,
  setHasAddedMarker,
  setSelectedPanorama,
  setCurrentPanorama,
  // setTargetPanorama,
  setAlertParams,
  setIsEditorSceneReady,
  setIsImageLoading,
  addAppMarker,
  setSceneEventCount,
  setCameraElRotation,
  setCameraElOrigRotation,
  getCameraContainerEl,
  getCameraRotation,
  deleteAppMarker,
  undoSetRotation,
  resetCamera,
  doImageUpload,
  setIsUploadingMarker,
  setTagPhoto,
  setIsLoading
} from '../../vuex/actions'

export default {
  name: 'MarkerInfo',
  vuex: {
    getters: {
      panoramas,
      selectedPanorama,
      currentPanorama,
      // targetPanorama,
      selectedMarker,
      editorState,
      isEditorSceneReady,
      isUploadingMarker,
      hasAddedMarker,
      cameraContainerEl,
      cameraElRotation,
      cameraElOrigRotation,
      cameraContainerElRotation,
      uploadURLs
    },
    actions: {
      setSelectedMarker,
      setEditorState,
      setSceneEventCount,
      setHasAddedMarker,
      setSelectedPanorama,
      setCurrentPanorama,
      // setTargetPanorama,
      setAlertParams,
      setIsEditorSceneReady,
      setIsImageLoading,
      setCameraElRotation,
      setCameraElOrigRotation,
      getCameraRotation,
      addAppMarker,
      getCameraContainerEl,
      deleteAppMarker,
      undoSetRotation,
      resetCamera,
      doImageUpload,
      setIsUploadingMarker,
      setTagPhoto,
      setIsLoading
    }
  },

  components: {
    FormBuilder,
    MarkerButtons
  },

  data () {
    return {
      selectedTagFields: [
        { name: 'title', type: 'text', required: true, minLength: 2, order: 1 },
        { name: 'description', type: 'textarea', required: true, minLength: 2, order: 2 },
        { name: 'photo', type: 'file', text: 'photo', order: 3 },
        { name: 'price', type: 'text', order: 4 },
        { name: 'action', type: 'radioMarker', order: 5 },
        { name: 'url', type: 'text', minLength: 2, order: 6 }
      ],
      selectedMemoFields: [
        { name: 'description', type: 'textarea', required: true, minLength: 2, maxLength: 100 }
      ],
      defaultThumbnail: require('../../assets/default-photo.png')
    }
  },

  watch: {
    selectedMarker: {
      handler: function (val) {
        if (this.selectedMarker.type === 'tag') {
          if (val.description.trim().length < this.selectedTagFields[1].minLength) {
            this.setHasAddedMarker(true)
          } else {
            this.setHasAddedMarker(false)
          }
        } else if (this.selectedMarker.type === 'memo') {
          if (val.description.trim().length < this.selectedMemoFields[0].minLength || val.description.trim().length > this.selectedMemoFields[0].maxLength) {
            this.setHasAddedMarker(true)
          } else {
            this.setHasAddedMarker(false)
          }
        }
      },
      deep: true
    },

    uploadURLs (val, oldVal) {
      if (!this.isUploadingMarker) return
      this.setTagPhoto(val[0])
      this.setIsUploadingMarker(false)
      this.handleMarkerAfterUpdate()
    }
  },

  methods: {
    formDone () {
      let photoFile = null
      if (this.selectedMarker.type === 'tag') {
        photoFile = util.getFileFromInput('photo')
      }
      this.setIsLoading(true)
      if (photoFile) {
        this.filename = photoFile.name
        this.setIsUploadingMarker(true)
        this.doImageUpload(photoFile, [])
      }
      if (!photoFile) this.handleMarkerAfterUpdate()
    },

    menuClose () {
      if (!this.editorState) this.setEditorState(1)
      else this.setEditorState(2)
    },

    selectPanorama (panorama) {
      if (this.editorState === 0) {
        // select current panorama
        if (this.selectedPanorama.id === panorama.id) {
          this.setEditorState(1)
          return
        }
        this.setSelectedPanorama(panorama)
        this.setCurrentPanorama(panorama)
        this.setEditorState(1)
        this.setIsEditorSceneReady(false)
        this.setIsImageLoading(true)
        this.$nextTick(() => {
          const rotationReset = { x: 0, y: 0, z: 0 }
          this.setIsEditorSceneReady(true)
          // the camera rotation is not clearing automatically
          this.getCameraRotation(rotationReset, this.cameraContainerElRotation)
          this.setSceneEventCount(this.selectedPanorama.markers.length)
        })
      } else if (this.editorState === 3) {
        // select target panorama
        if (this.selectedPanorama.id === panorama.id) {
          return
        }
        this.setSelectedPanorama(panorama)
        this.setIsImageLoading(true)
        const isCurrentPanorama = panorama === this.currentPanorama
        if (isCurrentPanorama) return
        this.setCameraElOrigRotation({
          x: 0,
          y: this.cameraElRotation.y + this.cameraContainerElRotation.y,
          z: 0
        })
        const isSelectedMarker = panorama.id === this.selectedMarker.nextPanoId
        if (isSelectedMarker) this.useSelectedMarkerRotation()
        else this.resetCamera(this)
        // this.isOverlayActive = true
        this.setEditorState(4)
        this.setIsEditorSceneReady(false)
        this.$nextTick(() => {
          this.setIsEditorSceneReady(true)
        })
      }
    },

    useSelectedMarkerRotation () {
      const cameraRotation = { x: 0, y: this.selectedMarker.forward_y, z: 0 }
      this.resetCamera(this, () => {
        // use reset version of the cameraContainer
        this.setCameraElRotation(this.cameraContainerEl, cameraRotation)
      })
    },

    handleCancelDelete () {
      this.setEditorState(2)
    },

    handleDeleteMarker () {
      this.deleteAppMarker()
      if (this.hasAddedMarker) this.setHasAddedMarker(false)
      this.setEditorState(1)
      this.setSelectedMarker({})
      // this.setAlertParams({ type: 'danger', text: 'markerDeleted', show: true })
    },

    isCurrentPanorama (panorama) {
      return panorama === this.currentPanorama
    },

    isTargetPanorama (panorama) {
      return panorama.id === this.selectedMarker.nextPanoId
    },

    isDisabledPanorama (panorama) {
      return !panorama.web
    },

    validatePhoto () {
      const photoFile = util.getFileFromInput('photo')
      const maxFileSize = 1048576 // 1MB
      if (photoFile.size > maxFileSize) {
        // this.alert = { type: 'danger', text: 'floorplanError' }
        this.setAlertParams({ type: 'danger', text: 'floorplanError', show: true })
        return
      }
      util.setFileLinkName('photo-link', photoFile.name)
    },

    handleMarkerAfterUpdate () {
      this.setSelectedMarker({})
      // this.setAlertParams({ type: 'success', text: 'markerUpdated', show: true })
      this.setEditorState(1)
      this.setIsLoading(false)
    }
  }
}
</script>

<style lang="stylus">
@import '../../variables'
@import '../../mixins'

.panorama-menu,
.tag-menu,
.memo-menu,
.delete-menu {
  top: 0
  left: 0
  right: 0
  bottom: 0
  background-color: alpha($default-color, 70%)

  .marker-window-inner {
    margin: 50px 0 0
  }
}

.panorama-menu {
  top: 0
  left: 0
  right: 0
  bottom: 0
  background-color: alpha($gray-light, 70%)

  .marker-window-inner {
    padding: 20px
  }

  .panorama-thumbnail {
    display: block
    margin: 8px
    position: relative
    display: inline-block
    width: 250px

    .thumbnail-text {
      /*background: $default-color*/
      background-color: alpha($default-color, 50%)
    }

    &:hover {
      .thumbnail-img {
        border: 1px solid $brand-color
      }
    }

    .thumbnail-label {
      color: $white
      position: absolute
      top: 50%
      left: 50%
      transform: translate(-50%,-50%)
      text-transform: uppercase
      black-text-shadow()
    }

    &.current {
      .thumbnail-img {
        opacity: .4
        border: 1px solid white
        /*cursor: not-allowed*/
        /*border: 2px solid $default-color*/
      }
    }

    &.target {
      .thumbnail-img {
        border: 1px solid $brand-color
      }

      .thumbnail-text {
        background-color: alpha($brand-color, 50%)
      }

      .thumbnail-label {
        color: $brand-color
      }
    }

    &.disabled {
      .thumbnail-img {
        opacity: .4
        border: 1px solid transparent
        pointer-events: none
        cursor: default
      }
    }
  }

  .thumbnail-img {
    width: 100%
    height: 130px
    border: 1px solid transparent
  }

  .thumbnail-text {
    color: $white
    font-size: 13px
    padding: 5px 0
    position: absolute
    bottom: 0
    text-align: center
    text-transform: uppercase
    width: 100%
  }

  .selected-panorama {
    position: absolute
    top: 50%
    left: 50%
    transform: translate(-50%,-50%)

    .fa-circle {
      color: $white
      font-size: 25px
    }

    .fa-check-circle {
      color: $brand-color
      font-size: 30px
    }
  }

  .close-button {
    right: 20px
    margin-top: -30px
  }
}

.tag-menu,
.memo-menu {
  form {
    margin: 0 auto
    width: 100%
    max-width: 320px

    .btn-default {
      background-color: $default-color !important
      border-top-color: $default-color !important
      border-left-color: $default-color !important
      border-right-color: $default-color !important
      border-bottom-color: #4d4d4d !important
    }

    .btn-primary {
      background-color: $brand-color
      border-top-color: $brand-color
      border-left-color: $brand-color
      border-right-color: $brand-color
      border-bottom-color: #3ea08d
      width: 100%
    }

    .input-group {
      width: 100%
    }

    .col-sm-offset-2 {
      paddin-left: 0
      margin-left: 0
    }

    .radio-inline {
      /*width: 30%*/
      /*vertical-align: middle*/
      margin-left: 0

      input {
        top: 50%
        transform: translateY(-50%)
      }
    }
  }

  .close-button {
    left: 50%
    margin-left: 180px
  }
}

.memo-menu {
  textarea {
    height: 200px
  }
}

.delete-menu {
  .delete-inner {
    margin: 140px auto
    width: 100%
    max-width: 320px
  }
}

</style>



