<template>
  <div id="marker-editor" class="col-md-12">
    <div class="subheader clearfix">
      <h1 class="header-left">{{ $t('markerEditor') }}</h1>
    </div>
    <div
      class="row"
      v-show="!panoramas.length">
      <div class="alert alert-warning">
        {{ $t('noItems') }}
      </div>
    </div>
    <div class="marker-container" v-show="panoramas.length">
      <p
        class="instruction-text"
        :class="{ 'delete' : editorState === -1 }">
        {{ $t(instructionText) }}
      </p>
      <!-- it throws an error if v-if is used -->
      <marker-info></marker-info>
      <marker-panoramas v-show="editorState === 2 &&
        !hasAddedMarker &&
        selectedMarker.nextPanoId &&
        selectedMarker.type === 'point'">
      </marker-panoramas>
      <a-scene vr-mode-ui="enabled: false" v-if="isEditorSceneReady">
        <a-assets
          v-if="selectedPanorama.web"
          timeout="100000"
          @loaded="setLoadedImage">
          <img
            id="panorama-image"
            :src="setImgSize(selectedPanorama.web)"
            crossorigin="anonymous">
        </a-assets>
        <a-entity
          v-el:camera-container
          rotation="0 0 0"
          @componentchanged="onCameraChange($event)">
          <a-camera
            v-el:camera
            position="0 0 0"
            rotation="0 0 0">
          </a-camera>
        </a-entity>
        <a-sky v-if="selectedPanorama.web" src="#panorama-image"></a-sky>
        <a-image
          v-for="marker in selectedPanorama.markers"
          track-by="initId"
          v-if="editorState !== 4 && !marker.isDeleted"
          :id="marker.initId"
          :src="markerImageSrc(marker)"
          :color="isSelectedMarker(marker) ? markerColor: markerInactive"
          opacity="1"
          rotation="0 0 0"
          :width="markerSize"
          :height="markerSize"
          :position="joinAppMarkerPosition(marker)"
          look-at="[camera]"
          transparent="true"
          draggable>
          <a-animation
            attribute="scale"
            from=".8 .8 1"
            to="1 1 1"
            dur="1500"
            fill="both"
            repeat="indefinite">
          </a-animation>
        </a-image>
        <!-- <a-image
          :src="logoImage"
          opacity="1"
          :width="markerSize"
          :height="markerSize"
          position="0 -200 0"
          rotation="0 0 0"
          look-at="[camera]"
          transparent="true">
        </a-image> -->
      </a-scene>
      <marker-tools v-show="editorState === 1 && !isImageLoading"></marker-tools>
      <marker-buttons v-show="!isMarkerSelected"></marker-buttons>
      <div class="marker-overlay marker-overlay-left" v-if="editorState === 4">
      </div>
      <div class="marker-overlay marker-overlay-right" v-if="editorState === 4">
      </div>
      <div
        class="in-progress"
        v-if="isImageLoading"
        :class="['in-progress', resizingFailed ? 'in-progress-error' : '']">
        <div class="in-progress-els">
          <i class="fa fa-circle-o-notch fa-spin fa-3x fa-fw"></i>
          <p>{{ $t('loadingMessage') }}<p>
        </div>
      </div>
    </div>
    <form v-show="panoramas.length">
      <div class="form-group">
        <input
          type="submit"
          class="btn btn-primary"
          :class="{ 'disabled': hasAddedMarker }"
          :value="$t('updateAllMarkers')"
          @click.prevent="handleUpdateMarkers">
      </div>
    </form>
    <!-- <pre style="float:left;">panoramas <br> {{ panoramas | json }}</pre>
    <pre style="float:left;">selectedPanorama <br>{{ selectedPanorama.markers | json }}</pre>
    <pre style="float:left;">selectedMarker <br>{{ selectedMarker | json }}</pre> -->
  </div>
</template>

<script>
import 'custom-event-polyfill'
import util from '../../util'
import api from '../../api'
import MarkerTools from '../../pages/MarkerEditor/MarkerTools'
import MarkerInfo from '../../pages/MarkerEditor/MarkerInfo'
import MarkerButtons from '../../pages/MarkerEditor/MarkerButtons'
import MarkerPanoramas from '../../pages/MarkerEditor/MarkerPanoramas'
// import 'aframe-mouse-cursor-component'
// import AFRAME from 'aframe'
// import 'aframe-drag-look-controls-component'
// import Loading from '../components/Loading'
// import AlertParams from '../../components/Alert'
// import EditorLoading from '../../components/EditorLoading'
import {
  // cameraRotation,
  building,
  panoramas,
  selectedPanorama,
  currentPanorama,
  // targetPanorama,
  // activeLoading,
  selectedMarker,
  editorState,
  sceneEventCount,
  // isEditorChangeLoading,
  hasAddedMarker,
  isEditorSceneReady,
  // isSelectedMarker,
  isLoading,
  isImageLoading,
  alertParams,
  cameraContainerEl,
  cameraElRotation,
  cameraContainerElRotation,
  uploadURLs
} from '../../vuex/getters'

import {
  getCameraRotation,
  setSelectedPanorama,
  setCurrentPanorama,
  // setTargetPanorama,
  setSelectedMarker,
  setEditorState,
  setSceneEventCount,
  setHasAddedMarker,
  setIsEditorSceneReady,
  setIsLoading,
  setAlertParams,
  setCameraElRotation,
  addAppMarker,
  updateAppMarker,
  fetchBuildings,
  fetchBuildingById,
  fetchPanoramasByBuildingId,
  getCameraContainerEl,
  getCameraElRotation,
  getCameraContainerElRotation,
  setIsImageLoading
} from '../../vuex/actions'

export default {
  name: 'MarkerEditor',
  vuex: {
    getters: {
      building,
      panoramas,
      selectedPanorama,
      currentPanorama,
      // targetPanorama,
      selectedMarker,
      editorState,
      sceneEventCount,
      hasAddedMarker,
      isEditorSceneReady,
      // isSelectedMarker,
      cameraContainerEl,
      cameraElRotation,
      cameraContainerElRotation,
      alertParams,
      isLoading,
      isImageLoading,
      uploadURLs
    },
    actions: {
      getCameraRotation,
      setSelectedPanorama,
      setCurrentPanorama,
      // setTargetPanorama,
      setSelectedMarker,
      setSceneEventCount,
      setEditorState,
      setHasAddedMarker,
      setIsEditorSceneReady,
      setIsLoading,
      setAlertParams,
      setCameraElRotation,
      addAppMarker,
      updateAppMarker,
      fetchBuildings,
      fetchBuildingById,
      fetchPanoramasByBuildingId,
      getCameraContainerEl,
      getCameraElRotation,
      getCameraContainerElRotation,
      setIsImageLoading
    }
  },

  components: {
    MarkerTools,
    MarkerInfo,
    MarkerButtons,
    MarkerPanoramas
  },

  data () {
    return {
      // isChangeLoading: false,
      payload: {
        markers: []
      },
      isMarkerSelected: false,
      clickMarkerCount: 0,
      markerSize: 30,
      markerColor: '#45b29d',
      markerInactive: '#969696',
      markerImage: require('../../assets/marker.png'),
      tagColor: '#cbad45',
      // logoImage: require('../../assets/logo.png'),
      tagImage: require('../../assets/tag.png'),
      memoImage: require('../../assets/note.png'),
      markerUpdatePayload: Object
      // 0: select pano, or add/select marker
      // 1: select target pano
      // 2: confirm marker rotation
      // 3: enter tag details
      // 4: enter link details
    }
  },

  computed: {
    instructionText () {
      switch (this.editorState) {
        case -1: return 'confirmDeleteAppMarker'
        case 0: return 'selectCurrentPanorama'
        case 1: return 'selectMarker'
        case 2: return 'positionMarker'
        case 3:
          if (this.selectedMarker.type === 'point') return 'selectTargetPanorama'
          else if (this.selectedMarker.type === 'tag') return 'enterTagDetails'
          break
        case 4: return 'rotatePanorama'
        default: return ''
      }
    }
  },

  ready () {
    this.initAframeDraggable()
    this.getCameraContainerEl(this.$els.cameraContainer)
    this.setIsEditorSceneReady(true)
    // this.setIsImageLoading(true)
  },

  destroyed () {
    // otherwise the state is preserved when looking other buildings
    this.setEditorState(0)
    this.setSelectedPanorama({})
    this.setCurrentPanorama({})
    this.setIsImageLoading(false)
    this.setIsEditorSceneReady(false)
    this.setIsEditorSceneReady(true)
  },

  watch: {
    panoramas (val) {
      if (val.length > 0) {
        // this.setSelectedPanorama(this.panoramas[0])
        // this.setCurrentPanorama(this.panoramas[0])
        this.setSelectedMarker({})
      }
    }
  },

  methods: {
    markerImageSrc (marker) {
      if (marker.type === 'point') return this.markerImage
      else if (marker.type === 'tag') return this.tagImage
      else if (marker.type === 'memo') return this.memoImage
    },

    onCameraChange (event) {
      const oldData = event.detail.oldData
      const newData = event.detail.newData
      this.getCameraElRotation(this.$els.camera)
      if (this.cameraContainerEl) {
        this.getCameraContainerElRotation(this.cameraContainerEl)
      }
      if (oldData.y !== newData.y) {
        this.getCameraRotation(this.cameraElRotation, this.cameraContainerElRotation)
      }
    },

    setImgSize (image) {
      return util.setLeancloudImgSize(image, '1024', '512')
    },

    setLoadedImage (e) {
      this.setIsImageLoading(false)
    },

    joinAppMarkerPosition (marker) {
      if (marker.isUnity) {
        const newAppMarker = util.genUnityAppMarkerPostion(marker)
        return `${newAppMarker.x} ${newAppMarker.y} ${newAppMarker.z}`
      } else {
        return `${marker.x} ${marker.y} ${marker.z}`
      }
    },

    isSelectedMarker (marker) {
      return marker.initId === this.selectedMarker.initId
    },

    handleUpdateMarkers () {
      if (this.hasAddedMarker) return
      // reset the payload so the old one isn't added to the new one
      this.payload.markers = []
      this.panoramas.forEach(panorama => {
        const markersPayload = {
          panoId: panorama.id,
          markers: panorama.markers
        }
        if (panorama.markers.length > 0) {
          this.payload.markers.push(markersPayload)
        }
      })
      this.setIsLoading(true)
      api.updateMarkers(JSON.stringify(this.payload)).then(result => {
        // this.fetchBuildings()
        this.fetchBuildingById(this.building.id, () => {
          this.$nextTick(() => {
            const foundPanorama = this.panoramas.filter(panorama => {
              return panorama.id === this.selectedPanorama.id
            })[0]
            this.setSelectedPanorama(foundPanorama)
            this.setEditorState(1)
          })
        })
        // this.selectedPanorama.markers = result.data
        // this.selectedPanorama.markers = result.data.result
        // this.clearActiveLoading()
        // this.alert = { type: 'success', text: 'markerUpdated' }
        this.setEditorState(1)
        this.setIsLoading(false)
        this.setAlertParams({ type: 'success', text: 'markersUpdated', show: true })
      }, err => {
        console.log('An error occurred: ', err)
        this.setIsLoading(false)
        this.setAlertParams({ type: 'danger', text: 'errorOccurred', show: true })
      })
    },

    initAframeDraggable () {
      const self = this
      // delete component before creating a new one to avoid the duplicated
      // component error when changing pages
      if (AFRAME.components.draggable) { // eslint-disable-line
        delete AFRAME.components.draggable // eslint-disable-line
      }
      AFRAME.registerComponent('draggable', { // eslint-disable-line
        init () {
          this.mouse = new THREE.Vector2() // eslint-disable-line
          this.scene = this.el.sceneEl
          this.camera = this.scene.camera
          this.obj = this.el.object3D

          this.scene.addEventListener('mousemove', e => {
            this.mouse.x = (e.offsetX / this.scene.canvas.offsetWidth) * 2 - 1
            this.mouse.y = -(e.offsetY / this.scene.canvas.offsetHeight) * 2 + 1

            if (self.isMarkerSelected) {
              let r = new THREE.Raycaster() // eslint-disable-line
              r.setFromCamera(this.mouse, this.camera)
              let dist = this.obj.position.distanceTo(this.camera.position)
              let point = r.ray.direction.multiplyScalar(dist)
              // need the if statement to prevent elements to be dragged simultaneously
              if (this.el.id === self.selectedMarker.initId) {
                // find if two markers overlap. If true make one of them undragable
                this.el.setAttribute('position', `${point.x} ${point.y} ${point.z}`)
                const foundMarker = self.selectedPanorama.markers.filter(marker => {
                  return marker.initId === this.el.id
                })
                self.updateAppMarker(foundMarker[0], point.x, point.y, point.z)
              }
            }
          })

          this.scene.addEventListener('mousedown', e => {
            let r = new THREE.Raycaster() // eslint-disable-line
            r.setFromCamera(this.mouse, this.camera)
            let intersected = r.intersectObject(this.el.object3D, true)
            // TODO find out why 32 objects are added to intersected in some cases
            // every time add new a-image, intersected length will add
            if (intersected.length === 1) {
              self.isMarkerSelected = true
              // this.selected = true
              if (!self.hasAddedMarker) {
                const foundMarker = self.selectedPanorama.markers.filter(marker => {
                  return marker.initId === this.el.id
                })
                self.setSelectedMarker(foundMarker[0])
                self.setEditorState(2)
                self.instructionText = 'positionMarker'
              }
            } else {
              if (!self.hasAddedMarker) {
                self.clickMarkerCount += 1
                if (self.clickMarkerCount === self.sceneEventCount) {
                  self.setSelectedMarker({})
                  self.setEditorState(1)
                  self.clickMarkerCount = 0
                }
              }
            }
            if (self.isMarkerSelected) self.clickMarkerCount = 0
          })

          this.scene.addEventListener('mouseup', e => {
            self.isMarkerSelected = false
            // this.selected = undefined
          })
        }
      })
    }
  }
}
</script>

<style lang="stylus">
@import '../../variables'
@import '../../mixins'

$marker-height = 440px
$default-opacity = .8

#marker-editor {
  text-align: center

  .instruction-text {
    color: $white
    font-size: 16px
    position: absolute
    left: 50%
    top: 20px
    transform: translateX(-50%)
    z-index: 9999
    black-text-shadow()

    &.delete {
      top: 160px
    }
  }

  .alert-warning {
    text-align: left
  }
}

.marker-container {
  position: relative
  height: $marker-height
  margin: 0 0 15px
  width: 100%
}


.marker-window {
  position: absolute
  z-index: 999
  background-color: alpha(#555, 80%)
  font-size: 14px
  padding: 15px
  text-align: left
  /*border-radius($border-radius)*/

  h5 {
    color: $white
    text-transform: uppercase
  }

  label {
    color: $white
    margin-bottom: 5px !important
    width: 100%
    text-align: left !important
  }

  p {
    color: $white
  }

  input,
  textarea {
    background: $gray
    border-color: transparent
    color: $white
    margin-bottom: 10px !important
    resize: none
  }

  .fa-stack {
    height: 1.7em
    line-height: 1.7em
  }

  .form-group {
    margin-bottom: 0

    > div {
      width: 100%
    }
  }

  .input-group {
    > span {
      display: block
      width: 100%

      label {
        border-top-right-radius: 4px !important
        border-bottom-right-radius: 4px !important
        background-color: $brand-color
        border-top-color: $brand-color
        border-left-color: $brand-color
        border-right-color: $brand-color
        border-bottom-color: #3ea08d
        text-align: center !important

        &:hover {
          background-color: #3ea08d
          border-color: #3ea08d
          color: $white
        }
      }
    }

    > input {
      background: $gray
      border-top-left-radius: 4px !important
      border-bottom-left-radius: 4px !important
      width: 100%
    }
  }
}

.marker-window-inner {
  overflow-x: hidden
  overflow-y: auto
  height: 350px
}

.marker-overlay {
  background: alpha(#555, 30%)
  pointer-events: none
  position: absolute
  width: 390px
  height: $marker-height
}

.marker-overlay-left {
  top: 0
  left: 0
}

.marker-overlay-right {
  top: 0
  right: 0
}

/*.marker-loading {
  height: 440px
  line-height: 440px
}*/

.spinner-wrapper {
  min-width: 100%
  min-height: 100%
  height: 100%
  top: 0
  left: 0
  position: absolute
  background-color: $white
  z-index: -1

  .spinner-text {
    position: absolute
    top: 41.5%
    left: 47%
    margin: 10px 0 0 35px
    /*font-size: 9px*/
    /*font-family: Arial;*/
    color: $brand-color
    letter-spacing: 1px
    font-weight: 700
  }

  .spinner {
    margin: 0
    display: block
    position: absolute
    background-color: transparent
    left: 45%
    top: 40%
    border: 25px solid $brand-color
    width: 1px
    height: 1px
    border-left-color: transparent
    border-right-color: transparent
    border-radius(50px)
    -webkit-animation: rotating 1.5s infinite;
       -moz-animation: rotating 1.5s infinite;
            animation: rotating 1.5s infinite;
  }
}

.in-progress {
  background: $brand-color
  height: $marker-height
  position: relative
  text-align: center

  i {
    color: alpha($white, 50%)
    font-size: 40px
    margin-bottom: 8px
  }

  h4 {
    color: $white
    margin-top: 5px
    text-transform: uppercase
  }

  p {
    color: $white
  }

  .in-progress-els {
    position: absolute
    top: 50%
    left: 50%
    transform: translate(-50%, -50%)
  }

  &.in-progress-error {
    background: $red
  }
}

@-webkit-keyframes rotating {
  0%,100% {-webkit-transform:rotate(0deg)}
  50%     {-webkit-transform:rotate(720deg)}
}

@keyframes rotating {
  0%,100% {-webkit-transform:rotate(0deg)}
  50%     {-webkit-transform:rotate(720deg)}
}
</style>



