<template>
  <div id="manage-agents" class="col-md-12">
    <alert v-if="alert" :alert="alert" @on-click="alert = null">
    </alert>
    <div class="subheader clearfix">
      <!-- loop through all the categories and display the thumbnail of the
      fist one -->
      <h1 class="header-center">{{ $t('manageAgents') }}</h1>
    </div>
    <form-builder
      :object="signupInfo"
      :fields="formFields"
      btn-text="signUp"
      @on-submit="signupSubmit">
    </form-builder>
    <loading v-show="activeLoading === 'formLoading'" color="brand">
    </loading>
    <div class="row">
      <div id="panoramas" class="col-md-12">
        <alert v-if="alert" :alert="alert" @on-click="alert = null">
        </alert>
        <div class="subheader clearfix">
          <h1 class="header-left">Agents</h1>
        </div>
        <list
          :class-name="agents"
          object-name="agent"
          :fields="agentFields"
          :has-thumbnails="true"
          default-sort-key="date">
        </list>
      <!-- <spinner v-if="!user"></spinner> -->
      </div>
    </div>
  </div>
</template>

<script>
import { signupInfo, agents } from '../vuex/getters'
import FormBuilder from '../components/FormBuilder'
import Alert from '../components/Alert'
import Loading from '../components/Loading'
import Spinner from '../components/Spinner'
import List from '../components/List'

export default {
  name: 'ManageAgents',
  components: {
    FormBuilder,
    Alert,
    Loading,
    Spinner,
    List
  },

  vuex: {
    getters: {
      signupInfo,
      agents
    }
  },

  data () {
    return {
      alert: null,
      formFields: [
        { name: 'name', type: 'text', required: true, text: 'yourName' },
        {
          name: 'email',
          type: 'email',
          required: true,
          // matched: '^[a-zA-Z0-9]$'
          matches: '^[-a-z0-9~!$%^&*_=+}{\'?]+(\.[-a-z0-9~!$%^&*_=+}{\'?]+)*@([a-z0-9_][-a-z0-9_]*(\.[-a-z0-9_]+)*\.(aero|arpa|biz|com|coop|edu|gov|info|int|mil|museum|name|net|org|pro|travel|mobi|[a-z][a-z])|([0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}))(:[0-9]{1,5})?$' // eslint-disable-line
        },
        { name: 'phone', type: 'text', text: 'yourPhone' },
        { name: 'company', type: 'text', text: 'yourCompany' },
        { name: 'country' },
        { name: 'photo', type: 'file' },
        { name: 'password', type: 'password', required: true },
        { name: 'confirmPassword', type: 'password', required: true }
      ],
      formActions: [
        {
          name: 'signUp',
          submit: this.signupSubmit
        }
      ],
      agentFields: [
        { name: 'thumbnail', column: 1, isThumbnail: true },
        { name: 'name', column: 2, isDetail: true },
        { name: 'role', column: 2, isDetail: true },
        { name: 'company', column: 3, isDetail: true, hasTrans: true },
        { name: 'country', column: 2, isDetail: true },
        { name: 'phone', column: 2, isDetail: true }
        // { name: 'rotation', column: 2, isDetail: true }
      ]
    }
  },

  methods: {
    signupSubmit () {
      console.log('Submitted!')
    }
  }
}
</script>



