<template>
  <ul>
    <li v-for="obj in value">
      <a v-if="isObject(obj)" :href="'#/' + $parent.key + '/' + obj.id">
        {{ obj.name }}
      </a>
      <span v-if="isString(obj)">{{ obj ? value : '–' }}</span>
    </li>
  </ul>
</template>

<script>
export default {
  props: {
    key: String,
    value: {
      required: true
    },
    fields: Array,
    object: Object
  },

  methods: {
    isObject (value) {
      return value.constructor === Object
    },

    isString (value) {
      return value.constructor === String
    }
  }
}
</script>



