<template>
  <div id="panoramas" class="col-md-12">
    <div class="subheader clearfix">
      <h1 class="header-left">{{ $t('panoramaList') }}</h1>
    </div>
    <list
      :class-name="panoramas"
      :sortable="true"
      :sorted-class-name="sortedPanoramas"
      object-name="panorama"
      :fields="panoramaFields"
      :actions="panoramaActions"
      :has-thumbnails="true"
      default-sort-key="index"
      :default-sort-order="1">
    </list>
    <modal
      v-show="activeModal === 'deletePanoramaConfirm'"
      modal-title="confirmDelete"
      :post-save="deletePanoramaConfirm">
      <p>{{ $t('confirmDeletePanorama') }}</p>
    </modal>
    <modal
      v-show="activeModal === 'shareConfirm'"
      modal-title="confirmShare"
      :post-save="sharePanoramaConfirm">
      <p>{{ $t('confirmSharePanorama') }}</p>
    </modal>
    <modal
      v-show="activeModal === 'sharePanoramaSuccess'"
      modal-title="Viewer link">
      <a class="share-link" :href="panoViewerURL" target="_blank">{{ panoViewerURL }}</a>
      <share-buttons :url="panoViewerURL"></share-buttons>
    </modal>
    <modal
      v-show="activeModal === 'creatingPanorama'"
      :no-confirm="true"
      :no-close="true">
      <p>{{ $t('creatingPanorama') }}</p>
    </modal>
  </div>
</template>

<script>
import _ from 'lodash'
import List from '../components/List'
import Modal from '../components/Modal'
// import Alert from '../components/Alert'
// import Loading from '../components/Loading'
import AlertParams from '../components/Alert'
import EditorLoading from '../components/EditorLoading'
import ShareButtons from '../components/ShareButtons'
import api from '../api'
import {
  authInfo,
  building,
  panoramas,
  sortedPanoramas,
  currentPanoramaId,
  activeModal,
  isLoading,
  hasSortTriggered,
  alertParams
} from '../vuex/getters'

import {
  fetchBuildings,
  fetchBuildingById,
  setCurrentPanoramaId,
  removePanorama,
  setActiveModal,
  clearActiveModal,
  setIsLoading,
  setAlertParams,
  addPanoramaToState,
  setIsNewPanorama,
  setIsEditorSceneReady,
  setHasSortTriggered,
  setSelectedPanorama,
  setEditorState
} from '../vuex/actions'

export default {
  name: 'Panoramas',
  props: {
    props: Array
  },

  components: {
    List,
    Modal,
    AlertParams,
    EditorLoading,
    ShareButtons
  },

  vuex: {
    getters: {
      authInfo,
      building,
      panoramas,
      sortedPanoramas,
      currentPanoramaId,
      activeModal,
      isLoading,
      hasSortTriggered,
      alertParams
    },
    actions: {
      fetchBuildings,
      fetchBuildingById,
      setCurrentPanoramaId,
      removePanorama,
      setActiveModal,
      clearActiveModal,
      setIsLoading,
      setAlertParams,
      addPanoramaToState,
      setIsNewPanorama,
      setIsEditorSceneReady,
      setHasSortTriggered,
      setSelectedPanorama,
      setEditorState
    }
  },

  data () {
    return {
      buildingId: '',
      panoramaActions: {
        delete: { submit: this.deletePanorama },
        // share: { submit: this.sharePanorama },
        add: { name: 'addNew', submit: this.newPanorama }
      },
      panoramaFields: [
        { name: 'thumbnail', column: 1, isThumbnail: true },
        { name: 'name', column: 3, hasLink: true },
        { name: 'category', column: 3, isDetail: true, hasTrans: true },
        { name: 'x', column: 2, isDetail: true },
        { name: 'y', column: 2, isDetail: true }
        // { name: 'isDefault', column: 1, isCheckbox: true, hasTrans: true }
        // { name: 'index', column: 1, isCheckbox: true, hasTrans: true }
      ],
      // alert: null,
      sharePanoCode: ''
    }
  },

  computed: {
    panoViewerURL () {
      return api.panoViewerURL + '?code=' + this.sharePanoCode
    }
  },

  watch: {
    hasSortTriggered: {
      handler: function (val) {
        if (this.hasSortTriggered) {
          this.updatePanoramas()
          this.setHasSortTriggered(false)
        }
      },
      deep: true
    }
  },

  methods: {
    newPanorama () {
      this.setActiveModal('creatingPanorama')
      // this.setActiveLoading('newLoading')
      this.setIsLoading(true)
      api.createBlankPanorama(this.building.id).then(resp => {
        const panoId = resp.data.objectId
        const panorama = resp.data
        this.renamePanoramaProps(panorama)
        this.addPanoramaToState(panorama)
        this.clearActiveModal()
        // this.clearActiveLoading()
        this.setIsLoading(false)
        this.setIsNewPanorama(true)
        this.$route.router.go('/buildings/' + this.building.id + '/panoramas/' + panoId)
      }, err => {
        console.log('Error:', err)
        this.setIsLoading(false)
        this.setAlertParams({ type: 'danger', text: 'errorOccurred', show: true })
        this.clearActiveModal()
      })
    },

    updatePanoramas () {
      const payload = { panos: this.sortedPanoramas }
      this.setIsLoading(true)
      api.bulkUpdatePanorama(payload).then(result => {
        // this.fetchBuildingById(this.building.id, err => {
        //   if (err) {
        //     console.log('An error ocurred: ', err)
        //     this.setAlertParams({ type: 'danger', text: 'errorOccurred', show: true })
        //   }
        this.setIsLoading(false)
        this.setAlertParams({ type: 'success', text: 'panoramaUpdated', show: true })
        // })
      }, err => {
        console.log('An error occurred: ', err)
        this.setIsLoading(false)
        this.setAlertParams({ type: 'danger', text: 'errorOccurred', show: true })
      })
    },

    deletePanorama (id) {
      this.setCurrentPanoramaId(id)
      this.setActiveModal('deletePanoramaConfirm')
    },

    deletePanoramaConfirm () {
      const panorama = _.find(this.panoramas, { id: this.currentPanoramaId })
      const payload = { panoId: panorama.id }
      // this.setActiveLoading('deleteLoading')
      this.setIsLoading(true)
      return api.deletePanorama(payload).then(result => {
        // this.fetchBuildings(() => {
        const deletedPanorama = result.data.objectId
        this.removePanorama(deletedPanorama)
        // this.alert = { type: 'default', text: 'panoramaDeleted' }
        // this.clearActiveLoading()
        this.setIsLoading(false)
        this.setAlertParams({ type: 'default', text: 'panoramaDeleted', show: true })
        this.clearActiveModal()
        this.fetchBuildingById(this.building.id, err => {
          if (err) {
            console.log('An error ocurred: ', err)
            this.setAlertParams({ type: 'danger', text: 'errorOccurred', show: true })
          }
        })
        // this.$nextTick(() => {
        this.setIsEditorSceneReady(false)
        this.$nextTick(() => {
          this.setIsEditorSceneReady(true)
          this.setEditorState(0)
        })
      })
    },

    sharePanorama (id) {
      this.setCurrentPanoramaId(id)
      this.setActiveModal('shareConfirm')
    },

    sharePanoramaConfirm () {
      const payload = {
        'panoId': this.currentPanoramaId
      }
      // this.setActiveLoading('shareLoading')
      this.setIsLoading(true)
      return api.sharePanorama(payload).then(result => {
        // this.clearActiveLoading()
        this.setIsLoading(false)
        this.sharePanoCode = result.data.code
        this.setActiveModal('sharePanoramaSuccess')
      })
    },

    renamePanoramaProps (panorama) {
      const tempIsDefault = false
      panorama.category = panorama.categoryType
      panorama.id = panorama.objectId
      delete panorama.categoryType
      delete panorama.objectId
      delete panorama.isDefault
      panorama.isDefault = tempIsDefault
    }
  }
}
</script>

<style lang="stylus">
.share-link {
  display: block
  text-align: center
}
</style>



