<template>
  <div id="comments-editor" class="col-md-12">
    <!-- <alert v-if="alert" :alert="alert" @on-click="alert = null"> -->
    <!-- </alert> -->
    <div class="subheader clearfix">
      <h1 class="header-left">{{ $t('commentsManager') }}</h1>
    </div>
    <list
      :class-name="firebaseComments"
      object-name="firebaseComment"
      :fields="firebaseCommentsFields"
      :actions="firebaseCommentsActions"
      default-sort-key="date">
    </list>
    <modal
      v-show="activeModal === 'deleteCommentConfirm'"
      modal-title="confirmDelete"
      :post-save="deleteCommentConfirm">
      <p>{{ $t('confirmDeleteComment') }}</p>
      <!-- <loading v-show="activeLoading === 'deleteLoading'" color="brand"> -->
      <!-- </loading> -->
    </modal>
  </div>
</template>

<script>
import List from '../components/List'
import Modal from '../components/Modal'
// import Alert from '../components/Alert'
// import Loading from '../components/Loading'
import AlertParams from '../components/Alert'
import EditorLoading from '../components/EditorLoading'
import {
  building,
  firebaseComments,
  currentCommentId,
  // activeLoading,
  isLoading,
  alertParams,
  activeModal
}
from '../vuex/getters'

import {
  fetchBuildings,
  setActiveModal,
  // setActiveLoading,
  // clearActiveLoading,
  setIsLoading,
  setAlertParams,
  setCurrentCommentId,
  deleteFirebaseComments,
  clearActiveModal
} from '../vuex/actions'

export default {
  components: {
    List,
    Modal,
    // Alert,
    // Loading
    AlertParams,
    EditorLoading
  },

  vuex: {
    getters: {
      building,
      firebaseComments,
      currentCommentId,
      // activeLoading,
      isLoading,
      alertParams,
      activeModal
    },
    actions: {
      fetchBuildings,
      setActiveModal,
      // setActiveLoading,
      // clearActiveLoading,
      setIsLoading,
      setAlertParams,
      setCurrentCommentId,
      deleteFirebaseComments,
      clearActiveModal
    }
  },

  data () {
    return {
      firebaseCommentsActions: {
        delete: { submit: this.deleteComment }
      },
      firebaseCommentsFields: [
        { name: 'author', column: 3, isDetail: true },
        { name: 'date', column: 2, isDetail: true },
        { name: 'message', column: 6, isDetail: true }
      ]
      // alert: null
    }
  },

  methods: {
    deleteComment (id) {
      this.setCurrentCommentId(id)
      this.setActiveModal('deleteCommentConfirm')
    },

    deleteCommentConfirm () {
      // this.setActiveLoading('deleteLoading')
      this.setIsLoading(true)
      return this.deleteFirebaseComments(this.building.code, this.currentCommentId).then(result => {
        // this.alert = {
        //   type: 'default',
        //   text: 'commentDeleted'
        // }
        this.clearActiveModal()
        // this.clearActiveLoading()
        this.setIsLoading(false)
        this.setAlertParams({ type: 'default', text: 'commentDeleted', show: true })
      })
    }
  }
}
</script>



