<template>
  <div id="email-verified" class="col-md-12">
    <h2
      v-if="isVerified !== null && isVerified === true"
      class="text-center">
      {{ $t('emailVerified') }}
    </h2>
    <h2
      v-if="isVerified !== null && isVerified === false"
      class="text-center">
      {{ $t('emailNotVerified') }}
    </h2>
    <loading v-if="isVerified === null" color="white"></loading>
    <hr>
    <a class="btn btn-primary" href="http://vreditor.istaging.co">
      {{ $t('emailVerifiedButton') }}
    </a>
  </div>
</template>

<script>
import Loading from '../components/Loading'
import api from '../api'

import {
  setIsLoading,
  setAlertParams,
  clearActiveModal
} from '../vuex/actions'

export default {
  name: 'EmailVerified',
  vuex: {
    actions: {
      setIsLoading,
      setAlertParams,
      clearActiveModal
    }
  },

  components: {
    Loading
  },

  data () {
    return {
      nonce: '',
      isVerified: null
    }
  },

  watch: {
    nonce () {
      api.verifyEmail(this.nonce).then(resp => {
        this.isVerified = true
      }, err => {
        console.log('emailVerify error: ', err)
        this.setIsLoading(false)
        this.setAlertParams({ type: 'danger', text: 'errorOccurred', show: true })
        this.clearActiveModal()
        this.isVerified = false
      })
    }
  },

  route: {
    data ({ to }) {
      const nonce = to.params.nonce
      return {
        nonce: nonce
      }
    },

    activate: function () {
      const body = document.getElementsByTagName('body')[0]
      body.className += ' bg-brand'
    }
  },

  methods: {
    redirectToHome () {
      console.log('test3')
      this.$route.router.go('http://vreditor.istaging.co')
    }
  }
}
</script>

<style lang="stylus">
@import '../variables'
@import '../mixins'

#email-verified,
#email-verified .container {
  max-width: 420px // TODO find a better way of doing this
}

#email-verified {
  color: $white
  transform: translate(-50%, -50%)
  top: 50%
  left: 50%
  position: fixed
  width: 100%

  .col-centered {
    text-align: center
  }

  hr {
    margin: 16px 0
    opacity: 0.4
  }

  a {
    background-color: $default-color
    border-top-color: $default-color
    border-left-color: $default-color
    border-right-color: $default-color
    border-bottom-color: darken($default-color, 10%)
    margin-top: 4px
    width: 100%

    &:hover {
      hover-color($default-color)
    }
  }
}
</style>



