<template>
  <div class="col-md-1 col-error">
    <a
      data-toggle="tooltip"
      data-placement="top"
      title="Tooltip on left">
      <img class="img-circle" :src="defaultThumb">
      <div>
        <p>
          {{ $t('invalidPanorama') }}
        </p>
        <i class="fa fa-caret-left"></i>
      </div>
    </a>
  </div>
</template>

<script>
export default {
  props: {
    objectName: String
  },

  data () {
    return {
      defaultThumb: require('../../assets/default-thumb.png')
    }
  }
}
</script>



