<template>
  <div>
    <a v-if="!isNestedRoute($route)" :href="'#' + $route.path + '/' + object.id">
      <img :src="getThumbnail(value)" class="img-circle">
    </a>
    <a
      v-if="isNestedRoute($route)"
      :href="'#' + $route.path + '/' + parentComponent + '/' + object.id">
      <img :src="getThumbnail(value)" class="img-circle">
    </a>
  </div>
</template>

<script>
import util from '../../util'
export default {
  props: {
    value: {
      required: true
    },
    fields: Array,
    object: Object
  },

  computed: {
    parentComponent () {
      const parentComponent = this.$parent.$parent.$options.name
      return util.PascalToParamCase(parentComponent)
    }

    // thumbnail () {
    //   // get buildings thumbnail or default
    //   if (this.value.constructor === Array) {
    //     return this.value[0].thumbnail
    //       ? this.value[0].thumbnail
    //       : this.defaultThumb
    //   }
    //   // get panoramas thumbnail or default
    //   if (this.value.constructor === String) {
    //     return this.value ? this.value : this.defaultThumb
    //   }
    // }
  },

  methods: {
    getThumbnail (value) {
      // for building
      if (value.constructor === Array) {
        const objsWithThumbs = value.filter(obj => {
          return obj.thumbnail || obj.thumbnail
        })
        return objsWithThumbs[0].thumbnail // get the first thumbnail
      }
      // for panorama
      return value
    },

    isNestedRoute (route) {
      const rp = route.params
      const id = rp.id || rp.shareCodeId || rp.buildingId || rp.panoramaId
      return !!(id)
    }
  }
}
</script>



