export function camelCaseToText (str) {
  return str
    .replace(/([A-Z])/g, (str) => { return ' ' + str.toLowerCase() })
    .replace(/^.?/, (str) => { return str.toUpperCase() })
}

export function urlPathToFileName (str) {
  if (!str) return
  return str.match(/\/([^/]*)$/)[1]
}



