<template>
  <ul class="share-buttons">
    <li>
      <a
        :href="'https://www.facebook.com/sharer/sharer.php?u=' + encodeURIComponent(url) + '&t='"
        target="_blank"
        title="Share on Facebook"
        onclick="window.open('https://www.facebook.com/sharer/sharer.php?u=' + encodeURIComponent(url) + '&t=' + encodeURIComponent(url)); return false;">
        <i class="fa fa-facebook-square fa-2x"></i>
      </a>
    </li>
    <li>
      <a
        :href="'https://twitter.com/intent/tweet?source=' + encodeURIComponent(url) + '&text=' + encodeURIComponent(url)"
        target="_blank"
        title="Tweet"
        onclick="window.open('https://twitter.com/intent/tweet?text=' + encodeURIComponent(url) + ':%20' + encodeURIComponent(url)); return false;">
        <i class="fa fa-twitter-square fa-2x"></i>
      </a>
    </li>
    <li>
      <a
        :href="'https://plus.google.com/share?url=' + encodeURIComponent(url)"
        target="_blank"
        title="Share on Google+"
        onclick="window.open('https://plus.google.com/share?url=' + encodeURIComponent(url)); return false;">
        <i class="fa fa-google-plus-square fa-2x"></i>
      </a>
    </li>
    <li>
      <a
        :href="'http://www.linkedin.com/shareArticle?mini=true&url=' + encodeURIComponent(url) + '&title=&summary=&source=' + encodeURIComponent(url)"
        target="_blank"
        title="Share on LinkedIn"
        onclick="window.open('http://www.linkedin.com/shareArticle?mini=true&url=' + encodeURIComponent(url) + '&title=' +  encodeURIComponent(url)); return false;">
        <i class="fa fa-linkedin-square fa-2x"></i>
      </a>
    </li>
    <li>
      <a
        :href="'mailto:?subject=&body=' + encodeURIComponent(url)"
        target="_blank"
        title="Email"
        onclick="window.open('mailto:?subject=' + encodeURIComponent(url) + '&body=' +  encodeURIComponent(url)); return false;">
        <i class="fa fa-envelope-square fa-2x"></i>
      </a>
    </li>
  </ul>
</template>

<script>
export default {
  props: {
    url: String
  }
}
</script>

<style lang="stylus">
.share-buttons {
  list-style: none
  padding: 0
  margin: 6px auto 0
  width: 158px

  li {
    display: inline-block
  }
}

.fa-facebook-square {
  color: #3b5998
}

.fa-twitter-square {
  color: #55acee
}

.fa-google-plus-square {
  color: #dd4b39
}

.fa-linkedin-square {
  color: #0e76a8
}

.fa-envelope-square {
  color: #45b29d
}
</style>



