import $ from 'jquery'
import 'jquery-ui'
import '../vendor/jquery-ui-rotatable'
import util from '../util'

export const active = {
  bind () {
    this.vm.$nextTick(() => { // run the code after the list has been rendered
      const self = this
      const arr = util.getParentElements(self)

      arr[1].className = ' active' // the first element is active by default
      self.el.addEventListener('click', () => {
        util.resetActiveClass(arr)
        util.addClassToParent(self)
      })
    })
  }
}

export const activeLink = {
  priority: 800, // necessary to get the href attribute
  update (route) {
    this.vm.$nextTick(() => {
      const self = this
      const elPath = this.el.getAttribute('href')
      const routePath = '#' + route.path
      const routePathStart = routePath.match(/#\/\b[\w-]+\b/)
      if (routePathStart && elPath === routePathStart[0]) {
        // TODO find a way to do this without jQuery
        const $activeEls = $('.active')
        $activeEls.removeClass('active')
        util.addClassToParent(self)
      }
    })
  }
}

export const drag = {
  bind () {
    this.vm.$nextTick(() => {
      const self = this
      if (self.el) {
        const numberRE = /[+-]?\d+(\.\d+)?/g
        const child = self.el.querySelector('.marker .fa-circle')
        const childRotation = child.style.transform
        const childRotationNumber = childRotation.match(numberRE).map(v => {
          return parseFloat(v)
        })
        // the if pevents the error that happens when reloading Panorama and going
        // to the previous page
        if (self.vm) {
          $(self.el).draggable({
            containment: $(self.el.parentElement),
            drag: dragListener
          })
          $(child).rotatable({
            containment: $(self.el.parentElement),
            angle: childRotationNumber[0],
            rotate: rotateListener
          })
        }
      }

      function dragListener (ev) {
        self.vm.$emit('drag-event', ev)
      }
      function rotateListener (ev) {
        self.vm.$emit('rotate-event', ev)
      }
    })
  }
}



