<template>
  <div id="editor-loading">
    <loading color="brand">
  </div>
</template>

<script>
import Loading from '../components/Loading'
export default {
  components: {
    Loading
  }
}
</script>

<style lang="stylus">
@import '../variables'

#editor-loading {
  position: fixed
  top: 0
  left: 0
  width: 100%
  height: 100%
  background-color: rgba(238,238,238,.5)
  z-index: 9999

  #loading {
    position: relative
    left: 50%
    top: 50%
    transform: translate(-50%, -50%)

    .sk-fading-circle {
      width: 34px
      height: 34px
    }
  }
}
</style>



