<template>
  <div class="row">
    <label class="control-label col-sm-2"></label>
    <div class="col-sm-10">
      <div class="progress" v-if="hasProgressStarted">
        <div
          class="progress-bar progress-bar-striped active"
          :style="'width:' + fileProgress + '%'">
          {{ fileProgress }}%
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    fileProgress: Number
  },

  computed: {
    hasProgressStarted () {
      return this.fileProgress !== -1 && this.fileProgress !== undefined
    }
  }
}
</script>

<style lang="stylus">
@import '../variables'

.progress-bar {
  background-color: $brand-color
}
</style>



