<template>
  <div id="marker-panoramas">
    <div class="target-panorama">
      <img :src="targetPanorama ? targetPanorama.thumbnail : ''">
      <span>{{ $t('target') }}</span>
    </div>
  </div>
</template>

<script>
import {
  panoramas,
  targetPanorama,
  selectedMarker
} from '../../vuex/getters'

export default {
  vuex: {
    getters: {
      panoramas,
      targetPanorama,
      selectedMarker
    }
  },

  methods: {
    isTargetPanorama (panorama) {
      return panorama === this.targetPanorama
    }
  },

  computed: {
    targetPanorama () {
      const foundPanorama = this.panoramas.filter(panorama => {
        return panorama.id === this.selectedMarker.nextPanoId
      })
      return foundPanorama[0]
    }
  }
}
</script>

<style lang="stylus">
@import '../../variables'

#marker-panoramas {
  opacity: .6
  position: absolute
  top: 20px
  right: 20px
  z-index: 9999

  .current-panorama,
  .target-panorama {
    position: relative
    width: 130px

    img {
      width: 100%
      height: 75px
    }

    span {
      color: $white
      font-size: 13px
      padding: 2px 0
      position: absolute
      left: 0
      bottom: 0
      text-align: center
      text-transform: uppercase
      width: 100%
    }
  }

  .current-panorama {
    margin: 0 0 8px 0

    span {
      background: $default-color
    }
  }

  .target-panorama {
    span {
      background: alpha($brand-color, 60%)
    }
  }
}
</style>



