<template>
  <div
    class="modal"
    id="myModal"
    tabindex="-1"
    role="dialog"
    aria-labelledby="myModalLabel">
    <div class="modal-dialog" role="document">
      <div class="modal-content">
        <div class="modal-header">
          <button
            v-if="!noClose"
            class="close"
            data-dismiss="modal"
            aria-label="Close"
            @click="close">
            <span aria-hidden="true">&times;</span>
          </button>
          <h4 class="modal-title" id="myModalLabel">{{ $t(modalTitle) }}</h4>
        </div>
        <div class="modal-body">
          <slot></slot>
        </div>
        <div class="modal-footer">
          <button
            v-if="!noClose"
            class="btn btn-default"
            data-dismiss="modal"
            @click="close()">
            {{ $t('close') }}
          </button>
          <button
            v-if="!noConfirm"
            class="btn btn-primary"
            @click="save()">
            {{ $t('confirm') }}
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { setActiveModal } from '../vuex/actions'

export default {
  props: {
    modalTitle: String,
    postSave: Function,
    postParam: Object,
    closeOnComplete: Boolean,
    noConfirm: Boolean,
    noClose: Boolean
  },

  vuex: {
    actions: {
      setActiveModal
    }
  },

  data () {
    return {
      isVisible: true
    }
  },

  ready () {
    document.addEventListener('keydown', e => {
      if (this.isVisible && e.keyCode === 27) {
        this.close()
      }
    })
  },

  methods: {
    close () {
      this.isVisible = true
      this.setActiveModal('')
    },

    save () {
      if (!this.postSave) {
        this.close()
      } else {
        const payload = this.postParam || {}
        this.postSave(payload).then(result => {
          if (this.closeOnComplete) this.close()
        })
      }
    }
  }
}
</script>

<style lang="stylus">
.container {
  .modal {
    display: block
    z-index: 999999
  }
}
</style>



