<template>
  <span v-if="!keyHasTrans(key)">
    {{ isNonFalsy(value) ? value : '–' }}
  </span>
  <span v-if="keyHasTrans(key)">
    {{ isNonFalsy(value) ? $t(value) : '–' }}
  </span>
</template>

<script>
export default {
  props: {
    key: String,
    value: {
      required: true
    },
    fields: Array
  },

  methods: {
    keyHasTrans (key) {
      const fieldIsArray = this.findField(key, 'hasTrans')
      return fieldIsArray
    },

    isNonFalsy (value) {
      if (value) return true
      if (value === 0) return true
      if (!value) return false
    },

    findField (key, prop) {
      let field = null
      this.fields.filter(obj => {
        if (obj.name === key) field = obj
      })
      return field ? field[prop] : ''
    }
  }
}
</script>



