<template>
  <div id="reset-pass" class="col-md-12">
    <alert v-if="alert" :alert="alert" @on-click="alert = null">
    </alert>
    <h2 class="text-center">{{ $t('resetPass') }}</h2>
    <form-builder
      :object="authInfo"
      :fields="formFields"
      @on-submit="resetPassSubmit">
    </form-builder>
    <loading v-show="activeLoading === 'authLoading'" color="white">
    </loading>
  </div>
</template>

<script>
import FormBuilder from '../components/FormBuilder'
import Alert from '../components/Alert'
import Loading from '../components/Loading'
import api from '../api'
import { authInfo, activeLoading, alertParams } from '../vuex/getters'
import {
  setActiveLoading,
  clearActiveLoading,
  setAlertParams
} from '../vuex/actions'
import auth from '../auth'

export default {
  name: 'ResetPass',
  components: {
    FormBuilder,
    Alert,
    Loading
  },

  vuex: {
    getters: {
      authInfo,
      activeLoading,
      alertParams
    },
    actions: {
      setActiveLoading,
      clearActiveLoading,
      setAlertParams
    }
  },

  data () {
    return {
      nonce: '',
      alert: null,
      formFields: [
        { name: 'password', type: 'password', required: true },
        { name: 'confirmPassword', type: 'password', required: true }
      ],
      sharedState: api
    }
  },

  route: {
    data ({ to }) {
      const nonce = to.params.nonce
      return {
        nonce: nonce
      }
    },

    canActivate () {
      return !auth.user.authenticated
    },

    activate: function () {
      const body = document.getElementsByTagName('body')[0]
      body.className += ' bg-brand'
    }
  },

  methods: {
    resetPassSubmit () {
      const payload = { newPassword: this.authInfo.password }
      if (this.authInfo.password !== this.authInfo.confirmPassword) {
        this.setAlertParams({ type: 'danger', text: 'passMatchFailed', show: true })
        return
      }
      this.setActiveLoading('authLoading')
      auth.resetPass(this, this.nonce, payload).then(result => {
        this.clearActiveLoading()
        this.setAlertParams({ type: 'success', text: 'resetPassSucceded', show: true })
      }).catch(err => {
        console.log('Error:', err)
        this.clearActiveLoading()
        this.setAlertParams({ type: 'danger', text: 'resetPassFailed', show: true })
      })
    }
  }
}
</script>

<style lang="stylus">
@import '../variables'
@import '../mixins'

$fb-color = #3b5998

#reset-pass,
#reset-pass .container {
  max-width: 420px // TODO find a better way of doing this
}

#reset-pass {
  color: $white
  transform: translate(-50%, -50%)
  top: 50%
  left: 50%
  position: fixed
  width: 100%

  .col-centered {
    text-align: center
  }

  .form-group {
    margin-bottom: 4px
  }

  hr {
    margin: 16px 0
    opacity: 0.4
  }

  input {
    background-color: alpha($white, 20%)
    border-color: $brand-color
    color: $white
    placeholder-color(alpha($white, 60%))
  }

  input[type="submit"] {
    background-color: $default-color
    border-top-color: $default-color
    border-left-color: $default-color
    border-right-color: $default-color
    border-bottom-color: darken($default-color, 10%)
    width: 100%

    &:hover {
      hover-color($default-color)
    }
  }

  .control-label {
    display: none
  }

  .col-sm-10 {
    width: 100%
  }

  .col-sm-offset-2 {
    margin-left: 0
  }

  .has-error {
    color: $white
  }
}
</style>



