<template>
  <div id="building-buttons">
    <a
      :href="building ? building.shareCode : ''"
      @click="showBrandModal"
      target="_blank">
      <i class="fa fa-eye"></i>
    </a>
    <a @click="shareBuilding"><i class="fa fa-share-alt"></i></a>
    <a @click="syncBuilding"><i class="fa fa-refresh"></i></a>
    <modal
      v-show="activeModal === 'authWithVRTour'"
      modal-title="authWithVRTour"
      :post-save="authConfirm">
      <form-builder
        :object="leanAuthInfo"
        :fields="leanAuthFormFields"
        :is-in-modal="true">
      </form-builder>
      <loading v-show="activeLoading === 'authLoading'" color="brand">
      </loading>
    </modal>
    <modal
      v-show="activeModal === 'syncBuilding'"
      modal-title="syncToVRTour"
      :post-save="syncBuildingConfirm">
      <form-builder
        :object="leanBuildingInfo"
        :fields="leanBuildingFormFields"
        :is-in-modal="true">
      </form-builder>
      <loading v-show="activeLoading === 'syncLoading'" color="brand">
      </loading>
    </modal>
    <modal
      v-show="activeModal === 'buildingSynced'"
      modal-title="syncToVRTour"
      :no-confirm="true">
      <p v-if="isBuildingSynced">{{ $t('buildingSynced') }}</p>
      <alert v-if="alert" :alert="alert" @on-click="alert = null">
      </alert>
    </modal>
    <modal
      v-show="activeModal === 'shareBuilding'"
      modal-title="shareBuilding">
      <a :href="building ? building.shareCode : ''">
        {{ building ? building.shareCode : '' }}
      </a>
      <share-buttons :url="building ? building.shareCode : ''"></share-buttons>
    </modal>
    <modal
      v-show="activeModal === 'chooseBrand'"
      class="choose-brand"
      modal-title="selectLink"
      :no-confirm="true">
      <h5>{{ $t('branded') }}</h5>
      <a :href="building.shareCode" target="_blank">
        {{ building.shareCode }}
      </a>
      <h5>{{ $t('noBranded') }}</h5>
      <a :href="buildingShareCodeNoBrand" target="_blank">
        {{ buildingShareCodeNoBrand }}
      </a>
    </modal>
  </div>
</template>

<script>
import FormBuilder from '../components/FormBuilder'
import Alert from '../components/Alert'
import Modal from '../components/Modal'
import Loading from '../components/Loading'
import ShareButtons from '../components/ShareButtons'
import api from '../api'
import auth from '../auth'
import util from '../util'
import {
  building,
  activeModal,
  activeLoading,
  leanAuthInfo,
  leanBuildingInfo,
  user
} from '../vuex/getters'

import {
  setActiveModal,
  clearActiveModal,
  setActiveLoading,
  clearActiveLoading,
  setUserLeanAuth
} from '../vuex/actions'
export default {
  vuex: {
    getters: {
      building,
      activeModal,
      activeLoading,
      leanAuthInfo,
      leanBuildingInfo,
      user
    },
    actions: {
      setActiveModal,
      clearActiveModal,
      setActiveLoading,
      clearActiveLoading,
      setUserLeanAuth
    }
  },

  components: {
    FormBuilder,
    Alert,
    Modal,
    Loading,
    ShareButtons
  },

  data () {
    return {
      leanAuthFormFields: [
        { name: 'username', type: 'text', required: true },
        { name: 'password', type: 'password', required: true }
      ],
      leanBuildingFormFields: [
        { name: 'address', type: 'text', required: true }
      ],
      isBuildingSynced: false,
      alert: null
    }
  },

  computed: {
    buildingShareCodeNoBrand () {
      return this.building.shareCode + '?ui=true'
    }
  },

  methods: {
    authConfirm () {
      const credentials = {
        username: this.leanAuthInfo.username,
        password: this.leanAuthInfo.password
      }
      this.setActiveLoading('authLoading')
      return api.authWithVRTour(credentials).then(result => {
        this.clearActiveLoading()
        const sessionToken = result.data.sessionToken
        auth.setLeanLocalStorage(sessionToken)
        auth.checkLeanAuth(authenticated => {
          this.setUserLeanAuth(authenticated)
        })
        this.setActiveModal('syncBuilding')
      }, err => {
        console.log(err)
        this.clearActiveModal()
        this.clearActiveLoading()
        // this.alert = { type: 'danger', text: 'authWithVRTourFailed' }
      })
    },

    syncBuilding () {
      if (this.user.VRAuthenticated === true) {
        this.setActiveModal('syncBuilding')
      } else {
        this.setActiveModal('authWithVRTour')
      }
    },

    syncBuildingConfirm () {
      const thumbnail = util.getFirstThumbnail(this.building)
      let leanPayload = {
        name: this.building.name,
        description: this.building.description,
        address: this.leanBuildingInfo.address,
        imageUrl: thumbnail
      }
      // data existed in leancloud before?
      if (this.building.leanId) {
        leanPayload.leanBuildId = this.building.leanId
      }
      this.setActiveLoading('syncLoading')
      return api.syncToVRTour(leanPayload).then(result => {
        const leanBuildId = result.data.buildId
        // always update leanBuildId in vrbackend database
        // no matter building was created or updated in leancloud
        const payload = { leanBuildId: leanBuildId }
        return api.updateBuilding(this.building.id, payload).then(result => {
          this.clearActiveModal()
          this.clearActiveLoading()
          this.setActiveModal('buildingSynced')
          this.isBuildingSynced = true
        })
      }, err => {
        console.log(err)
        this.clearActiveModal()
        this.clearActiveLoading()
        this.setActiveModal('buildingSynced')
        // this.alert = { type: 'danger', text: 'buildingSyncedError' }
      })
    },

    shareBuilding () {
      this.setActiveModal('shareBuilding')
    },

    showBrandModal (e) {
      if (this.user.country !== 'United States') return
      e.preventDefault()
      this.setActiveModal('chooseBrand')
    }
  }
}
</script>

<style lang="stylus">
#building-buttons {
  margin-bottom: 15px

  > a {
    font-size: 18px
  }

  .choose-brand h5 {
    text-transform: uppercase
  }
}
</style>



