<template>
  <div id="signup" class="col-md-12">
    <alert v-if="alert" :alert="alert" @on-click="alert = null">
    </alert>
    <img class="logo" :src="logoImage">
    <hr>
    <!-- <h2 class="text-center">{{ $t('logIn') }}</h2> -->
    <form-builder
      :object="signupInfo"
      :fields="formFields"
      btn-text="signUp"
      @on-submit="signupSubmit">
    </form-builder>
    <hr>
    <a class="btn btn-default btn-facebook" @click="fbLoginSubmit">
      <i class="fa fa-facebook"></i>
      {{ $t('signUpWithFacebook') }}
      <div id="fb-root"></div>
    </a>
    <loading v-show="isLoading" color="brand"></loading>
    <alert v-show="alertParams.show"></alert>
  </div>
</template>

<script>
import FormBuilder from '../components/FormBuilder'
import Alert from '../components/Alert'
import Loading from '../components/Loading'
import api from '../api'
import auth from '../auth'
// import util from '../util'
import {
  signupInfo,
  user,
  isLoading,
  alertParams
} from '../vuex/getters'

import {
  fetchShareCodes,
  fetchBuildings,
  checkBuildings,
  setActiveLoading,
  clearActiveLoading,
  setIsLoading,
  setAlertParams
} from '../vuex/actions'

export default {
  name: 'Signup',
  components: {
    FormBuilder,
    Alert,
    Loading
  },

  vuex: {
    getters: {
      signupInfo,
      user,
      isLoading,
      alertParams
    },
    actions: {
      fetchShareCodes,
      fetchBuildings,
      checkBuildings,
      setActiveLoading,
      clearActiveLoading,
      setIsLoading,
      setAlertParams
    }
  },

  data () {
    return {
      alert: null,
      formFields: [
        { name: 'name', type: 'text', required: true, text: 'yourName' },
        {
          name: 'email',
          type: 'email',
          required: true,
          // matched: '^[a-zA-Z0-9]$'
          matches: '^[-a-z0-9~!$%^&*_=+}{\'?]+(\.[-a-z0-9~!$%^&*_=+}{\'?]+)*@([a-z0-9_][-a-z0-9_]*(\.[-a-z0-9_]+)*\.(aero|arpa|biz|com|coop|edu|gov|info|int|mil|museum|name|net|org|pro|travel|mobi|[a-z][a-z])|([0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}))(:[0-9]{1,5})?$' // eslint-disable-line
        },
        { name: 'phone', type: 'text', text: 'yourPhone', required: true },
        { name: 'company', type: 'text', text: 'yourCompany', required: true },
        { name: 'country', required: true },
        // { name: 'photo', type: 'file' },
        { name: 'password', type: 'password', required: true },
        { name: 'confirmPassword', type: 'password', required: true }
      ],
      formActions: [
        {
          name: 'signUp',
          submit: this.signupSubmit
        }
      ],
      logoImage: require('../assets/logo-login.png')
    }
  },

  route: {
    canActivate () {
      return !auth.user.authenticated
    },

    activate: function () {
      const body = document.getElementsByTagName('body')[0]
      body.className += ' bg-brand'
    }
  },

  ready () {
    window.fbAsyncInit = function () {
      /* eslint-disable */
      FB.init({
      /* eslint-enable */
        appId: api.facebookAppId,
        oauth: true,
        status: true, // check login status
        cookie: true, // enable cookies to allow the server to access the session
        xfbml: true // parse XFBML
      })
    }

    ;(function () {
      var e = document.createElement('script')
      e.src = document.location.protocol + '//connect.facebook.net/en_US/all.js'
      e.async = true
      document.getElementById('fb-root').appendChild(e)
    }())
  },

  methods: {
    // validatePhoto () {
    //   const photoFile = util.getFileFromInput('file')
    //   const maxFileSize = 1048576 // 1MB
    //   if (photoFile.size > maxFileSize) {
    //     this.alert = { type: 'danger', text: 'photoError' }
    //     return
    //   }
    //   util.setFileLinkName('file-link', photoFile.name)
    // },
    fbLoginSubmit () {
      /* eslint-disable */
      FB.login(response => {
      /* eslint-enable */
        console.log('statusChangeCallback')
        if (response.status === 'connected') {
          this.setIsLoading(true)
          const credentials = { access_token: response.authResponse.accessToken }
          auth.loginWithFacebook(credentials).then(() => {
            this.setIsLoading(false)
            this.handleAfterLogin()
          })
        } else if (response.status === 'not_authorized') {
          console.log('Please log into this app.')
        } else {
          console.log('Please log into this Facebook.')
        }
      }, {
        scope: 'email'
      })
    },

    signupSubmit () {
      let payload = {
        name: this.signupInfo.name,
        role: this.$route.query.role || '',
        email: this.signupInfo.email,
        phone: this.signupInfo.phone,
        company: this.signupInfo.company,
        country: this.signupInfo.country,
        password: this.signupInfo.password
      }
      if (this.signupInfo.password !== this.signupInfo.confirmPassword) {
        this.alert = { type: 'danger', text: 'passMatchFailed' }
        return
      }
      // const photoFile = util.getFileFromInput('file')
      // if (photoFile === undefined) {
      //   this.alert = { type: 'danger', text: 'photoRequired' }
      //   return
      // }
      let formData = null
      formData = new FormData() // eslint-disable-line
      console.log(payload)
      // formData.append('file', photoFile)
      formData.append('field', JSON.stringify(payload))
      this.setIsLoading(true)
      api.signUp(formData).then(result => {
        this.setIsLoading(false)
        this.setAlertParams({ type: 'success', text: 'signUpSucceded', show: true })
        setTimeout(() => { this.$route.router.go('/') }, 2000)
      }, err => {
        console.log('err: ', err)
        this.setIsLoading(false)
        this.setAlertParams({ type: 'danger', text: 'signUpFailed', show: true })
      })
    }
  }
}
</script>

<style lang="stylus">
@import '../variables'
@import '../mixins'

$fb-color = #3b5998

#signup,
#signup .container {
  max-width: 420px // TODO find a better way of doing this
}

#signup {
  /*color: $white*/
  transform: translate(-50%, -50%)
  top: 50%
  left: 50%
  position: fixed
  width: 100%

  .logo {
    display: block
    margin: 0 auto
    width: 170px
  }

  .col-centered {
    text-align: center
  }

  .form-group {
    margin-bottom: 4px
  }

  a {
    color: $white
    display: block
    text-align: center
  }

  hr {
    margin: 16px 0
    opacity: 0.4
  }

  input {
    background-color: alpha($white, 20%)
    border-color: $brand-color
    color: $white
    placeholder-color(alpha($white, 60%))
  }

  input[type="submit"] {
    background-color: $default-color
    border-top-color: $default-color
    border-left-color: $default-color
    border-right-color: $default-color
    border-bottom-color: darken($default-color, 10%)
    width: 100%

    &:hover {
      hover-color($default-color)
    }
  }

  .control-label {
    display: none
  }

  .col-sm-10 {
    width: 100%
  }

  .col-sm-offset-2 {
    margin-left: 0
  }

  .has-error {
    color: $white
  }

  label.btn-default {
    background-color: $brand-color
    border-color: darken($brand-color, 10%)
    color: $white
  }

  .input-group input {
    border-left-color: darken($brand-color, 10%)
  }

  select {
    background-color: alpha($white, 20%)
    border-color: $brand-color
    color: $white
    width: 100%

    option {
      color: $default-color
    }

    &.option-default {
      color: alpha($white, 60%)
    }
  }

  .btn-facebook {
    background: $fb-color
    border-color: darken($fb-color, 10%)
    color: $white
    display: block
    margin: 0 auto 10px

    i {
      color: $white
      border-right: 1px solid darken($fb-color, 10%)
      padding-right: 10px
      margin-right: 5px
    }

    &:hover {
      background: darken($fb-color, 10%)
    }
  }
}
</style>



