<template>
  <div
    class="list-labels list-item col-md-12 clearfix">
    <div
      v-if="hasThumbnails"
      class="col-md-1">
    </div>
    <div
      v-if="!field.isThumbnail"
      v-for="field in fields"
      :class="'col-md-' + field.column">
      <a @click="setSort(field.name, sortOrder)">
        {{ $t(field.name) }}
        <i
          v-if="!sortable && field.name === sortKey"
          :class="['fa', caretClass]">
        </i>
      </a>
    </div>
  </div>
</template>

<script>
import { sort } from '../../vuex/getters'
import { setSort, setStartSort } from '../../vuex/actions'

export default {
  props: {
    fields: Array,
    hasThumbnails: Boolean,
    sortable: Boolean,
    defaultSortKey: String,
    defaultSortOrder: Number
  },

  vuex: {
    getters: {
      sort
      // sortList
    },
    actions: {
      setSort,
      setStartSort
      // setSortList
    }
  },

  computed: {
    sortKey () {
      return this.sort.key
    },

    sortOrder () {
      return this.sort.order
    },

    caretClass () {
      return this.sortOrder > 0 ? 'fa-caret-up' : 'fa-caret-down'
    }
  },

  ready () {
    this.setStartSort(this.defaultSortKey, this.defaultSortOrder)
    // this.setSortList(this.defaultSortKey)
  }
}
</script>



