import Vue from 'vue'
import Router from 'vue-router'
import api from '../api'

Vue.use(Router)
var router = new Router()

const FORGET_PASS_URL = api.serverURL + '/api/v1/users/password/forget'
const LOGIN_URL = api.serverURL + '/api/v1/users/login'
const CHANGE_PASSWORD_URL = api.serverURL + '/api/v1/users/password/change'
const LOCAL_ITEM_USER_INFO = 'istaging_userinfo'
const LOCAL_ITEM_LEANTOKEN_NAME = 'leanistaging_token'

export default {
  user: {},
  // userInfo: null,

  login (context, creds) {
    return new Promise((resolve, reject) => {
      context.$http.post(LOGIN_URL, creds).then(resp => {
        const userInfo = this.setUserInfo(resp.data)
        console.log(resp.data)
        this.setUserLocalStorage(userInfo)
        this.checkAuth()
        resolve(resp)
      }, err => {
        console.log('Failed to login')
        reject(err)
      })
    })
  },

  changePassword (context, creds) {
    return new Promise((resolve, reject) => {
      context.$http.post(CHANGE_PASSWORD_URL, creds).then(resp => {
        const userInfo = this.setUserInfo(resp.data)
        this.setUserLocalStorage(userInfo)
        this.checkAuth()
        resolve(resp)
      }, err => {
        console.log('Failed to changePassword')
        reject(err)
      })
    })
  },

  loginWithFacebook (credentials) {
    return new Promise((resolve, reject) => {
      api.authWithFacebook(credentials).then(resp => {
        const userInfo = this.setUserInfo(resp.data)
        this.setUserLocalStorage(userInfo)
        this.checkAuth()
        resolve(resp)
      }, err => {
        console.log('Failed to login')
        reject(err)
      })
    })
  },

  logout (redirect) {
    localStorage.removeItem(LOCAL_ITEM_USER_INFO) // eslint-disable-line
    localStorage.removeItem(LOCAL_ITEM_LEANTOKEN_NAME) // eslint-disable-line
    this.user.authenticated = false
    this.user.vrTourAuthenticated = false
    if (redirect) router.go(redirect)
  },

  forgotPass (context, payload) {
    return new Promise((resolve, reject) => {
      context.$http.post(FORGET_PASS_URL, payload).then(resp => {
        resolve(resp)
      }, err => {
        reject(err)
      })
    })
  },

  resetPass (context, nonce, payload) {
    const RESET_PASS_URL = api.serverURL + '/api/v1/users/password/' + nonce + '/reset'
    return new Promise((resolve, reject) => {
      context.$http.post(RESET_PASS_URL, payload).then(resp => {
        resolve(resp)
      }, err => {
        reject(err)
      })
    })
  },

  checkAuth () {
    const userInfo = JSON.parse(localStorage.getItem(LOCAL_ITEM_USER_INFO)) // eslint-disable-line
    if (userInfo && userInfo.apitoken) {
      this.user = userInfo
      this.user.authenticated = true
      Vue.http.headers.common['istaging'] = this.user.apitoken
    } else {
      this.user.authenticated = false
    }
  },

  setUserLocalStorage (newInfo) {
    // if values aren't provided use the existing user info
    for (let prop in this.user) {
      if (newInfo[prop] !== '' && !newInfo[prop]) {
        newInfo[prop] = this.user[prop]
      }
      if (newInfo[prop] === '') newInfo[prop] = ''
    }
    localStorage.setItem(LOCAL_ITEM_USER_INFO, JSON.stringify(newInfo)) // eslint-disable-line
  },

  // VR Tour
  checkLeanAuth (callback) {
    const leanApiToken = localStorage.getItem(LOCAL_ITEM_LEANTOKEN_NAME) // eslint-disable-line
    if (leanApiToken) {
      this.user.vrTourAuthenticated = true
      Vue.http.headers.common['leanistaging'] = leanApiToken
    } else {
      this.user.vrTourAuthenticated = false
    }
    if (callback) {
      callback(this.user.vrTourAuthenticated)
    }
  },

  setLeanLocalStorage (token) {
    localStorage.setItem(LOCAL_ITEM_LEANTOKEN_NAME, token) // eslint-disable-line
  },

  setUserInfo (data) {
    var userData = {
      id: data.objectId || '',
      username: data.username || '',
      apitoken: data.sessionToken,
      name: data.name,
      role: data.userType || '',
      company: data.company || '',
      country: data.country || '',
      photo: data.profileUrl || '',
      phone: data.phone || ''
    }
    return userData
  }
}



