<template>
  <form class="form-horizontal" autocomplete="off">
    <div
      class="form-group"
      :class="[showValidationClasses($key, value), { 'form-hidden': !isInFields($key) }]"
      :style="'order:' + findOrder($key)"
      v-for="value in object">
      <label
        :class="['control-label', setLabelColumns()]"
        v-if="$key !== 'url'">
        {{ $t(findCustomText($key)) || $t($key) }}
      </label>
      <div :class="setFieldColumns()">
        <input
          class="form-control"
          v-if="isTextField($key)"
          v-model="value"
          :type="getPropType($key)"
          :placeholder="$t(findCustomText($key)) || $t($key)"
          @input="validateOnChange($key, value)">
        <input
          class="form-control"
          v-if="isNumberField($key)"
          v-model="value"
          :type="getPropType($key)"
          :placeholder="$t(findCustomText($key)) || $t($key)"
          @input="validateOnChange($key, value)"
          number>
        <textarea
          class="form-control"
          v-if="isTextareaField($key)"
          v-model="value"
          :placeholder="$t(findCustomText($key)) || $t($key)"
          @input="validateOnChange($key, value)">
        </textarea>
        <select
          v-if="$key === 'category'"
          v-model="value"
          :class="{ 'form-other-select': value === 'other' }">
          <!-- adding selected makes previous panorama stay in the form -->
          <option :value="value">{{ $t(value) }}</option>
          <option
            v-if="category !== value"
            v-for="category in categories"
            :value="category">
            {{ $t(category) }}
          </option>
        </select>
        <!-- for panorama -->
        <label class="radio-inline" v-if="isBooleanField($key)">
          <input type="radio" :value="false" v-model="value">
          {{ $t('no') }}
        </label>
        <label class="radio-inline" v-if="isBooleanField($key)">
          <input type="radio" :value="true" v-model="value">
          {{ $t('yes') }}
        </label>
        <!-- for marker -->
        <label class="radio-inline" v-if="isMarkerBooleanField($key)">
          <input type="radio" :value="0" v-model="value">
          {{ $t('none') }}
        </label>
        <label class="radio-inline" v-if="isMarkerBooleanField($key)">
          <input type="radio" :value="1" v-model="value">
          {{ $t('buyNow') }}
        </label>
        <label class="radio-inline" v-if="isMarkerBooleanField($key)">
          <input type="radio" :value="2" v-model="value">
          {{ $t('viewAtHome') }}
        </label>
        <label class="radio-inline" v-if="isMarkerBooleanField($key)">
          <input type="radio" :value="3" v-model="value">
          {{ $t('learnMore') }}
        </label>
        <input
          class="form-control form-other"
          v-if="value === 'other'"
          v-model="object.customCategory">
        <!-- TODO join these two select tags -->
        <select
          v-if="$key === 'country'"
          v-model="value"
          :class="{ 'option-default': !value }">
          <!-- adding selected makes previous panorama stay in the form -->
          <option v-if="!value" value="">
            {{ $t('country') }}
          </option>
          <option v-if="country" :value="value.name">{{ value.native }}</option>
          <option
            v-if="country.name !== value.name"
            v-for="country in countries | orderBy 'name'"
            :value="country.name">
            {{ country.native }}
          </option>
        </select>
        <div v-if="getPropType($key) === 'file'" class="input-group">
          <span class="input-group-btn">
            <label
              :class="{ 'disabled': isLoading }"
              :for="findFieldName($key)"
              class="btn btn-default">
              {{ $t('browse') }}
            </label>
            <input
              v-if="getPropType($key) === 'file'"
              type="file"
              :name="findFieldName($key)"
              :id="findFieldName($key)"
              class="hidden"
              @change="onChange">
          </span>
          <input
            type="text"
            :name="findFieldName($key) + '-link'"
            class="form-control"
            :placeholder="getFilenameFromUrl(value) || $t($key)"
            disabled>
          <!-- <input
            type="text"
            :name="findFieldName($key) + '-link'"
            class="form-control"
            :placeholder="value || $t($key)"
            disabled> -->
          <img class="upload-prev" v-if="value && hasPreviewPhoto" :src="value">
        </div>
        <p v-if="$key === 'viewerLink' && value" class="form-control-static">
          <a :href="value">{{ value }}</a>
        </p>
        <p v-if="$key === 'viewerLink' && !value" class="form-control-static">
          {{ $t('noLink') }}
        </p>
        <div
          v-if="field.name === $parent.$key"
          v-for="field in fields">
          <p class="help-block">{{ $t(field.description) }}</p>
          <p
            class="has-error"
            v-if="isKeyRequired($parent.$key) &&
              isEmpty(value) &&
              formSubmitted">
            {{ $t('requiredError') }}{{ $t($parent.$key) | lowercase }}
          </p>
          <p
            class="has-error"
            v-if="isKeyRequired($parent.$key) &&
              isTooShort(value, field.minLength) &&
              formSubmitted">
            {{ $t('minLengthError', { n: field.minLength }) }}
          </p>
          <p
            class="has-error"
            v-if="isKeyRequired($parent.$key) &&
              isTooLong(value, field.maxLength) &&
              formSubmitted">
            {{ $t('maxLengthError', { n: field.maxLength }) }}
          </p>
          <p
            class="has-error"
            v-if="isKeyRequired($parent.$key) &&
            isUnmatched(value, field.matches) &&
            formSubmitted">
            {{ $t('invalidError') }}{{ $t($parent.$key) | lowercase }}
          </p>
        </div>
      </div>
    </div>
    <!-- TODO modularize these actions -->
    <div class="form-group form-actions" v-if="!isInModal">
      <div class="col-sm-offset-2 col-sm-10">
        <input
          type="submit"
          :class="{ 'disabled': isLoading }"
          class="btn btn-primary"
          :value="btnText ? $t(btnText) : $t('update')"
          @click.prevent="onSubmit">
        <input
          v-if="hasDelete"
          type="submit"
          :class="{ 'disabled': isLoading }"
          class="btn btn-default"
          :value="delText ? $t(delText) : $t('delete')"
          @click.prevent="onDelete">
      </div>
    </div>
  </form>
</template>

<script>
import countriesList from 'countries-list'
import { hasFieldChanged, isLoading } from '../vuex/getters'
import {
  setFieldChanged,
  unsetFieldChanged
} from '../vuex/actions'

export default {
  props: {
    fields: Array,
    object: Object,
    hasDelete: Boolean,
    hasPreviewPhoto: Boolean,
    isInModal: Boolean,
    isInEditor: Boolean,
    btnText: String,
    delText: String,
    categories: Array
  },

  vuex: {
    getters: {
      hasFieldChanged,
      isLoading
    },
    actions: {
      setFieldChanged,
      unsetFieldChanged
    }
  },

  data () {
    return {
      formSubmitted: false,
      formInvalid: false,
      requiredFields: [],
      minLengthFields: [],
      maxLengthFields: [],
      matchesFields: []
    }
  },

  computed: {
    countries () {
      const country = countriesList.countries
      let countriesNative = []
      for (let property in country) {
        countriesNative.push({
          name: country[property].name,
          native: country[property].native
            ? country[property].native
            : country[property].name
        })
      }
      return countriesNative
    }
  },

  ready () {
    this.fields.forEach(field => {
      if (field.required) this.requiredFields.push(field.name)
      if (field.minLength) this.minLengthFields.push(field)
      if (field.maxLength) this.maxLengthFields.push(field)
      if (field.matches) this.matchesFields.push(field)
    })
  },

  methods: {
    getFilenameFromUrl (url) {
      if (!url) return
      const filename = url.match(/([^\/]+)\.\w+(?=\?|$)/)[0]
      return filename
    },

    setPlaceholderValue (value) {
      return this.$options.filters.urlPathToFileName(value)
    },

    onChange () {
      this.$emit('on-change')
    },

    onDelete () {
      this.$emit('on-delete')
    },

    onSubmit () {
      this.validate()
      this.formSubmitted = true
      if (!this.formInvalid) {
        this.$emit('on-submit')
        this.unsetFieldChanged()
      }
    },

    setLabelColumns () {
      return (this.isInModal) ? 'col-sm-3' : 'col-sm-2'
    },

    setFieldColumns () {
      return (this.isInModal) ? 'col-sm-9' : 'col-sm-10'
    },

    showValidationClasses (key, value) {
      return {
        'has-error': (this.isKeyRequired(key)) &&
          (this.isEmpty(value) ||
          this.isTooShort(value, this.findMinLength(key)) ||
          this.isTooLong(value, this.findMaxLength(key)) ||
          this.isUnmatched(value, this.findMatches(key))) &&
          this.formSubmitted,
        'required': this.isKeyRequired(key)
      }
    },

    findField (key, prop) {
      let field = null
      this.fields.filter(obj => {
        if (obj.name === key) field = obj
      })
      return field ? field[prop] : ''
    },

    findFieldName (key) {
      return this.findField(key, 'name')
    },

    findCustomText (key) {
      return this.findField(key, 'text')
    },

    findOrder (key) {
      return this.findField(key, 'order')
    },

    findRadioFields (key) {
      return this.findField(key, 'radioFields')
    },

    isKeyRequired (key) {
      return this.requiredFields.indexOf(key) > -1
    },

    isEmpty (value) {
      return !value
    },

    isTooShort (value, minLength) {
      if (minLength && value) return value.length < minLength
    },

    isTooLong (value, maxLength) {
      if (maxLength && value) return value.length > maxLength
    },

    isUnmatched (value, string) {
      if (string) {
        const regex = new RegExp(string)
        return !regex.test(value)
      }
    },

    findMinLength (key) {
      if (this.minLengthFields) {
        let minLength = null
        this.minLengthFields.filter(obj => {
          if (obj.name === key) minLength = obj.minLength
        })
        return minLength
      }
    },

    findMaxLength (key) {
      if (this.maxLengthFields) {
        let maxLength = null
        this.maxLengthFields.filter(obj => {
          if (obj.name === key) maxLength = obj.maxLength
        })
        return maxLength
      }
    },

    findMatches (key) {
      if (this.matchesFields) {
        let matches = null
        this.matchesFields.filter(obj => {
          if (obj.name === key) matches = obj.matches
        })
        return matches
      }
    },

    validate () {
      for (let key in this.object) {
        const value = this.object[key]
        if ((this.isKeyRequired(key)) &&
          this.isEmpty(value) ||
            this.isTooShort(value, this.findMinLength(key)) ||
            this.isTooLong(value, this.findMaxLength(key)) ||
            this.isUnmatched(value, this.findMatches(key))) {
          this.formInvalid = true
          break
        }
        this.formInvalid = false
      }
    },

    validateOnChange (key, value) {
      // this.hasFieldChanged = true
      this.setFieldChanged()
      this.validate()
    },

    isInFields (key) {
      let fieldNames = []
      this.fields.forEach(field => fieldNames.push(field.name))
      if (fieldNames) {
        return fieldNames.indexOf(key) > -1
      } else {
        return true // show all the fields if they are not defined
      }
    },

    getPropType (key) {
      return this.fields.map(field => {
        if (field.name === key) return field.type
      }).join('') // TODO find out why commas are being added
    },

    isTextField (key) {
      const propType = this.getPropType(key)
      const textTypes = ['text', 'password', 'email']
      if (key !== 'url') {
        console.log('not url')
        return textTypes.indexOf(propType) > -1
      } else {
        console.log('is url')
        // console.log(textTypes.indexOf(propType) > -1)
        // console.log(this.object.action)
        console.log(textTypes.indexOf(propType) > -1 && this.object.action > 0)
        return textTypes.indexOf(propType) > -1 && this.object.action > 0
      }
    },

    isNumberField (key) {
      const propType = this.getPropType(key)
      const textTypes = ['number']
      return textTypes.indexOf(propType) > -1
    },

    isBooleanField (key) {
      const propType = this.getPropType(key)
      const textTypes = ['radio']
      return textTypes.indexOf(propType) > -1
    },

    isMarkerBooleanField (key) {
      const propType = this.getPropType(key)
      const textTypes = ['radioMarker']
      return textTypes.indexOf(propType) > -1
    },

    isTextareaField (key) {
      const propType = this.getPropType(key)
      const textTypes = ['textarea']
      return textTypes.indexOf(propType) > -1
    },

    isFirstAction (index) {
      return index === 0
    },

    isSecondAction (index) {
      return index === 1
    }
  }
}
</script>

<style lang="stylus">
@import '../variables'

.form-horizontal {
  display: flex
  flex-flow: row wrap

  .form-group {
    flex: 1 100%
  }

  .form-actions {
    order: 9999
  }

  .form-hidden {
    display: none
  }
}

p.has-error {
  color: $red
}

.upload-prev {
  width: 100%
}

.form-other-select {
  float: left
  margin-right: 10px
}

.form-other {
  float: left
  width: 100%
  max-width: 300px
}
</style>



