api.authWithVRTour = (credentials) => {
  return new Promise((resolve, reject) => {
    const URLEnd = '/leanapi/vrbackend/login'
    const options = { timeout: timeoutSeconds }
    const AUTH_WITH_VRTOUR_URL = api.bpLeanURL + URLEnd
    Vue.http.post(AUTH_WITH_VRTOUR_URL, credentials, options).then(resp => {
      if (resp.status === 200) resolve(resp)
      else reject(resp)
    }, err => {
      console.log('authWithVRTour error: ', err)
      reject(err)
    })
  })
}

