api.updateUserProfile = function (payload, photoForm) {
  var promiseUpload = Promise.resolve()
  if (photoForm) {
    promiseUpload = api.uploadUserPhoto(photoForm)
  }
  console.log('PAYLOAD: ', payload)
  var promiseUpdate = new Promise((resolve, reject) => {
    const URLEnd = '/api/v1/users/profile'
    const options = { timeout: timeoutSeconds }
    const UPDATE_USER_PROFILE_URL = api.serverURL + URLEnd
    Vue.http.post(UPDATE_USER_PROFILE_URL, payload, options).then(resp => {
      console.log('RESP: ', resp)
      if (resp.status === 200) resolve(resp)
      else reject(resp)
    }, err => {
      console.log('updateUserProfile error: ', err)
      reject(err)
    })
  })
  return Promise.all([promiseUpdate, promiseUpload])
}

