<template>
  <div id="forgot-pass" class="col-md-12">
    <!-- <alert v-if="alert" :alert="alert" @on-click="alert = null">
    </alert> -->
    <p>{{ $t('forgotPass') }}</p>
    <form-builder
      :object="authInfo"
      :fields="formFields"
      btn-text="recoveryEmail"
      @on-submit="forgotPassSubmit">
    </form-builder>
    <alert v-show="alertParams.show"></alert>
    <loading v-show="isLoading" color="white" :no-overlay="true"></loading>
  </div>
</template>

<script>
import FormBuilder from '../components/FormBuilder'
import Alert from '../components/Alert'
import Loading from '../components/Loading'
import { authInfo, activeLoading, alertParams, isLoading } from '../vuex/getters'
import {
  setActiveLoading,
  clearActiveLoading,
  setAlertParams,
  setIsLoading
} from '../vuex/actions'

import auth from '../auth'

export default {
  name: 'ForgotPass',
  components: {
    FormBuilder,
    Alert,
    Loading
  },

  vuex: {
    getters: {
      authInfo,
      activeLoading,
      alertParams,
      isLoading
    },
    actions: {
      setActiveLoading,
      clearActiveLoading,
      setAlertParams,
      setIsLoading
    }
  },

  data () {
    return {
      nonce: '',
      // alert: null,
      formFields: [
        { name: 'email', type: 'email', required: true }
      ]
    }
  },

  route: {
    data ({ to }) {
      const nonce = to.params.nonce
      return {
        nonce: nonce
      }
    },

    canActivate () {
      return !auth.user.authenticated
    },

    activate: function () {
      const body = document.getElementsByTagName('body')[0]
      body.className += ' bg-brand'
    }
  },

  methods: {
    forgotPassSubmit () {
      const payload = {
        email: this.authInfo.email
      }
      this.setIsLoading(true)
      auth.forgotPass(this, payload).then(result => {
        this.setIsLoading(false)
        this.setAlertParams({ type: 'success', text: 'forgotPassSucceded', show: true })
      }).catch(err => {
        this.setIsLoading(false)
        console.log('Error:', err)
        this.setAlertParams({ type: 'danger', text: 'forgotPassFailed', show: true })
      })
    }
  }
}
</script>

<style lang="stylus">
@import '../variables'
@import '../mixins'

$fb-color = #3b5998

#forgot-pass,
#forgot-pass .container {
  max-width: 420px // TODO find a better way of doing this
}

#forgot-pass {
  color: $white
  transform: translate(-50%, -50%)
  top: 50%
  left: 50%
  position: fixed
  width: 100%

  .col-centered {
    text-align: center
  }

  .form-group {
    margin-bottom: 4px
  }

  hr {
    margin: 16px 0
    opacity: 0.4
  }

  input {
    background-color: alpha($white, 20%)
    border-color: $brand-color
    color: $white
    placeholder-color(alpha($white, 60%))
  }

  input[type="submit"] {
    background-color: $default-color
    border-top-color: $default-color
    border-left-color: $default-color
    border-right-color: $default-color
    border-bottom-color: darken($default-color, 10%)
    width: 100%

    &:hover {
      hover-color($default-color)
    }
  }

  .control-label {
    display: none
  }

  .col-sm-10 {
    width: 100%
  }

  .col-sm-offset-2 {
    margin-left: 0
  }

  .has-error {
    color: $white
  }

  .btn-facebook {
    background: $fb-color
    border-color: darken($fb-color, 10%)
    color: $white
    display: block
    margin: 0 auto

    i {
      border-right: 1px solid darken($fb-color, 10%)
      padding-right: 10px
      margin-right: 5px
    }

    &:hover {
      background: darken($fb-color, 10%)
    }
  }
}
</style>



