<template>
  <div id="panorama" class="col-md-12">
    <alert
      v-if="alert"
      :alert="alert"
      @on-click="alert = null">
    </alert>
    <div class="subheader clearfix">
      <h1
        v-if="!isEmptyObj(panorama)"
        class="header-center">
        {{ panorama ? panorama.name : '' }}
      </h1>
      <!-- <div id="panorama-buttons">
        <a
          :href="'https://www.facebook.com/sharer/sharer.php?u=' + encodeURIComponent(panorama.web) + '&t='"
          target="_blank"
          title="Share on Facebook">
          <i class="fa fa-facebook-square fa-2x"></i>
        </a>
      </div> -->
    </div>
    <progress-bar :file-progress="fileProgress"></progress-bar>
    <form-builder
      :object="panorama"
      :fields="formFields"
      :has-delete="true"
      :categories="categories"
      btn-text="updateDetails"
      del-text="deletePanorama"
      @on-change="validatePanorama"
      @on-submit="updatePanorama('updateLoading')"
      @on-delete="deletePanorama">
    </form-builder>
    <loading v-show="activeLoading === 'updateLoading'" color="brand">
    </loading>
    <div class="row">
      <hr>
      <panorama-prev
        v-if="!isEmptyObj(panorama)"
        :item="panorama"
        @update="updatePanorama('editLoading')"
        @reset="updatePanorama('resetLoading')">
      </panorama-prev>
    </div>
    <div class="row">
      <div class="col-md-12 panorama-bottom">
        <a
          :href="buildingURL"
          class="btn btn-default btn-back">
          {{ $t('backToBuilding') }}
        </a>
      </div>
    </div>
    <modal
      v-show="activeModal === 'shareConfirm'"
      modal-title="confirmShare"
      :post-save="sharePanoramaConfirm">
      <p>{{ $t('confirmSharePanorama') }}</p>
    </modal>
    <modal
      v-show="activeModal === 'sharePanoramaSuccess'"
      modal-title="viewerLink">
      <a class="share-link" :href="panoViewerURL" target="_blank">
        {{ panoViewerURL }}
      </a>
      <share-buttons :url="panoViewerURL"></share-buttons>
    </modal>
    <modal
      v-show="activeModal === 'deletePanoramaConfirm'"
      modal-title="confirmDelete"
      :post-save="deletePanoramaConfirm">
      <p>{{ $t('confirmDeletePanorama') }}</p>
    </modal>
    <spinner v-if="isEmptyObj(panorama)"></spinner>
    <alert v-show="alertParams.show"></alert>
    <loading v-show="isLoading" color="brand"></loading>
    <!-- <pre>{{ panorama | json }}</pre> -->
  </div>
</template>

<script>
import _ from 'lodash'
import util from '../util'
import FormBuilder from '../components/FormBuilder'
import Modal from '../components/Modal'
import Alert from '../components/Alert'
import Spinner from '../components/Spinner'
import Loading from '../components/Loading'
import ShareButtons from '../components/ShareButtons'
import ProgressBar from '../components/ProgressBar'
import PanoramaPrev from '../pages/PanoramaPrev'
import PanoramaEditor from '../pages/PanoramaEditor'
import Auth from '../mixins/Auth'
import api from '../api'
import {
  buildings,
  building,
  panoramas,
  panorama,
  activeModal,
  activeLoading,
  alertParams,
  isNewBuilding,
  isNewPanorama,
  isLoading,
  isImageLoading,
  cameraRotation,
  uploadURLs
} from '../vuex/getters'

import {
  fetchBuildings,
  fetchBuildingById,
  fetchPanoramaById,
  fetchPanoramasByBuildingId,
  removePanorama,
  setCurrentPanoramaId,
  setActiveModal,
  clearActiveModal,
  setIsNewPanorama,
  setIsUpdatedPanorama,
  setAlertParams,
  setIsLoading,
  doImageUpload,
  updatePanoramaState,
  setIsImageLoading,
  setPanoramaPhotoState,
  clearPanorama
} from '../vuex/actions'

export default {
  name: 'Panorama',
  mixins: [
    Auth
  ],

  components: {
    FormBuilder,
    Modal,
    Spinner,
    Alert,
    Loading,
    PanoramaPrev,
    PanoramaEditor,
    ProgressBar,
    ShareButtons
  },

  vuex: {
    getters: {
      buildings,
      building,
      panoramas,
      panorama,
      activeModal,
      activeLoading,
      alertParams,
      isNewBuilding,
      isNewPanorama,
      isLoading,
      isImageLoading,
      cameraRotation,
      uploadURLs
    },
    actions: {
      fetchBuildings,
      fetchBuildingById,
      fetchPanoramaById,
      fetchPanoramasByBuildingId,
      removePanorama,
      setCurrentPanoramaId,
      setActiveModal,
      clearActiveModal,
      setIsNewPanorama,
      setIsUpdatedPanorama,
      setAlertParams,
      setIsLoading,
      doImageUpload,
      updatePanoramaState,
      setIsImageLoading,
      setPanoramaPhotoState,
      clearPanorama
    }
  },

  data () {
    return {
      alert: null,
      panoramaId: '',
      panoramaPreview: '',
      buildingId: '',
      formFields: [
        { name: 'name', type: 'text', required: true, text: 'panoName' },
        { name: 'category' },
        { name: 'web', type: 'file', description: 'panoramaHint' }
        // { name: 'isDefault', type: 'radio' }
      ],
      redirectUrl: '',
      sharePanoCode: '',
      filename: '',
      fileProgress: -1,
      panoUpdatePayload: Object,
      panoResizeInterval: null,
      panoResizeIntervalTime: 5000,
      panoramaFile: null
    }
  },

  watch: {
    uploadURLs (val, oldVal) {
      const metadata = {
        app: val[0],
        thumbnail: val[1],
        panoId: this.panorama.id
      }
      this.fileProgress = 100
      this.updatePanoFileMetadata(metadata, this.panoUpdatePayload)
    },

    building () {
      this.fetchPanoramasByBuildingId(this.buildingId)
    },

    panoramas () {
      if (!this.isNewPanorama) this.fetchPanoramaById(this.panoramaId)
    },

    panorama () {
      if (this.isImageLoading) {
        // this.setIsLoading(false)
        this.setAlertParams({ type: 'success', text: 'panoramaUpdated', show: true })
      }
    }
  },

  route: {
    data (transition) {
      const buildingId = transition.to.params.buildingId
      const panoramaId = transition.to.params.panoramaId
      if (!this.isNewBuilding) {
        this.fetchBuildingById(buildingId, err => {
          if (err) {
            console.log('An error ocurred: ', err)
            this.setAlertParams({ type: 'danger', text: 'errorOccurred', show: true })
          }
        })
      }
      this.setIsNewPanorama(false)
      return {
        panoramaId: panoramaId,
        buildingId: buildingId
      }
    },

    deactivate () {
      this.clearPanorama()
      this.clearCheckPanoInterval()
    }
  },

  computed: {
    buildingURL () {
      const baseURL = window.location.protocol + '//' + window.location.host
      return this.building ? baseURL + '/#!/buildings/' + this.building.id : ''
    },

    categories () {
      if (!this.panorama) return
      if (this.panorama.category &&
        api.categories.indexOf(this.panorama.category) === -1) {
        api.categories.unshift(this.panorama.category)
      }
      return api.categories
    },

    panoViewerURL () {
      return api.panoViewerURL + '?code=' + this.sharePanoCode
    }
  },

  ready () {
    const shareCodeId = this.$route.params.shareCodeId
    const buildingId = this.$route.params.buildingId
    if (this.$route.params.shareCodeId) {
      this.redirectUrl = '/share-codes/' +
      shareCodeId +
      '/buildings/' +
      buildingId
    } else {
      this.redirectUrl = '/buildings/' + buildingId
    }
  },

  methods: {
    isEmptyObj (panorama) {
      return _.isEmpty(panorama)
    },

    clearCheckPanoInterval () {
      clearInterval(this.panoResizeInterval)
      this.panoResizeInterval = null
    },

    updatePanorama (loadingEl) {
      let payload = this.panorama
      let cameraRotation
      // need this to prevent reseting the panorama when saving the building
      if (loadingEl !== 'updateLoading') {
        (loadingEl !== 'resetLoading')
          // TODO turn cameraRotation into an Object
          ? cameraRotation = this.cameraRotation.y
          : cameraRotation = 0
        payload.panoRotation = cameraRotation
      }
      if (payload.customCategory) {
        payload.category = payload.customCategory
        delete payload.customCategory
      }

      this.panoUpdatePayload = payload
      this.setIsLoading(true)
      const panoramaFile = util.getFileFromInput('web')
      if (panoramaFile) {
        var resizeArr = [{ h: 2048, w: 4096 }, { h: 90, w: 180 }]
        this.filename = panoramaFile.name
        this.fileProgress = 0
        this.doImageUpload(panoramaFile, resizeArr)
      } else {
        api.updatePanorama(this.panorama.id, payload).then(resp => {
          // this.fetchBuildings()
          this.fetchBuildingById(this.buildingId, err => {
            if (err) {
              console.log('An error ocurred: ', err)
              this.setIsLoading(false)
              this.setAlertParams({ type: 'danger', text: 'errorOccurred', show: true })
            }
          })
          // console.log('RESP:', resp.data)
          // const panorama = resp.data
          // this.renamePanoramaProps(panorama)
          // this.updatePanoramaState(panorama)
          this.setIsUpdatedPanorama(true)
          this.setIsLoading(false)
          this.setAlertParams({ type: 'success', text: 'panoramaUpdated', show: true })
        }, err => {
          console.log('update panorama failed E: ', err)
          this.setIsLoading(false)
          this.setAlertParams({ type: 'danger', text: 'errorOccurred', show: true })
        })
      }
      this.setIsLoading(true)
    },

    updatePanoFileMetadata (metadata, payload) {
      api.updatePanoFileMetadata(metadata, payload).then(result => {
        console.log('result: ', result)
        this.fileProgress = -1
        this.fetchBuildingById(this.buildingId, err => {
          if (err) {
            console.log('An error ocurred: ', err)
            this.setIsLoading(false)
            this.setAlertParams({ type: 'danger', text: 'errorOccurred', show: true })
            return
          }
          // this.setPanoramaPhotoState('', '')
          this.$nextTick(() => {
            const newPanoramaSize = util.setLeancloudImgSize(result.data.web, '1024', '512')
            this.setPanoramaPhotoState(newPanoramaSize, result.data.thumbnail)
            this.setIsLoading(false)
            this.setIsImageLoading(true)
            // this.setAlertParams({ type: 'success', text: 'panoramaUpdated', show: true })
            util.clearFileFromInput('web')
          })
        })
          // image.onload = () => {
          // this.setIsImageLoading(false)
          // this.setAlertParams({ type: 'success', text: 'panoramaUpdated', show: true })
          // }
        // })
      }, err => {
        this.fileProgress = -1
        console.log('update panorama failed E: ', err)
        this.setIsLoading(false)
        this.setAlertParams({ type: 'danger', text: 'errorOccurred', show: true })
      })
    },

    sharePanorama (id) {
      this.setCurrentPanoramaId(id)
      this.setActiveModal('shareConfirm')
    },

    sharePanoramaConfirm () {
      const payload = { 'panoId': this.panorama.id }
      this.setIsLoading(true)
      return api.sharePanorama(payload).then(result => {
        this.setIsLoading(false)
        this.sharePanoCode = result.data.code
        this.setActiveModal('sharePanoramaSuccess')
      })
    },

    deletePanorama () {
      if (this.panoramas.length === 1) {
        this.setAlertParams({ type: 'warning', text: 'lastPanoramaError', show: true })
      } else {
        this.setActiveModal('deletePanoramaConfirm')
      }
    },

    deletePanoramaConfirm () {
      const panorama = _.find(this.panoramas, { id: this.panoramaId })
      const payload = { panoId: panorama.id }
      this.setIsLoading(true)
      return api.deletePanorama(payload).then(result => {
        // this.fetchBuildings(() => {
        const deletedPanorama = result.data.objectId
        this.removePanorama(deletedPanorama)
        this.setAlertParams({ type: 'default', text: 'panoramaDeleted', show: true })
        this.clearActiveModal()
        this.setIsLoading(false)
        setTimeout(() => {
          this.$route.router.go(this.redirectUrl)
        }, 2000)
        // })
      })
    },

    validatePanorama () {
      // TODO fix error not popping up on second attempt
      const panoramaFile = util.getFileFromInput('web')
      const maxFileSize = 20971520 // 1MB
      const imageExtentionRE = /\.(jpg|jpeg)$/i
      if (panoramaFile.size > maxFileSize) {
        this.setAlertParams({ type: 'danger', text: 'panoramaError', show: true })
        util.clearFileFromInput('web')
        return
      }
      if (!imageExtentionRE.test(panoramaFile.name)) {
        this.setAlertParams({ type: 'danger', text: 'panoramaInvalid', show: true })
        util.clearFileFromInput('web')
        return
      }
      util.setFileLinkName('web-link', panoramaFile.name)
    },

    renamePanoramaProps (panorama) {
      // TODO find a better way to order the props
      const x = panorama.x
      const y = panorama.y
      const thumbnail = panorama.thumbnail
      const category = panorama.categoryType
      const id = panorama.objectId
      const name = panorama.name
      // const isDefault = panorama.isDefault

      delete panorama.categoryType
      delete panorama.objectId
      delete panorama.x
      delete panorama.y
      delete panorama.thumbnail
      delete panorama.name
      // delete panorama.isDefault
      panorama.id = id
      panorama.thumbnail = thumbnail
      panorama.name = name
      panorama.category = category
      panorama.x = x
      panorama.y = y
      // panorama.isDefault = isDefault
    }
  }
}
</script>

<style lang="stylus">
#panorama {
  /*h1.header-center {
    margin-bottom: 2px
  }*/

  .panorama-prev-submit {
    margin: 15px 0 0
    text-align: center
  }

  .panorama-bottom {
    display: flex
    justify-content: center
  }

  .btn-back {
    margin: 30px 0 0
  }

  /*#panorama-buttons {
    margin-bottom: 15px

    > a {
      display: inline-block
      font-size: 12px
      margin-top: 2px
    }
  }*/
}
</style>



