<template>
  <nav class="navbar navbar-default navbar-static-top">
    <div class="container">
      <div class="navbar-header">
        <a class="navbar-brand" :href="'#/buildings'">
          <img :src="logoImage">
        </a>
      </div>
      <div id="navbar">
        <ul class="nav navbar-nav">
          <li v-for="link in links" v-if="shouldNotHide(link)">
            <a
              :href="link.path ? link.path : '/#'"
              @click="link.method ? link.method() : null"
              v-active-link="$route">
              {{ $t(link.name) }}
            </a>
          </li>
        </ul>
        <ul class="nav navbar-nav navbar-right">
          <!-- <li v-if="user.role !== 'admin'"> -->
          <li>
            <a href="#/profile" v-active-link="$route">
              <img
                v-if="!isEmptyObj(user)"
                class="img-circle"
                :src="profilePhoto">
              {{ user.name ? user.name : $t('anonymous') }}
            </a>
          </li>
          <li><a @click="logout">{{ $t('logOut') }}</a></li>
        </ul>
      </div>
      <modal
        v-show="activeModal === 'creatingBuilding'"
        :no-confirm="true"
        :no-close="true">
        <p>{{ $t('creatingBuilding') }}</p>
      </modal>
    </div>
    <loading v-show="isLoading" color="brand"></loading>
  </nav>
</template>

<script>
import _ from 'lodash'
import moment from 'moment'
import auth from '../auth'
import api from '../api'
import Modal from '../components/Modal'
import Loading from '../components/Loading'
import {
  user,
  authInfo,
  activeModal,
  activeLoading,
  profilePhoto,
  isLoading
} from '../vuex/getters'

import {
  resetAll,
  fetchBuildings,
  setActiveModal,
  clearActiveModal,
  addBuildingToState,
  setIsNewBuilding,
  setIsLoading
} from '../vuex/actions'

export default {
  vuex: {
    getters: {
      user,
      authInfo,
      activeModal,
      activeLoading,
      profilePhoto,
      isLoading
    },
    actions: {
      resetAll,
      fetchBuildings,
      setActiveModal,
      clearActiveModal,
      addBuildingToState,
      setIsNewBuilding,
      setIsLoading
    }
  },

  components: {
    Modal,
    Loading
  },

  data () {
    return {
      logoImage: require('../assets/logo.png'),
      links: [
        { name: 'myProjects', path: '#/buildings' },
        { name: 'newProject', method: this.newBuilding }
        // { name: 'sharingHistory', path: '#/share-codes' }
        // { name: 'myProfile', path: '#/profile' }
      ],
      alert: null,
      isFormLoading: false
    }
  },

  methods: {
    logout () {
      auth.logout('/')
      this.resetAll()
    },

    isEmptyObj (building) {
      return _.isEmpty(building)
    },

    newBuilding () {
      this.setActiveModal('creatingBuilding')
      this.setIsLoading(true)
      api.createBlankBuilding().then(resp => {
        const buildingId = resp.data.objectId
        const building = resp.data
        building.shareCode = building.sharecode
        building.shareCode = api.viewerURL + '#!/' + building.shareCode
        delete building.sharecode
        this.renameBuildingProps(building)
        this.addBuildingToState(building)
        this.clearActiveModal()
        this.setIsLoading(false)
        this.setIsNewBuilding(true)
        this.$route.router.go('/buildings/' + buildingId)
        // this.fetchBuildings(() => {
        //   this.clearActiveModal()
        //   this.clearActiveLoading()
        // })
      }, err => {
        console.log('Error:', err)
        this.setIsLoading(false)
        this.setAlertParams({ type: 'danger', text: 'errorOccurred', show: true })
        this.clearActiveModal()
        // this.alert = { type: 'danger', text: 'createBuildingFailed' }
      })
    },

    shouldNotHide (link) {
      return this.user.role !== 'admin' || link.name !== 'myProfile'
    },

    renameBuildingProps (building) {
      building.date = moment(building.updatedAt).format('YYYY/MM/DD')
      building.id = building.objectId
      building.panoramas = []
      delete building.objectId
      delete building.createdAt
      delete building.updatedAt
    }
  }
}
</script>

<style lang="stylus">
@import '../variables'

.navbar-brand {
  width: 110px

  img {
    width: 100%
  }
}

.nav {
  .active a {
    background: none !important
    color: $brand-color !important
  }

  .img-circle {
    vertical-align: top
    width: 20px
    height: 20px
  }

  li {
    img,
    a {
      display: inline-block
    }

    img {
      margin-right: 8px
    }
  }
}
</style>



