<template>
  <div>
    <div class="marker-menu marker-window window-left">
      <a
        @click="changeCurrentPanorama"
        :data-tooltip="$t(changeCurrentPanoramaTool.hintText)">
        <i :class="'fa ' + changeCurrentPanoramaTool.class"></i>
      </a>
    </div>
    <!-- <span class="divider"></span> -->
    <div class="marker-menu marker-window window-right">
      <a
        :class="{ 'not-allowed': !pointMarkerState && editorTool.type === 'point' }"
        v-for="editorTool in editorTools"
        @click="handleAddAppMarker(editorTool)"
        :data-tooltip="$t(editorTool.hintText)">
        <!-- <i :class="'fa ' + editorTool.class"></i> -->
        <i :class="['fa ' + editorTool.class, { 'not-allowed': !pointMarkerState && editorTool.type === 'point' }] "></i>
      </a>
    </div>
  </div>
</template>

<script>
import {
  panoramas,
  selectedPanorama,
  selectedMarker,
  editorState,
  hasAddedMarker,
  cameraContainerElRotation,
  cameraContainerEl
} from '../../vuex/getters'

import {
  addAppMarker,
  setSceneEventCount,
  setSelectedMarker,
  // addTag,
  // addLink,
  setEditorState,
  setHasAddedMarker,
  setCameraElRotation
} from '../../vuex/actions'

export default {
  vuex: {
    getters: {
      panoramas,
      selectedPanorama,
      selectedMarker,
      editorState,
      hasAddedMarker,
      cameraContainerElRotation,
      cameraContainerEl
    },
    actions: {
      setSelectedMarker,
      setSceneEventCount,
      addAppMarker,
      // addTag,
      // addLink,
      setEditorState,
      setHasAddedMarker,
      setCameraElRotation
    }
  },

  data () {
    return {
      pointMarkerState: true,
      pointMarkerHint: '',
      changeCurrentPanoramaTool: {
        class: 'fa-th-large', hintText: 'switchCurrentPanorama'
      }
    }
  },

  computed: {
    editorTools () {
      return [
        { class: 'fa-bullseye', type: 'point', hintText: this.pointMarkerHint },
        { class: 'fa-tag', type: 'tag', hintText: 'addTag' },
        { class: 'fa-sticky-note-o', type: 'memo', hintText: 'addMemo' }
      ]
    }
  },

  watch: {
    panoramas (val) {
      if (val.length > 1) {
        this.pointMarkerState = true
        this.pointMarkerHint = 'addStandpoint'
      } else {
        this.pointMarkerState = false
        this.pointMarkerHint = 'please add at least 2 panorama first'
      }
    }
  },

  methods: {
    handleAddAppMarker (editorTool) {
      if (!this.pointMarkerState && editorTool.type === 'point') return
      // need to modify camera in order to catch the new camera rotation
      const nullRotation = 0.01
      const cameraRotation = { x: 0, y: this.cameraContainerElRotation.y + nullRotation, z: 0 }
      this.setCameraElRotation(this.cameraContainerEl, cameraRotation)
      this.setSceneEventCount()
      this.addAppMarker(editorTool.type)
      const lastMarkerPos = this.selectedPanorama.markers.length - 1
      this.setSelectedMarker(this.selectedPanorama.markers[lastMarkerPos])
      this.setEditorState(2)
      this.setHasAddedMarker(true)
    },

    changeCurrentPanorama () {
      this.setEditorState(0)
    }
  }
}
</script>

<style lang="stylus">
@import '../../variables'
@import '../../mixins'

.marker-menu.marker-window {
  bottom: 20px
  left: 50%
  transform: translateX(-50%)
  border-radius(100px)

  a {
    display: inline-block
    position: relative
  }

  .fa-bullseye,
  .fa-tag,
  .fa-sticky-note-o,
  .fa-th-large {
    color: white
    font-size: 28px
    margin-left: 2px
    margin-right: 2px
    cursor: pointer

    &:hover {
      opacity: .8
    }
  }

  .fa-bullseye {
    margin: 0 5px

    &.not-allowed {
      cursor: default
      opacity: .2
    }
  }

  .divider {
    border-left: 1px solid alpha($gray-light, 40%)
    display: inline-block
    padding: 0 6px 0 0
    margin: 0 0 0 6px
    height: 27px
    vertical-align: top
  }

  .tool-hint {
    position: absolute
    width: 300px
    border: 1px solid white
    top: -30px
    left: -80px
    text-align: center
    color: white
  }
}

.window-left {
  margin-left: -60px
  padding: 8px 7px
  background-color: alpha($default-color, 70%)
}

.window-right {
  margin-left: 25px
  padding: 8px 10px 8px 5px
  background-color: alpha($brand-color, 70%)
}

[data-tooltip] {
  display: inline-block
  position: relative
  color: #f52e62
  cursor: text

  &:before {
    display: none
    position: absolute
    bottom: 130%
    left: 50%
    z-index: 2
    transform: translate(-50%, 50%) rotate(45deg)
    width: 8px
    height: 8px
    /*border: solid $white*/
    border-width: 0 1px 1px 0
    background-color: $default-color
    content: ''
  }

  &:after {
    display: none
    position: absolute
    bottom: 130%
    left: 50%
    padding: 4px 8px
    transform: translateX(-50%)
    /*border: 1px solid $white*/
    /*border-radius: 3px*/
    color: $white
    white-space: nowrap
    background-color: $default-color
    pointer-events: none
    content: attr(data-tooltip)
  }

  &:focus, &:hover {
    &:before, &:after {
      display: block;
    }
  }
}
</style>



