export const fetchShareCodes = ({ dispatch, state }, callback) => {
  if (state.user.role === '' ||
    state.user.role === 'normal' ||
    state.user.role === 'agent' ||
    state.user.role === 'shopifyAgent') {
    api.fetchShareCodes().then(shareCodes => {
      dispatch('SET_SHARECODES', shareCodes)
    }).then(() => {
      callback()
    })
  }
  if (state.user.role === 'admin') {
    api.fetchAdminShareCodes().then(shareCodes => {
      dispatch('SET_SHARECODES', shareCodes)
    }).then(() => {
      callback()
    })
  }
}

export const fetchBuildings = ({ dispatch, state }, callback) => {
  if (state.user.role === '' ||
    state.user.role === 'normal' ||
    state.user.role === 'agent' ||
    state.user.role === 'shopifyAgent') {
    api.fetchBuildings().then(buildings => {
      dispatch('SET_BUILDINGS', buildings)
    }, err => {
      return err
    }).then(err => {
      callback(err)
    })
  }
  if (state.user.role === 'admin') {
    api.fetchAdminBuildings().then(buildings => {
      dispatch('SET_BUILDINGS', buildings)
    }).then(() => {
      callback()
    })
  }
}

