export const fetchFirebaseComments = ({ dispatch }, code) => {
  const commentRef = firebase.database().ref('comments/' + code)
  commentRef.once('value', snapshot => {
    const firebaseComments = snapshot.val()
    let comments = []
    for (let firebaseComment in firebaseComments) {
      const newFirebaseCommentDate = firebaseComments[firebaseComment].date
      const newFirebaseComment = {
        id: firebaseComment,
        author: firebaseComments[firebaseComment].author,
        date: moment(newFirebaseCommentDate).format('YYYY/MM/DD'),
        message: firebaseComments[firebaseComment].message
      }
      comments.push(newFirebaseComment)
    }
    dispatch('FETCH_FIREBASE_COMMENTS', comments)
  })
}

export const deleteFirebaseComments = ({ dispatch }, sharecode, commentId) => {
  const commentRef = firebase.database().ref('comments/' + sharecode + '/' + commentId)
  const buildingRef = firebase.database().ref('buildings/' + sharecode + '/commentCount')
  buildingRef.transaction(function (currCount) {
    return currCount - 1
  })
  commentRef.remove()
  return Promise.resolve()
}

function getRootUrl (url) {
  return url.toString().replace(/^(.*\/\/[^\/?#]*).*$/, '$1')
}

// resizeArr = [], return origin size
// resizeArr = [{w:1,h:2},{w:2,h:4}], return 2 resize images
export const doImageUpload = ({ dispatch, state }, file, resizeArr) => {
  file.progress = 20
  const avFile = new AV.File(file.name, file) // eslint-disable-line
  avFile.save().then(AVFile => {
    var result = []
    if (resizeArr.length >= 1) {
      for (let index in resizeArr) {
        var rootUrl = getRootUrl(AVFile.url())
        console.log(rootUrl)
        var url = AVFile.thumbnailURL(resizeArr[index].w, resizeArr[index].h).replace(rootUrl, api.resizeURL).replace('imageView', 'imageView2').replace('png', 'jpg')
        result.push(url)
      }
    } else {
      result.push(AVFile.url())
    }
    dispatch('DO_IMAGE_UPLOAD', result)
  }, error => {
    console.log(error)
  })
}

