export const undoSetRotation = ({ dispatch, state }, context) => {
  const resetCameraRotation = `${0} ${state.cameraElOrigRotation.y} ${0}`
  // TODO turn this into a function
  // dispatch('SET_IS_EDITOR_SCENE_READY', !state.isEditorSceneReady)
  Vue.nextTick(() => {
    dispatch('SET_IS_EDITOR_SCENE_READY', true)
    Vue.nextTick(() => {
      // use reset version of cameraContainer
      // TODO find a better way to get the parent
      dispatch('GET_CAMERA_CONTAINER_EL', context.$parent.$els.cameraContainer)
      dispatch('SET_CAMERA_EL_ROTATION', state.cameraContainerEl, resetCameraRotation)
    })
  })
  // dispatch('SET_TARGET_PANORAMA', state.selectedPanorama)
  dispatch('SET_SELECTED_PANORAMA', state.currentPanorama)
}

export const resetCamera = ({ dispatch, state }, context, callback) => {
  dispatch('SET_IS_EDITOR_SCENE_READY', !state.isEditorSceneReady)
  Vue.nextTick(() => {
    dispatch('SET_IS_EDITOR_SCENE_READY', true)
    Vue.nextTick(() => {
      // use reset version of cameraContainer
      // TODO find a better way to get the parent
      dispatch('GET_CAMERA_CONTAINER_EL', context.$parent.$els.cameraContainer)
      if (callback) callback()
    })
  })
}

