<template>
  <div id="footer" :class="['col-md-12', footerClasses]">
    <div class="container">
      <hr>
      <p>{{ $t('copyright', { y: currentYear }) }}</p>
    </div>
  </div>
</template>

<script>
export default {
  name: 'IFooter',
  computed: {
    currentYear () {
      return new Date().getFullYear()
    }
  }
}
</script>

<style lang="stylus">
@import '../variables'

#footer {
  position: absolute
  bottom: 0
  width: 100%
  height: 69px

  p {
    color: $gray
    font-size: 13px
    margin: 20px 0
  }
}

body.bg-brand {
  #footer {
    p {
      color: $white
    }

    hr {
      border-top-color: $white
    }
  }
}
</style>



