<template>
  <div id="login" class="col-md-12">
    <img class="logo" :src="logoImage">
    <hr>
    <form-builder
      :object="authInfo"
      :fields="formFields"
      btn-text="logIn"
      @on-submit="loginSubmit">
    </form-builder>
    <loading v-show="isLoading" color="white" :no-overlay="true"></loading>
    </loading>
    <alert v-show="alertParams.show"></alert>
    <!-- <hr> -->
    <!-- <a class="btn btn-default btn-facebook" @click="fbLoginSubmit">
      <i class="fa fa-facebook"></i>
      {{ $t('logInWithFacebook') }}
      <div id="fb-root"></div>
    </a>
    <a :href="'#/forgot-pass'">{{ $t('forgotPassLink') }}</a> -->
  </div>
</template>

<script>
import FormBuilder from '../components/FormBuilder'
import Alert from '../components/Alert'
import Loading from '../components/Loading'
// import api from '../api'
import auth from '../auth'
import {
  isLoading,
  authInfo,
  user,
  activeLoading,
  alertParams
} from '../vuex/getters'

import {
  fetchShareCodes,
  fetchBuildings,
  checkBuildings,
  checkShareCodes,
  setUserInfo,
  setIsLoading,
  setProfilePhoto,
  setAlertParams
} from '../vuex/actions'

export default {
  name: 'Login',
  components: {
    FormBuilder,
    Alert,
    Loading
  },

  vuex: {
    getters: {
      isLoading,
      authInfo,
      user,
      activeLoading,
      alertParams
    },
    actions: {
      fetchShareCodes,
      fetchBuildings,
      checkBuildings,
      checkShareCodes,
      setUserInfo,
      setIsLoading,
      setProfilePhoto,
      setAlertParams
    }
  },

  data () {
    return {
      alert: null,
      formFields: [
        {
          name: 'email',
          type: 'email',
          required: true
          // matches: '^[a-zA-Z0-9]$'
          // matches: '^[-a-z0-9~!$%^&*_=+}{\'?]+(\.[-a-z0-9~!$%^&*_=+}{\'?]+)*@([a-z0-9_][-a-z0-9_]*(\.[-a-z0-9_]+)*\.(aero|arpa|biz|com|coop|edu|gov|info|int|mil|museum|name|net|org|pro|travel|mobi|[a-z][a-z])|([0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}))(:[0-9]{1,5})?$'
        },
        { name: 'password', type: 'password', required: true }
      ],
      formActions: [
        {
          name: 'logIn',
          submit: this.loginSubmit
        }
      ],
      logoImage: require('../assets/logo-login.png')
    }
  },

  route: {
    canActivate () {
      return !auth.user.authenticated
    },

    activate: function () {
      const body = document.getElementsByTagName('body')[0]
      body.className += ' bg-brand'
    }
  },

  ready () {
    // window.fbAsyncInit = function () {
    //   /* eslint-disable */
    //   FB.init({
    //   /* eslint-enable */
    //     appId: api.facebookAppId,
    //     oauth: true,
    //     status: true, // check login status
    //     cookie: true, // enable cookies to allow the server to access the session
    //     xfbml: true // parse XFBML
    //   })
    // }
    //
    // ;(function () {
    //   var e = document.createElement('script')
    //   e.src = document.location.protocol + '//connect.facebook.net/en_US/all.js'
    //   e.async = true
    //   document.getElementById('fb-root').appendChild(e)
    // }())
  },

  methods: {
    loginSubmit () {
      const credentials = {
        email: this.authInfo.email,
        password: this.authInfo.password
      }
      this.setIsLoading(true)
      auth.login(this, credentials).then(result => {
        this.handleAfterLogin()
      }).catch(err => {
        this.setIsLoading(false)
        console.log('Error:', err)
        this.setAlertParams({ type: 'danger', text: 'logInFailed', show: true })
      })
    },

    // fbLoginSubmit () {
    //   /* eslint-disable */
    //   FB.login(response => {
    //   /* eslint-enable */
    //     console.log('statusChangeCallback')
    //     if (response.status === 'connected') {
    //       this.setActiveLoading('authLoading')
    //       const credentials = { access_token: response.authResponse.accessToken }
    //       auth.loginWithFacebook(credentials).then(() => {
    //         this.handleAfterLogin()
    //       })
    //     } else if (response.status === 'not_authorized') {
    //       console.log('Please log into this app.')
    //     } else {
    //       console.log('Please log into this Facebook.')
    //     }
    //   }, {
    //     scope: 'email'
    //   })
    // },

    handleAfterLogin () {
      this.setUserInfo(auth.user)
      this.fetchShareCodes(() => this.checkShareCodes())
      this.fetchBuildings(() => this.checkBuildings())
      this.setProfilePhoto()
      this.setIsLoading(false)
      this.$route.router.go('/buildings')
    }
  }
}
</script>

<style lang="stylus">
@import '../variables'
@import '../mixins'

$fb-color = #3b5998

#login,
#login .container {
  max-width: 420px // TODO find a better way of doing this
}

#login {
  color: $white
  transform: translate(-50%, -50%)
  top: 50%
  left: 50%
  position: fixed
  width: 100%

  .logo {
    display: block
    margin: 0 auto
    width: 170px
  }

  .col-centered {
    text-align: center
  }

  .form-group {
    margin-bottom: 4px
  }

  a {
    color: $white
    display: block
    text-align: center
  }

  hr {
    margin: 16px 0
    opacity: 0.4
  }

  input {
    background-color: alpha(white, 20%)
    border-color: $brand-color
    color: $white
    placeholder-color(alpha($white, 60%))
  }

  input[type="submit"] {
    background-color: $default-color
    border-top-color: $default-color
    border-left-color: $default-color
    border-right-color: $default-color
    border-bottom-color: darken($default-color, 10%)
    width: 100%

    &:hover {
      hover-color($default-color)
    }
  }

  .control-label {
    display: none
  }

  .col-sm-10 {
    width: 100%
  }

  .col-sm-offset-2 {
    margin-left: 0
  }

  .has-error {
    color: $white
  }

  .btn-facebook {
    background: $fb-color
    border-color: darken($fb-color, 10%)
    color: $white
    display: block
    margin: 0 auto 10px

    i {
      color: $white
      border-right: 1px solid darken($fb-color, 10%)
      padding-right: 10px
      margin-right: 5px
    }

    &:hover {
      background: darken($fb-color, 10%)
    }
  }
}
</style>



